<template>
  <div></div>
</template>

<script>
/**
 * 消息中转站，使用storage和其它标签页通讯
 * 监听到storage变化，把对应事件、参数原样弹射
 * 注意：storage事件不会在发生变化的窗口触发
 */
export default {
  data () {
    return {
      freezeList: {},
      eventFunc: {},
      /**
       * 转发事件列表
       */
      eventList: [
        'code-test',
        'pop-helper-show-menu',
        'pop-helper-hide-menu',
        'pop-helper-update-prop',
        'pop-helper-register-component',
        'code-test-pass',
        'code-test-error-card',
        'code-test-end',
        'code-test-focus-error-code'
      ]
    }
  },
  created () {
    window.setTargetWindow = (newWindow) => {
      window.__targetWindow = newWindow
      newWindow.__targetWindow = window
    }
    window.clearTargetWindow = () => {
      if (window.__targetWindow) {
        delete window.__targetWindow.__targetWindow
      }
      delete window.__targetWindow
    }
    window.forwardBusEvent = (eventName, args) => {
      let freezeName = 'freeze' + eventName
      this.freezeList[freezeName] = true // 转发到本窗口的事件，不做转发
      this.$bus.$emit(eventName, ...args)
    }
    for (let eventName of this.eventList) {
      this.eventFunc[eventName] = (...args) => {
        let freezeName = 'freeze' + eventName
        if (this.freezeList[freezeName]) {
          delete this.freezeList[freezeName]
          return
        }
        if (window.__targetWindow && window.__targetWindow.forwardBusEvent) {
          window.__targetWindow.forwardBusEvent(eventName, args)
        }
      }
      this.$bus.$on(eventName, this.eventFunc[eventName])
    }
  },
  beforeDestroy () {
    for (let eventName of this.eventList) {
      this.$bus.$off(eventName, this.eventFunc[eventName])
      delete this.eventFunc[eventName]
    }
    delete window.setTargetWindow
    delete window.clearTargetWindow
  },
  methods: {
  }
}
</script>
