<template>
  <div class="radio-wrapper" :class="{'checked': currentValue}">
    <label class="radio-box">
      <span class="radio-icon"></span>
      <input
        type="radio"
        :checked="currentValue"
        :name="groupName"
        @change="change"
        style="display: none">
      <span class="radio-label"><slot/></span>
    </label>
  </div>
</template>

<script>
import { findComponentUpward } from './assist'

export default {
  name: 'xRadio',
  props: {
    value: {
      type: [String, Number, Boolean],
      default: false
    },
    trueValue: {
      type: [String, Number, Boolean],
      default: true
    },
    falseValue: {
      type: [String, Number, Boolean],
      default: false
    },
    label: {
      type: [String, Number]
    },
    disabled: {
      type: Boolean,
      default: false
    },
    name: {
      type: String
    }
  },
  data () {
    return {
      currentValue: this.value,
      group: false,
      parent: findComponentUpward(this, 'xRadioGroup'),
      groupName: this.name
    }
  },
  mounted () {
    if (this.parent) {
      this.group = true
      if (this.name && this.name !== this.parent.name) {
        /* eslint-disable no-console */
        if (console.warn) {
          console.warn('[iview] Name does not match xRadio Group name.')
        }
        /* eslint-enable no-console */
      } else {
        this.groupName = this.parent.name
      }
    }
    if (this.group) {
      this.parent.updateValue()
    } else {
      this.updateValue()
    }
  },
  methods: {
    change (event) {
      if (this.disabled) {
        return false
      }
      const checked = event.target.checked
      this.currentValue = checked
      const value = checked ? this.trueValue : this.falseValue
      this.$emit('input', value)
      if (this.group) {
        if (this.label !== undefined) {
          this.parent.change({
            value: this.label,
            checked: this.value
          })
        }
      } else {
        this.$emit('on-change', value)
      }
    },
    updateValue () {
      this.currentValue = this.value === this.trueValue
    }
  }
}
</script>

