<template>
  <div class="x-checkbox-group">
    <slot/>
  </div>
</template>

<script>
import { findComponentsDownward } from './assist'
import uuidv4 from 'uuid/v4'

export default {
  name: 'xCheckboxGroup',
  props: {
    value: {
      type: Array,
      default: () => []
    },
    name: {
      type: String,
      default: uuidv4()
    }
  },
  data () {
    return {
      currentValue: this.value,
      childrens: []
    }
  },
  mounted () {
    this.updateModel(true)
  },
  watch: {
    value () {
      this.updateModel(true)
    }
  },
  methods: {
    updateModel (update) {
      this.childrens = findComponentsDownward(this, 'xCheckbox')
      if (this.childrens) {
        const { value } = this
        this.childrens.forEach(child => {
          child.model = value
          if (update) {
            child.currentValue = value.indexOf(child.label) >= 0
            child.group = true
          }
        })
      }
    },
    change (data) {
      this.currentValue = data
      this.updateModel()
      this.$emit('input', data)
      this.$emit('on-change', data)
    }
  }
}
</script>

