// getters
const getters = {

}

// actions
const actions = {
  async init ({ commit }) {
    const list = await getSimpleList({ type: 1 })
    if (list) {
      commit('setList', list)
      console.log('组件载入完毕')
    } else {
      return false
    }
  },
  async customComponentList ({ commit }, relationComponentAOList) {
    const list = await getSimpleList({
      type: 2,
      relationComponentAOList
    })
    if (list) {
      commit('setCustomComponentList', list)
    } else {
      return false
    }
  }
}

