// mutations
const mutations = {
  setList (state, data) {
    /**
     * type
     * 1 单选
     * 2 多选
     * 4 判断
     * 7 多填
     */
    // console.log(data)
    // 布尔型
    // multiple
    // paste
    // showUploadList

    // 数字
    // maxSize
    for (const n in data) {
      // 计算问题和选项
      if (data[n].type === 1 && data[n].otherConfig) {
        const tmp = data[n].otherConfig.split(';')
        if (tmp.includes('singleSelect')) {
          state.question['singleSelect'] = data[n]
        } else if (tmp.includes('multiSelect')) {
          state.question['multiSelect'] = data[n]
        } else if (tmp.includes('judge')) {
          state.question['judge'] = data[n]
        } else if (tmp.includes('multiInput')) {
          state.question['multiInput'] = data[n]
        } else if (tmp.includes('tree')) {
          state.question['tree'] = data[n]
        } else if (tmp.includes('singleSelect-option')) {
          state.options['singleSelect-option'] = data[n]
        } else if (tmp.includes('multiSelect-option')) {
          state.options['multiSelect-option'] = data[n]
        } else if (tmp.includes('judge-option')) {
          state.options['judge-option'] = data[n]
        } else if (tmp.includes('input')) {
          state.options['input'] = data[n]
        }

        // // 处理数据类型错误的情况
        // for (const i in data[n].items[0].configs) {
        //   let config = data[n].items[0].configs[i]
        //   // convertDataType(config)
        //   // let configCode = config['code']
        //   let type = getInputType(config['code'])
        //   if (type === 1) {
        //     if (!isNaN(config['configData']['configData'])) {
        //       config['configData']['configData'] = parseInt(config['configData']['configData'])
        //     }
        //     if (!isNaN(config['defaultValue'])) {
        //       config['defaultValue'] = parseInt(config['defaultValue'])
        //     }
        //   } else if (type === 2) {
        //     config['configData']['configData'] = JSON.parse(config['configData']['configData'])
        //     config['defaultValue'] = JSON.parse(config['defaultValue'])
        //   }
        // }
      }
    }

    // 添加到组件列表
    state.list = data

    // // 特殊处理
    // // 多项选择的排异 获取其对应的rejection_condition的componentItemId
    // for (const n in state.question.multiSelect.configs) {
    //   if (state.question.multiSelect.configs[n].code === 'rejection_condition') {
    //     state.rejection_condition_componentItemId = state.question.multiSelect.configs[n].componentItemId
    //     return
    //   }
    // }
  },
  setCustomComponentList (state, data) {
    state.cList = data
  },
  addList (state, data) {
    state.list.push(data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
