<template>
  <div class="custom-component-edit" v-if="cnode.items[0]">
    <Modal v-model="value"
           :mask-closable="false"
           :loading="loading"
           :closable="true"
           @on-cancel="customComponentCancel"
           width="98%">
      <p slot="header">
        <span v-if="editItem">配置自定义组件</span>
        <span v-else>添加自定义组件</span>
      </p>
      <Row :gutter="16">
        <Col span="24"
             v-if="!loading">
        <Form :label-width="180"
              ref="formCustomComponentEdit"
              :model="cnode"
              :rules="formCustomComponentEditValidate">
          <Row :gutter="16"
               class="edit-item">
            <Col span="24">
            <Form-item :label="'名称' + (cnode.items[0] && cnode.items[0].code && !isNew ? ' (' + cnode.items[0].code + ')' : '')"
                       prop="name">
              <Input v-model="cnode.name"
                     @on-change="changName"
                     placeholder="请输入标题"
                     style="width: 100%"></Input>
            </Form-item>
            <Form-item label="描述">
              <Input v-model="cnode.remarks"
                     placeholder="请输入描述"
                     style="width: 100%"></Input>
            </Form-item>
            </Col>
          </Row>
          <Row :gutter="16"
               class="edit-item">
            <Col span="24">
            <Form-item label="输入方式">
              <ButtonGroup>
                <Button @click="clickCustomComponentTypeButton('judge')"
                        :class="selectCustomComponentType === 'judge' ? 'editing' : '' ">判断</Button>
                <Button @click="clickCustomComponentTypeButton('singleSelect')"
                        :class="selectCustomComponentType === 'singleSelect' ? 'editing' : '' ">单选</Button>
                <Button @click="clickCustomComponentTypeButton('multiSelect')"
                        :class="selectCustomComponentType === 'multiSelect' ? 'editing' : '' ">多选</Button>
                <Button @click="clickCustomComponentTypeButton('multiInput')"
                        :class="selectCustomComponentType === 'multiInput' ? 'editing' : '' ">多填</Button>
                <Button @click="clickCustomComponentTypeButton('tree')"
                        :class="selectCustomComponentType === 'tree' ? 'editing' : '' ">树形</Button>
              </ButtonGroup>
            </Form-item>
            </Col>
          </Row>
          <Row v-if="selectCustomComponentType === 'other'">
            <Col span="24">
            <Form-item label="输入方式">
              <Select :filterable="true"
                      :size="'default'"
                      :transfer="false"
                      @on-change="changeCustomComponent($event, cnode)"
                      v-if="cnode.items[0] && cnode.items[0].componentId"
                      v-model="cnode.items[0].componentId">
                <Option v-for="(com, index) in componentList"
                        v-if="(com.type === 1 || (com.type === 2 && (!com.items[0].uuid || (com.items[0].uuid && com.items[0].uuid !== '00000000-0000-0000-0000-000000000012')))) && (!com.otherConfig || (com.otherConfig && com.otherConfig.indexOf('display') === -1))"
                        :key="index"
                        :value="com.id">{{com.name}}</Option>
              </Select>
            </Form-item>
            </Col>
          </Row>
          <Row v-if="!isNew && isTeamComponent">
            <Col span="24">
            <Form-item label="修改归属">
              <RadioGroup v-model="moveToComponent">
                  <Radio label="false">不修改</Radio>
                  <Radio label="org">{{ '移动到 ' + currentCompanyName + ' 公司' }}</Radio>
                  <Radio v-if="teamType === 3 && teamName(projectId)" label="team">{{ '移动到 ' + teamName(projectId) + ' 团队' }}</Radio>
              </RadioGroup>
            </Form-item>
            </Col>
          </Row>
          <Row v-if="selectCustomComponentType !== 'multiInput' && selectCustomComponentType !== 'multiSelect' && selectCustomComponentType !== 'other'"
               class="custom-component-config-other-config"
               style="margin-bottom: 10px">
            <Col span="2"
                 style="text-align: right">选项配置项</Col>
            <Col span="22">
            <div v-if="!showEditor"
                 style="height: 100px; width: 100%; text-align: center">
              <Spin size="large"
                    fix
                    v-if="!showEditor"></Spin>
            </div>
            <configComponent v-if="componentDefaultData"
                            :componentId="cnode.items[0].componentId"
                            :componentDefaultData="componentDefaultData"
                            :componentData="cnode.componentInfo"
                            :cType="2"
                            @updateConfig="updateConfig"
                            @updateComponentData="updateComponentData($event, cnode)" />
            </Col>
          </Row>
          <template v-if="cnode.items[0] && showCustomComponentOptions">
            <div class="coptions-table">
              <Row class="coptions-title"
                   :gutter="16">
                <Col span="2">序号</Col>
                <Col span="7">选项标题</Col>
                <Col span="5"
                     v-show="showCustomComponentSelectType">输入方式</Col>
                <!-- <Col span="5"
                     v-show="selectCustomComponentType === 'judge' || selectCustomComponentType === 'singleSelect' || selectCustomComponentType === 'multiSelect' ">选项类型</Col> -->
                <Col span="5">选择或填写提示</Col>
                <Col span="1">必填</Col>
                <Col span="3"
                     v-if="selectCustomComponentType !== 'judge'">操作</Col>
              </Row>
              <draggable v-model="cnode.items" v-if="cnode.items.length"
                         :options="{handle: '.cindex', dragClass: 'dragging', draggable:'.coptions-item'}">
                <Row class="coptions-item"
                     v-if="index !== 0 && (selectCustomComponentType !== 'judge' || index < 3)"
                     v-for="(option, index) in cnode.items"
                     :key="index"
                     :gutter="10">
                  <Col span="2"
                       class="cindex">#{{ index }} {{ option.code && !isNew ? '(' + option.code + ')' : '' }}</Col>
                  <Col span="7">
                  <Input placeholder="请输入标题"
                         type="text"
                         v-model="option.name" />
                  </Col>
                  <!-- <Col span="5"
                       style="text-align: left"
                       v-show="selectCustomComponentType === 'judge' || selectCustomComponentType === 'singleSelect' || selectCustomComponentType === 'multiSelect' "> -->
                  <!-- <Select :filterable="true"
                          :size="'default'"
                          :transfer="false"
                          placeholder="默认（选项）"
                          clearable
                          @on-change="changeComponentType($event, option, index)"
                          v-model="option.point">
                    <Option v-for="(com, index) in componentList"
                            :key="index"
                            v-if="com.uuid === '00000000-0000-0000-0000-000000000011' || com.uuid === '00000000-0000-0000-0000-000000000015' || com.uuid === '00000000-0000-0000-0000-000000000017' || com.uuid === '00000000-0000-0000-0000-000000000001'"
                            :value="com.id">{{ com.name }}</Option>
                  </Select> -->
                  <!-- </Col> -->
                  <Col span="5"
                       v-show="showCustomComponentSelectType"
                       style="text-align: left">
                  <Select :filterable="true"
                          :size="'default'"
                          :transfer="false"
                          @on-change="changeCustomComponent($event, option, index)"
                          v-if="showCustomComponentSelectType"
                          v-model="option.componentId">
                    <Option v-for="(com, index) in componentList"
                            :key="index"
                            v-if="(com.type === 1 || (com.type === 2 && com.items[0].uuid !== '00000000-0000-0000-0000-000000000012' && com.items[0].uuid !== '00000000-0000-0000-0000-000000000016' && com.items[0].uuid !== '00000000-0000-0000-0000-000000000024')) && (!com.otherConfig || (com.otherConfig && com.otherConfig.indexOf('display') === -1))"
                            :value="com.id">{{ com.name }}</Option>
                  </Select>
                  </Col>
                  <Col span="5">
                  <Input placeholder="选项描述"
                         type="text"
                         v-model="option.remarks" />
                  </Col>
                  <Col span="1">
                  <Checkbox v-model="option.require"
                            :disabled="option.point" />
                  </Col>
                  <Col span="3"
                       v-if="selectCustomComponentType !== 'judge'">
                  <Button @click="deleteCustomComponentOption(option, index)">删除</Button>
                  </Col>
                  <Col span="24">
                  <template v-for="(item, itemIndex) in componentList"
                            v-if="showCustomComponentSelectType && option.componentId && item.id === option.componentId">
                    <Row :gutter="10"
                         :key="itemIndex">
                      <Col span="7">
                      <div>&nbsp;</div>
                      </Col>
                      <Col span="17">
                      <div class="config-component">
                        <div class="poptip-arrow"></div>
                        <!-- 判断是否是自定义的单选、判断、多选 -->
                        <template v-if="(selectCustomComponentType === 'multiInput' || selectCustomComponentType === 'other') && item.type === 2 && (item.items[0].uuid === '00000000-0000-0000-0000-000000000005' || item.items[0].uuid === '00000000-0000-0000-0000-000000000007' || item.items[0].uuid === '00000000-0000-0000-0000-000000000013')">
                          <div v-for="(it, inx) in item.items"
                               :key="inx">
                            <template v-if="inx > 0">
                              <h2>{{ it.name }}</h2>
                              <configComponent :componentDefaultData="it"
                                               v-if="cnode.items[index]['children'] && cnode.items[index]['children'].length"
                                               :componentData="cnode.items[index]['children'][inx - 1]"
                                               :itemId="0"
                                               :componentType="item.type"
                                               :cType="2"
                                               :useItem="true"
                                               @updateComponentData="updateComponentData($event, cnode.items[index]['children'][inx - 1])" />
                            </template>
                          </div>
                        </template>
                        <configComponent v-else
                                         :componentDefaultData="option"
                                         :componentData="option"
                                         :itemId="0"
                                         :useItem="true"
                                         :componentType="item.type"
                                         :cType="2"
                                         @updateComponentData="updateComponentData($event, option)" />
                      </div>
                      </Col>
                    </Row>
                  </template>
                  </Col>
                </Row>
              </draggable>
            </div>
            <Button type="primary"
                    style="margin-top: 10px"
                    shape="circle"
                    icon="md-add"
                    v-if="selectCustomComponentType !== 'judge'"
                    @click="addCustomComponentOption">添加选项</Button>
          </template>
          <template v-if="treeShow"><!--如果是树形-->
            <ul class="switch-list">
              <li class="switch-item"><Button @click="addRow">添加一行（选定位置之后）</Button></li>
              <li class="switch-item"><Button @click="prependTable">添加一行（到表头）</Button></li>
              <!-- <li class="switch-item"><Button @click="prependRow">添加子行（作为第一个子元素）</Button></li> -->
              <li class="switch-item"><Button @click="appendRow">添加子行（作为最后一个子元素）</Button></li>
              <li class="switch-item"><Button @click="removeRow">删除当前选中行（及其子元素）</Button></li>
              <li class="switch-item"><Button @click="expandedAllOrCollapsedAll">全部展开/收起（数据量大时慎用）</Button></li>
              <li class="switch-item"><Button @click="exportAsExcel">导出数据为Excel文档</Button></li>
              <li class="switch-item"><Button @click="downloadExcelTemplate">下载Excel树形数据校检模板</Button></li>
              <li class="switch-item"><Button @click="getGTCITY">实时获取国家统计局《统计用区划代码和城乡划分代码》</Button></li>
              <li class="switch-item"><Button @click="getMZCITY">实时获取国家民政部《中华人民共和国行政区划代码》</Button></li>
            </ul>
          </template>
          <div id="grid"
               v-show="treeShow"></div>
        </Form>
        </Col>
        <Col span="24"
             v-if="loading">
        <Spin fix>
          <div>正在保存数据，请稍候...</div>
        </Spin>
        </Col>
      </Row>
      <div class="config-modal-footer"
           slot="footer">
        <Row type="flex"
             justify="end">
          <Button type="primary"
                  :loading="loading"
                  @click="customComponentOk">
            保存
          </Button>
        </Row>
      </div>
    </Modal>
    <Modal v-model="loadingCity"
           :mask-closable="false"
           :closable="false"
           width="50%">
      <p slot="header">
        提示
      </p>
      <div style="text-align: center">
        <div class="spin-loading">
          <Spin size="large"
                fix></Spin>
        </div>
        数据正在获取中，请耐心等待。预计需要1分钟，请勿关闭浏览器。
      </div>
      <div class="config-modal-footer"
           slot="footer">
        <Row type="flex"
             justify="end">
          <Button type="primary"
                  :loading="true">
            关闭
          </Button>
        </Row>
      </div>
    </Modal>
  </div>
</template>

<script>
import { saveCompontent, checkName, getCompontentCache, updateComponent } from '@/api/component'
import configComponent from '@components/configComponent'
import draggable from 'vuedraggable'
import uuidv4 from 'uuid/v4'
// import JsTreeList from 'js-tree-list'
import XLSX from 'xlsx'
import { toTreeData } from '@/libs/util'
// import { toTreeData, getToken } from '@/libs/util'
// import { DevBaseUrl } from '@/libs/config'

export default {
  name: 'custom-component-edit',
  components: {
    configComponent,
    draggable
  },
  props: {
    value: {
      type: Boolean,
      require: true
    },
    editItem: {
      type: Object,
      require: false
    },
    nowSelectUUID: {
      type: Object,
      required: false
    },
    judgeComponent: {
      type: Object,
      required: false
    },
    org: {
      type: String,
      require: true
    },
    team: {
      type: String,
      required: false
    },
    teamList: {
      type: Array,
      require: false
    },
    projectList: {
      type: Array,
      require: false
    }
  },
  data () {
    const validateName = async (rule, value, callback) => {
      if (!value) {
        return callback(new Error('名称不能为空'))
      }
      if (!this.editItem) { // 如果是新增
        if (await checkName(this.cnode.name) === false) {
          this.$Message.error('自定义组件名称重复')
          return callback(new Error('自定义组件名称重复'))
        }
      }
      callback()
    }

    return {
      loadingCity: false,
      loading: false,
      // 拖动选项
      dragging: false,
      // 自定义组件部分
      newComponentData: null,
      selectCustomComponentType: null,
      showCustomComponentSelectType: false,
      showCustomComponentOptions: false,
      moveToComponent: 'false',
      cnode: {
        id: null,
        name: null,
        otherConfig: null,
        projectId: 0,
        range: 0,
        remarks: null,
        sort: 0,
        type: 0,
        items: [
          {
            componentId: 0,
            code: null
          }
        ]
      },
      showEditor: false,
      formCustomComponentEditValidate: {
        name: [
          { required: true, validator: validateName, trigger: 'blur' }
        ]
      },
      treeSum: 1,
      treeShow: false,
      columns: [
        {
          title: 'Code',
          name: 'code'
        },
        {
          title: '名称',
          name: 'name',
          width: 400,
          editOptions: {
            type: 'text',
            useViewMode: true
          }
        },
        {
          title: 'Name1',
          name: 'name1',
          editOptions: {
            type: 'text',
            useViewMode: true
          }
        },
        {
          title: 'Name2',
          name: 'name2',
          editOptions: {
            type: 'text',
            useViewMode: true
          }
        },
        {
          title: 'Name3',
          name: 'name3',
          editOptions: {
            type: 'text',
            useViewMode: true
          }
        },
        {
          title: '排序',
          name: 'sort',
          editOptions: {
            type: 'text',
            useViewMode: true
          }
        },
        {
          title: '备注',
          name: 'remarks',
          editOptions: {
            type: 'text',
            useViewMode: true
          }
        }
      ],
      tableData: [],
      treeTeample: {
        _children: [],
        code: null,
        componentId: null,
        configs: [],
        createTime: null,
        createrId: null,
        itemType: 6,
        dr: 0,
        id: null,
        name: '',
        name1: '',
        name2: '',
        name3: '',
        nestComponentId: null,
        otherConfig: null,
        parentId: null,
        remarks: null,
        require: false,
        sort: 1,
        treePath: null,
        type: 11,
        updateTime: null,
        parentUuid: null,
        uuid: null,
        dataUuid: uuidv4()
      },
      delList: [],
      adding: false,
      collapsed: true,
      GirdData: null,
      grid: null,
      isNew: false,
      componentDefaultData: null,
      last: null
    }
  },
  watch: {
    async value (val) {
      if (val) {
        setTimeout(() => { // 别删！别以为这三行代码毫无意义😎
          this.showEditor = true
        }, 1000)

        this.last = null

        if (this.editItem) { // 非新增
          this.isNew = false
          this.cnode = this.deepCopy(this.editItem)
          this.cnode.items.sort((a, b) => {
            return a.sort - b.sort
          })
          for (const n in this.cnode.items) {
            if (n > 0) {
              this.cnode.items[n].componentId = this.cnode.items[n].nestComponentId
            }
          }

          // this.getCustomComponentType()
          // this.initOptions()

          const tmpWaiting = () => {
            if (this.componentQuestion && this.componentQuestion['judge'] !== null) {
              this.getCustomComponentType()
              this.initOptions()
            } else {
              setTimeout(() => {
                tmpWaiting()
              }, 50)
            }
          }

          tmpWaiting()
        } else {
          // 一定是新增
          this.isNew = true
          this.cnode.name = null
          this.$set(this.cnode, 'items', [])
          for (let i = 0; i < 5; i++) {
            const tmp = this.deepCopy(this.judgeComponent.items[0])
            tmp.id = null
            if (i !== 0) tmp.itemType = 6
            this.cnode.items.push(tmp)
          }
          this.showCustomComponentSelectType = false
          this.showCustomComponentOptions = true
          this.treeShow = false
          this.selectCustomComponentType = 'judge'
          this.tableData.push(this.deepCopy(this.treeTeample))
        }

        this.delList = []
      } else {
        this.showEditor = false
      }
    },
    selectCustomComponentType (val) {
      if (val !== 'tree') {
        $('#grid').hide()
      }
    }
  },
  computed: {
    componentList () {
      return this.$store.state.component.list
    },
    componentQuestion () {
      return this.$store.state.component.question
    },
    componentOptions () {
      return this.$store.state.component.options
    },
    teamType () {
      return parseInt(this.team.split('-')[0])
    },
    projectId () {
      return this.team.split('-')[1]
    },
    isTeamComponent () {
      return this.team && (this.teamType === 2 || this.teamType === 3)
    },
    currentCompanyName () {
      return this.$store.state.user.currentCompanyName
    }
  },
  created () {
    this.initData()
    this.last = null
    setInterval(async () => {
      if (this.cnode.items[0].componentId && this.cnode.items[0].componentId !== this.last) {
        this.componentDefaultData = await getCompontentCache(this.cnode.items[0].componentId)
        console.log('componentDefaultData更新')
        console.log(this.componentDefaultData)
        this.last = this.cnode.items[0].componentId
      }
    }, 100)
  },
  methods: {
    teamName (projectId) {
      try {
        const teamId = this.projectList.find(val => val.id === projectId).orgId
        return this.teamList.find(val => val.id === teamId).name
      } catch (err) {
        return null
      }
    },
    // getCompontentData () { // 同步获取数据
    //   var componentData = ''
    //   $.ajax({
    //     url: DevBaseUrl + '/mvp-manager/component/getComponent?componentId=' + this.cnode.items[0].componentId,
    //     type: 'POST',
    //     async: false, // 同步请求
    //     timeout: 5000,
    //     headers: {
    //       Accept: 'application/json; charset=utf-8',
    //       token: getToken()
    //     },
    //     dataType: 'json', // 返回的数据格式：
    //     success (data, textStatus, jqXHR) {
    //       componentData = data.data
    //     },
    //     error (xhr, textStatus) {
    //     }
    //   })
    //   if (componentData) {
    //     return componentData
    //   } else {
    //     this.$Message.error('数据错误，请刷新重试')
    //   }
    // },
    updateConfig () {
      if (this.selectCustomComponentType === 'tree') {
        this.initTreeTableColumnsName(this.cnode.newComponentData)
      }
    },
    changeComponentType (e, val, index) {

    },
    addRow () {
      const treeTeample = [this.deepCopy(this.treeTeample)]

      const at = this.grid.getIndexOfRow(this.grid.getCheckedRowKeys()[0]) + 1

      const parentValue = this.grid.getRow(this.grid.getCheckedRowKeys()[0])

      treeTeample.parentUuid = parentValue.dataUuid

      treeTeample[0].sort = at

      this.grid.appendRow(treeTeample, {
        at,
        focus: true
      })
    },
    prependTable () {
      const treeTeample = [this.deepCopy(this.treeTeample)]

      treeTeample[0].sort = 0

      this.grid.prependRow(treeTeample, {
        focus: true
      })
    },
    appendRow () {
      const parentRowKey = this.grid.getCheckedRowKeys()[0]

      const parentValue = this.grid.getRow(parentRowKey)

      console.log(parentValue)

      const treeTeample = [this.deepCopy(this.treeTeample)]

      treeTeample[0].parentUuid = parentValue.dataUuid

      this.grid.appendRow(treeTeample, {
        parentRowKey
      })
    },
    removeRow () {
      _.each(this.grid.getCheckedRowKeys(), (rowKey) => {
        this.grid.removeRow(rowKey)
      })
    },
    expandedAllOrCollapsedAll () {
      if (this.collapsed === true) {
        this.grid.expandAll()
      } else {
        this.grid.collapseAll()
      }
      this.collapsed = !this.collapsed
    },
    GridInit (gridData) {
      if ($('#grid')) {
        $('#grid').html('')
        console.log('初始化了一次树形编辑器')
        if (this.grid && this.grid.destroy) this.grid.destroy()
        this.grid = null
        // console.log(this.columns)
        this.grid = new tui.Grid({
          el: $('#grid'),
          data: gridData,
          rowHeaders: [{
            type: 'rowNum',
            title: '顺序'
          }, {
            type: 'radio',
            title: '选中'
          }],
          bodyHeight: 400,
          rowHeight: 40,
          virtualScrolling: true,
          treeColumnOptions: {
            name: 'name',
            useCascadingCheckbox: false
          },
          columns: this.columns
        })
        this.treeShow = true
        $('#grid').show()
      } else {
        console.error('元素不存在')
      }
    },
    getGTCITY () { // 远程获取预先爬取的地区数据
      this.loadingCity = true

      setTimeout(async () => {
        const { data } = await this.$axios.get('https://apio.xyz/city.php')
        this.columns[0].title = '地区名称'
        this.columns[1].title = '区域编码'
        for (const n in this.cnode.items[0].configs) {
          for (const i in this.columns) {
            if (this.columns[i].prop === this.cnode.items[0].configs[n].code) {
              this.cnode.items[0].configs[n].configData.configData = this.columns[i].title
            }
          }
        }
        this.tableData = []
        let node = null
        for (const n in data['86']) { // 省份
          const newNode = this.deepCopy(this.treeTeample)
          newNode.dataUuid = uuidv4()
          newNode.parentUuid = null
          newNode.sort = node ? (node.sort + 1) : 1
          newNode.name = data['86'][n]
          newNode.name1 = n
          this.tableData.push(newNode)
          node = newNode

          for (const i in data[n]) { // 市级
            const node2 = newNode
            this.treeSum = this.treeSum + 1
            const newNode2 = this.deepCopy(this.treeTeample)
            newNode2.dataUuid = uuidv4()
            newNode2.parentUuid = node2.dataUuid

            if (!node2._children) {
              node2._children = []
            }

            if (node2._children.length) {
              newNode2.sort = node2._children[node2._children.length - 1].sort + 1
            } else {
              newNode2.sort = 1
            }

            newNode2.name = data[n][i]
            newNode2.name1 = i

            node2._children.push(newNode2)

            for (const x in data[i]) { // 县区
              const node3 = newNode2
              this.treeSum = this.treeSum + 1
              const newNode3 = this.deepCopy(this.treeTeample)
              newNode3.dataUuid = uuidv4()
              newNode3.parentUuid = node3.dataUuid

              if (!node3._children) {
                node3._children = []
              }

              if (node3._children.length) {
                newNode3.sort = node3._children[node3._children.length - 1].sort + 1
              } else {
                newNode3.sort = 1
              }

              newNode3.name = data[i][x]
              newNode3.name1 = x

              node3._children.push(newNode3)
            }
          }
        }

        // console.log(this.tableData)
        this.GridInit(this.tableData)
        let tmp = this.grid.getRows()
        tmp = toTreeData(tmp, 'null', 'dataUuid', 'parentUuid', '_children')
        this.GridInit(tmp)
        this.loadingCity = false
        // this.customComponentOk()
        this.$Modal.confirm({
          title: '恭喜',
          content: '<p>数据成功导入。</p>'
        })
      }, 100)
    },
    getMZCITY () {
      this.$Modal.confirm({
        title: '提示',
        content: '<p>已自动为您将民政局数据与统计局数据合并，请获取统计局数据。</p>'
      })
    },
    downloadExcelTemplate () {
      window.open(window.location.origin + '/static/excel-to-tree-v10.xlsm')
    },
    exportAsExcel () {
      const wb = XLSX.utils.book_new()
      const ws = XLSX.utils.json_to_sheet(this.tableData)
      wb.SheetNames.push('Tree')
      wb.Sheets['Tree'] = ws
      XLSX.writeFile(wb, 'out.xlsx')
    },
    async initOptions () {
      if (this.cnode.items[0].uuid !== '00000000-0000-0000-0000-000000000016') { // Tree
        const tmp = 5 - this.cnode.items.length
        if (tmp > 0) {
          let template = await getCompontentCache(this.componentQuestion['judge'].id)
          let temp = this.deepCopy(template.items[0])
          if (this.cnode.items[0].uuid === this.componentQuestion['singleSelect'].uuid) {
            let templateItem = await getCompontentCache(this.componentQuestion['singleSelect'].id)
            temp = this.deepCopy(templateItem.items[0])
          } else if (this.cnode.items[0].uuid === this.componentQuestion['multiSelect'].uuid) {
            let templateItem = await getCompontentCache(this.componentQuestion['multiSelect'].id)
            temp = this.deepCopy(templateItem.items[0])
          } else if (this.cnode.items[0].uuid === this.componentQuestion['multiInput'].uuid) {
            let templateItem = await getCompontentCache(this.componentOptions['input'].id)
            temp = this.deepCopy(templateItem.items[0])
          }

          temp.id = null
          temp.code = null

          if (this.cnode.items[0].uuid !== '00000000-0000-0000-0000-000000000012') { // 多填
            temp.itemType = 6
          }

          for (let n = tmp; n < 4; n++) {
            this.cnode.items.push(this.deepCopy(temp))
          }
        }
      }
    },
    /**
     * 修改自定义组件名称
     */
    changName (val) {
      this.cnode.items[0].name = this.cnode.name
    },
    initTreeTableColumnsName (node = this.cnode) {
      for (const n in node.items[0].configs) {
        for (const i in this.columns) {
          if (this.columns[i].name === node.items[0].configs[n].code) {
            this.columns[i].title = node.items[0].configs[n].configData.configData
          }
        }
      }
      console.log(this.columns)
    },
    async changeCustomComponent (id, item, index) {
      const name = item.name
      const remarks = item.remarks
      const require = item.require

      const template = await getCompontentCache(id)
      let tmp = this.deepCopy(template)
      item = this.deepCopy(template.items[0])
      item.require = require
      item.name = name
      item.remarks = remarks

      // console.log(item)

      if ((this.selectCustomComponentType === 'multiInput' || this.selectCustomComponentType === 'other') && tmp.type === 2 && (tmp.items[0].uuid === '00000000-0000-0000-0000-000000000005' || tmp.items[0].uuid === '00000000-0000-0000-0000-000000000007' || tmp.items[0].uuid === '00000000-0000-0000-0000-000000000013')) {
        // console.log(tmp)
        let tmp2 = tmp.items
        tmp2.shift()
        // for (const y in tmp2) {
        //   tmp2[y].id = null
        // }
        item['children'] = tmp2
      }

      // console.log(item)

      if (index || parseInt(index) === 0) {
        this.$set(this.cnode.items, index, item)
      }
      // console.log(this.cnode)
    },
    updateComponentData (val, node) { // 这里的函数逻辑和nodeEditBoard不一样
      /**
      * 这里的逻辑特别复杂，千万别修改
      */
      this.newComponentData = val
      if (node) {
        node.newComponentData = this.newComponentData
      }
    },
    customComponentOk () {
      this.$refs['formCustomComponentEdit'].validate(async (valid) => {
        if (valid) {
          /**
           * 警告
           * 禁止修改下面的内容
           */

          // 过滤数据
          if (!this.editItem) {
            delete this.cnode.id
            this.cnode.type = 2
          }
          // for (const n in this.cnode.items) {
          //   if (this.cnode.items[n].name === null || this.cnode.items[n].name === '') {
          //     this.cnode.items.splice(n, 1)
          //     console.log(this.cnode.items[n].name)
          //   }
          // }

          if (this.cnode.newComponentData) {
            if (this.cnode.newComponentData.useItem) {
              this.cnode.items[0].configs = this.cnode.newComponentData.configs
            } else {
              this.cnode.items[0].configs = this.cnode.newComponentData.items[0].configs
            }
            delete this.cnode.newComponentData
          }

          this.cnode.items[0].name = this.cnode.name

          if (!this.cnode.items[0].name) {
            this.$Message.info('没有输入组件名称，保存操作被取消。')
            return
          }

          const tmp = this.deepCopy(this.cnode.items).filter(item => {
            return item.name && item.name.replace(/\s/g, '') !== ''
          })

          if (this.selectCustomComponentType !== 'other' && this.selectCustomComponentType !== 'tree' && tmp.length < 2) {
            this.$Message.info('至少填写一个选项')
            return
          }

          this.cnode.items = this.cnode.items.filter(item => {
            return item.name && item.name.replace(/\s/g, '') !== ''
          })

          this.loading = true

          for (const n in this.cnode.items) {
            if (parseInt(n) > 0) {
              if (this.cnode.items[n].newComponentData) {
                if (this.cnode.items[n].newComponentData.useItem) {
                  this.cnode.items[n].configs = this.cnode.items[n].newComponentData.configs
                } else {
                  this.cnode.items[n].configs = this.cnode.items[n].newComponentData.items[n].configs
                }
                delete this.cnode.items[n].newComponentData
                // this.cnode.items[n].configs = this.cnode.items[n].newComponentData
              }
              if (this.cnode.items[n]['children'] && this.cnode.items[n]['children'].length) {
                for (const x in this.cnode.items[n]['children']) {
                  if (this.cnode.items[n]['children'][x].newComponentData) {
                    if (this.cnode.items[n]['children'][x].newComponentData.useItem) {
                      this.cnode.items[n]['children'][x].configs = this.cnode.items[n]['children'][x].newComponentData.configs
                    } else {
                      this.cnode.items[n]['children'][x].configs = this.cnode.items[n]['children'][x].newComponentData.items[n].configs
                    }
                    delete this.cnode.items[n]['children'][x].newComponentData
                    // this.cnode.items[n].configs = this.cnode.items[n].newComponentData
                  }
                }
              }
            }
            if (this.cnode.items[0].uuid !== '00000000-0000-0000-0000-000000000016') this.cnode.items[n].sort = n
            for (const i in this.cnode.items[n].configs) {
              //   delete this.cnode.items[n].configs[i].componentItemId
              //   this.cnode.items[n].configs[i].id
              this.cnode.items[n].configs[i].configData.componentId = this.cnode.items[n].componentId
              this.cnode.items[n].configs[i].configData.id = null
            }
            // this.cnode.items[n].configs = null
            if (!this.editItem) delete this.cnode.items[n].id // 如果是新增
            this.cnode.items[n].nestComponentId = this.cnode.items[n].componentId
            if (this.cnode.items[0].uuid !== '00000000-0000-0000-0000-000000000012' && parseInt(n) > 0) {
              this.cnode.items[n].uuid = null
            }// else {
            //   this.cnode.items[n].nestComponentId = null
            // }
            this.cnode.items[n].componentId = null
          }

          if (this.cnode.items[0].uuid === '00000000-0000-0000-0000-000000000012') { // 多填
            this.cnode.items[0].nestComponentId = this.componentQuestion['multiInput'].id
          } else if (this.cnode.items[0].uuid === '00000000-0000-0000-0000-000000000016') { // 树形
            this.cnode.items[0].nestComponentId = this.componentQuestion['tree'].id
          }

          if (this.selectCustomComponentType === 'tree') {
            this.cnode.items = [this.cnode.items[0]]
            let tmp = null
            if (this.isNew) {
              tmp = this.grid.getRows()
              // tmp = new JsTreeList.ListToTree(tmp, {
              //   key_id: 'dataUuid',
              //   key_parent: 'parentUuid',
              //   key_child: 'children',
              //   key_last: null,
              //   uuid: true,
              //   empty_children: true
              // }).GetTree()
              tmp = toTreeData(tmp, 'null', 'dataUuid', 'parentUuid', 'children')
              this.cnode.items.push(...tmp)
              this.cnode.items.push(...this.delList)
            } else {
              const { createdRows, updatedRows, deletedRows } = this.grid.getModifiedRows() // 只保存被修改的数据
              for (const n in createdRows) {
                if (createdRows[n].children) delete createdRows[n].children
                if (createdRows[n].configs) delete createdRows[n].configs
                if (createdRows[n].code) delete createdRows[n].code
              }

              // for (const n in updatedRows) {
              //   if (updatedRows[n].children) delete updatedRows[n].children
              //   if (updatedRows[n].configs) delete updatedRows[n].configs
              //   if (updatedRows[n].code) delete updatedRows[n].code
              // }

              for (const n in deletedRows) {
                deletedRows[n].dr = 1
              }

              console.log(createdRows)
              console.log(updatedRows)
              console.log(deletedRows)

              tmp = deletedRows.concat(createdRows)
              tmp = tmp.concat(updatedRows) // 顺序很重要

              this.$Message.info('正在保存中...', 6000)
              this.cnode.items = []
              this.cnode.items = tmp
              const result = await updateComponent(this.cnode)
              if (result) {
                this.initData()
                this.$emit('updateList')
                this.$Message.success('自定义组件创建成功！')
                this.$emit('selectNewId', {
                  componentId: result.id,
                  selectUUID: this.nowSelectUUID
                })
              } else {
                this.$Message.error('自定义组件创建失败！')
              }

              this.loading = false
              this.$emit('input', false)
              return
            }
          } else {
            if (this.delList && this.delList.length) {
              this.cnode.items.push(...this.delList)
            }
          }

          this.$Message.info('正在保存中...', 6000)
          const tmpItems = this.cnode.items.splice(1, this.cnode.items.length - 1)
          for (const n in tmpItems) { // 特殊处理
            for (const i in tmpItems[n].children) {
              for (const ee in tmpItems[n].children) {
                for (const mm in tmpItems[n].children[ee].configs) {
                  tmpItems[n].children[ee].configs[mm].configData.id = null
                }
              }
              if (tmpItems[n].children[i].type !== 11) {
                tmpItems[n].children[i].type = 0
              }
            }
          }
          this.cnode.items[0].children = tmpItems
          console.log(this.cnode)

          const orgFilter = {
            sourceId: this.org,
            sourceType: 1
          }

          const relationComponentList = []
          if (this.moveToComponent === 'org') {
            relationComponentList.push(orgFilter)
          } else if (this.moveToComponent === 'team') {
            const teamId = this.projectList.find(val => val.id === this.projectId).orgId
            const teamFilter = {
              sourceId: teamId,
              sourceType: 2
            }
            relationComponentList.push(teamFilter)
          } else if (this.team && this.moveToComponent === 'false') {
            const teamFilter = {
              sourceId: this.team.split('-')[1],
              sourceType: parseInt(this.team.split('-')[0])
            }
            relationComponentList.push(teamFilter)
          } else {
            relationComponentList.push(orgFilter)
          }

          this.cnode.relationComponentList = relationComponentList

          let result = await saveCompontent(this.cnode)

          if (result.id) {
            this.initData()
            this.$emit('updateList')
            this.$Message.success('自定义组件保存成功')
            this.$emit('selectNewId', {
              componentId: result.id,
              selectUUID: this.nowSelectUUID
            })
          } else {
            this.$Message.error('自定义组件保存失败')
          }

          // if (this.isNew) {
          // } else {
          //   result = await updateComponent(this.cnode)

          //   if (result) {
          //     this.initData()
          //     this.$emit('updateList')
          //     this.$Message.success('自定义组件保存成功！')
          //     this.$emit('selectNewId', {
          //       componentId: result.id,
          //       selectUUID: this.nowSelectUUID
          //     })
          //   } else {
          //     this.$Message.error('自定义组件保存失败！')
          //   }
          // }

          this.loading = false
          this.$emit('input', false)
        }
      })
    },
    customComponentCancel () {
      this.initData()
      this.$emit('input', false)
    },
    initData () {
      console.log('初始化自定义组件编辑组件')
      this.selectCustomComponentType = null
      this.showCustomComponentSelectType = false
      this.showCustomComponentOptions = false
      this.moveToComponent = 'false'
    },
    deleteCustomComponentOption (option, index) {
      this.$Modal.confirm({
        title: '提示',
        content: '删除后无法恢复，确定删除吗？',
        onCancel: () => { },
        onOk: () => {
          this.cnode.items[index].dr = 1
          this.delList.push(this.cnode.items.splice(index, 1)[0])
        }
      })
    },
    clickCustomComponentTypeButton (type) {
      if (this.editItem) {
        this.$Modal.confirm({
          title: '警告',
          content: '切换选项后会导致自定义配置项数据和部分选项数据丢失且无法恢复，确定切换吗？',
          onCancel: () => { },
          onOk: () => {
            this.customComponentHanck(type)
          }
        })
      } else {
        this.customComponentHanck(type)
      }
    },
    async customComponentHanck (type) {
      /**
       * 警告
       * 禁止修改该函数！
       */
      console.time('深拷贝执行时间')
      const newNode = this.deepCopy(this.cnode)
      console.timeEnd('深拷贝执行时间')

      if (type !== 'tree' && this.selectCustomComponentType === 'tree') {
        newNode.items = [newNode.items[0]]
        newNode.items.push(...this.tableData)
      }

      switch (type) {
        case 'judge': {
          let templateItems = await getCompontentCache(this.componentQuestion[type].id)
          for (const n in newNode.items) {
            if (parseInt(n) < 3) {
              if (parseInt(n) === 0) {
                newNode.items[0].uuid = templateItems.items[0].uuid
                newNode.items[n].itemType = templateItems.items[0].itemType
              } else {
                newNode.items[n].itemType = 6
              }
              newNode.items[n].componentId = templateItems.items[0].componentId
              newNode.items[n].otherConfig = templateItems.items[0].otherConfig
              newNode.items[n].type = templateItems.items[0].type
              newNode.items[n].require = templateItems.items[0].require
              // newNode.items[n].itemType = templateItems.items[0].itemType
              newNode.items[n].configs = this.deepCopy(templateItems.items[0].configs)
            }
          }

          this.cnode = this.deepCopy(newNode)
          this.$set(this.cnode, 'items', newNode.items)

          this.initOptions()
          this.showCustomComponentSelectType = false
          this.showCustomComponentOptions = true
          this.treeShow = false
          break
        }
        case 'singleSelect': {
          let templateItems = await getCompontentCache(this.componentQuestion[type].id)
          for (const n in newNode.items) {
            if (parseInt(n) === 0) {
              newNode.items[0].uuid = templateItems.items[0].uuid
              newNode.items[n].itemType = templateItems.items[0].itemType
            } else {
              newNode.items[n].itemType = 6
            }
            newNode.items[n].componentId = templateItems.items[0].componentId
            newNode.items[n].otherConfig = templateItems.items[0].otherConfig
            newNode.items[n].type = templateItems.items[0].type
            newNode.items[n].require = templateItems.items[0].require
            // newNode.items[n].itemType = templateItems.items[0].itemType
            newNode.items[n].configs = this.deepCopy(templateItems.items[0].configs)
          }

          this.cnode = this.deepCopy(newNode)
          this.$set(this.cnode, 'items', newNode.items)

          this.initOptions()
          this.showCustomComponentSelectType = false
          this.showCustomComponentOptions = true
          this.treeShow = false
          break
        }
        case 'multiSelect': {
          let templateItems = await getCompontentCache(this.componentQuestion[type].id)
          for (const n in newNode.items) {
            console.log(n)
            if (parseInt(n) === 0) {
              newNode.items[0].uuid = templateItems.items[0].uuid
              newNode.items[n].itemType = templateItems.items[0].itemType
            } else {
              newNode.items[n].itemType = 6
            }
            newNode.items[n].componentId = templateItems.items[0].componentId
            newNode.items[n].otherConfig = templateItems.items[0].otherConfig
            newNode.items[n].type = templateItems.items[0].type
            newNode.items[n].require = templateItems.items[0].require
            // newNode.items[n].itemType = templateItems.items[0].itemType
            newNode.items[n].configs = this.deepCopy(templateItems.items[0].configs)
          }

          this.cnode = this.deepCopy(newNode)
          this.$set(this.cnode, 'items', newNode.items)

          this.initOptions()
          this.showCustomComponentSelectType = false
          this.showCustomComponentOptions = true
          this.treeShow = false
          break
        }
        case 'multiInput':
          for (const n in newNode.items) {
            if (parseInt(n) === 0) {
              let templateItems = await getCompontentCache(this.componentQuestion[type].id)
              newNode.items[0].uuid = templateItems.items[0].uuid
              newNode.items[0].componentId = templateItems.items[0].componentId
              newNode.items[0].otherConfig = templateItems.items[0].otherConfig
              newNode.items[0].type = templateItems.items[0].type
              newNode.items[0].itemType = templateItems.items[0].itemType
              newNode.items[n].require = templateItems.items[0].require
              newNode.items[0].configs = this.deepCopy(templateItems.items[0].configs)
            } else {
              let templateItems = await getCompontentCache(this.componentOptions['input'].id)
              newNode.items[n].uuid = templateItems.items[0].uuid
              newNode.items[n].componentId = templateItems.items[0].componentId
              newNode.items[n].otherConfig = templateItems.items[0].otherConfig
              newNode.items[n].type = templateItems.items[0].type
              newNode.items[n].itemType = templateItems.items[0].itemType
              newNode.items[n].require = templateItems.items[0].require
              newNode.items[n].configs = this.deepCopy(templateItems.items[0].configs)
            }
          }

          this.cnode = this.deepCopy(newNode)
          this.$set(this.cnode, 'items', newNode.items)

          this.initOptions()
          this.showCustomComponentSelectType = true
          this.showCustomComponentOptions = true
          this.treeShow = false
          break
        case 'tree': {
          for (const n in newNode.items) {
            if (parseInt(n) === 0) {
              let templateItems = await getCompontentCache(this.componentQuestion[type].id)
              newNode.items[0].uuid = templateItems.items[0].uuid
              newNode.items[0].componentId = templateItems.items[0].componentId
              newNode.items[0].otherConfig = templateItems.items[0].otherConfig
              newNode.items[0].type = templateItems.items[0].type
              newNode.items[0].sort = 1
              newNode.items[0].itemType = templateItems.items[0].itemType
              // newNode.items[0].require = templateItems.items[0].require
              newNode.items[0].configs = this.deepCopy(templateItems.items[0].configs)
            } else {
              newNode.items[n].otherConfig = null
              newNode.items[n].configs = []
              newNode.items[n].type = 11
              newNode.items[n].itemType = 6
              newNode.items[n].sort = parseInt(n) + 1
              if (!newNode.items[n].children) {
                newNode.items[n].children = []
              }
              if (!newNode.items[n].dataUuid) {
                newNode.items[n].dataUuid = uuidv4()
              }
            }
          }

          console.time('拷贝执行时间')
          this.cnode = this.deepCopy(newNode)
          this.$set(this.cnode, 'items', newNode.items)

          const tmp = this.deepCopy(this.cnode.items)
          tmp.shift()
          console.timeEnd('拷贝执行时间')

          console.time('转为Tree执行时间')
          // this.tableData = new JsTreeList.ListToTree(tmp, {
          //   key_id: 'dataUuid',
          //   key_parent: 'parentUuid',
          //   key_child: '_children',
          //   key_last: null,
          //   uuid: true,
          //   empty_children: true
          // }).GetTree()
          this.tableData = toTreeData(tmp, 'null', 'dataUuid', 'parentUuid', '_children')
          this.treeShow = true
          console.timeEnd('转为Tree执行时间')

          console.time('其它处理时间')
          this.initOptions()

          this.initTreeTableColumnsName()

          this.showCustomComponentSelectType = false
          this.showCustomComponentOptions = false
          console.timeEnd('其它处理时间')

          console.time('树形组件初始化耗时')
          this.GridInit(this.tableData)
          console.timeEnd('树形组件初始化耗时')

          break
        }
        // case 'other':
        //   this.$set(this.cnode.items, 0, this.deepCopy(this.componentOptions['input'].items[0]))
        //   this.showCustomComponentSelectType = false
        //   this.showCustomComponentOptions = false
        //   break
      }

      this.selectCustomComponentType = type

      if (this.cnode.items[0] && this.cnode.items[0].name) {
        this.cnode.name = this.cnode.items[0].name
      }

      console.log(this.cnode)
    },
    getCustomComponentType () {
      // console.log(this.cnode.items[0])
      // console.log(this.componentQuestion['judge'])
      if (this.cnode.items[0].uuid === this.componentQuestion['judge'].uuid) {
        this.showCustomComponentSelectType = false
        this.showCustomComponentOptions = true
        this.treeShow = false
        this.selectCustomComponentType = 'judge'
      } else if (this.cnode.items[0].uuid === this.componentQuestion['singleSelect'].uuid) {
        this.showCustomComponentSelectType = false
        this.showCustomComponentOptions = true
        this.treeShow = false
        this.selectCustomComponentType = 'singleSelect'
      } else if (this.cnode.items[0].uuid === this.componentQuestion['multiSelect'].uuid) {
        this.showCustomComponentSelectType = false
        this.showCustomComponentOptions = true
        this.treeShow = false
        this.selectCustomComponentType = 'multiSelect'
      } else if (this.cnode.items[0].uuid === this.componentQuestion['multiInput'].uuid) {
        this.showCustomComponentSelectType = true
        this.showCustomComponentOptions = true
        this.treeShow = false
        this.selectCustomComponentType = 'multiInput'
      } else {
        this.showCustomComponentSelectType = false
        this.showCustomComponentOptions = false
        this.treeShow = true
        this.selectCustomComponentType = 'tree'

        getCompontentCache(this.cnode.id).then(r => {
          const tmp = r.items
          tmp.shift()
          // this.GirdData = new JsTreeList.ListToTree(tmp, {
          //   key_id: 'dataUuid',
          //   key_parent: 'parentUuid',
          //   key_child: '_children',
          //   key_last: null,
          //   uuid: true,
          //   empty_children: true
          // }).GetTree()
          console.time('tree算法')
          this.GirdData = toTreeData(tmp, 'null', 'dataUuid', 'parentUuid', '_children')
          console.timeEnd('tree算法')
          this.GridInit(this.GirdData)
        })
      }
    },
    async addCustomComponentOption () {
      // 不管是什么类型，肯定已经存在一个正常的item
      // const tmp = this.deepCopy(this.cnode.items[this.cnode.items.length - 1])
      // tmp.id = null
      // tmp.itemType = 6
      // this.$set(this.cnode.items, this.cnode.items.length, tmp)
      switch (this.selectCustomComponentType) {
        case 'singleSelect':
          const tmp = this.deepCopy((await getCompontentCache(this.componentQuestion[this.selectCustomComponentType].id)).items[0])
          tmp.id = null
          tmp.itemType = 6
          this.$set(this.cnode.items, this.cnode.items.length, tmp)
          break
        case 'multiSelect':
          const tmp1 = this.deepCopy((await getCompontentCache(this.componentQuestion[this.selectCustomComponentType].id)).items[0])
          tmp1.id = null
          tmp1.itemType = 6
          this.$set(this.cnode.items, this.cnode.items.length, tmp1)
          break
        case 'multiInput':
          const tmp2 = this.deepCopy((await getCompontentCache(this.componentOptions[this.selectCustomComponentType].id)).items[0])
          tmp2.id = null
          this.$set(this.cnode.items, this.cnode.items.length, tmp2)
          break
      }
    }
  }
}
</script>

