export default class Node {
  constructor ({ ...args }) {
    this.uuid = args.uuid
    this.nextUuid = args.nextUuid
    this.parentUuid = args.parentUuid
    this.prevUuid = args.prevUuid
    this.children = args.children || []
    this.lock = false

    this.title = args.title // 标题
    this.code = args.code || '' // 自定义代码
    this.additional = args.additional || false // 是否追加题， false否，true是
    this.automateCondition = args.automateCondition || null // 自动做题条件 String
    this.automateConditionNote = args.automateConditionNote || null // string 自动做题条件说明
    this.componentId = args.componentId || null // 组件ID
    this.computes = args.computes || null // 计算值 String
    this.customVar = args.customVar || null // 自定义变量 String
    this.expanded = args.expanded === null || args.expanded === undefined ? true : args.expanded // 展开状态，false收缩，true展开
    this.id = args.id || null // 节点ID
    this.dr = args.dr || null // 删除标志
    this.jumpCondition = args.jumpCondition || null // 跳转条件 String
    this.macroLogic = args.macroLogic || null // 宏逻辑 String
    this.mustFlag = (args.mustFlag === null || args.mustFlag === undefined) ? true : args.mustFlag // 是否必填， false否，true是
    this.unique = !!args.unique // 唯一， false否，true是
    // this.unique = args.unique // 唯一， false否，true是
    this.point = args.point || '' // 附加
    this.nestProjectId = args.nestProjectId || null // 共用包ID
    this.nestProjectTitle = args.nestProjectTitle || false // 是否使用嵌套包标题为前缀
    this.parentId = args.parentId // 父节点IDs
    this.permissionCondition = args.v || null // 权限条件 String
    this.premissionConditionNote = args.premissionConditionNote || null // string 权限条件说明
    this.projectId = args.projectId || null // 项目ID
    this.remarks = args.remarks || '' // 备注 String
    this.repeatFlag = args.repeatFlag || false // 是否每次调用都重复做题，false否，true是
    this.showCondition = args.showCondition || null // 显示条件 String
    this.sort = args.sort || 1024 // 排序值
    this.tip = args.tip || null // 提示 String
    this.type = args.type || 1 // 节点类型。1：问题；2：选项
    this.options = args.options || []
    this.tagInfo = args.tagInfo || []
    this.crossState = args.crossState || false // 是否跨项目
    this.crossProjectEnName = args.crossProjectEnName || '' // 跨项目引用共用包的名称

    this.nodeComponentConfigDatas = args.nodeComponentConfigDatas || [] // 组件配置信息

    this.rejectionCondition = args.rejectionCondition || null // 排异代码
    this.nodeConfigData = args.nodeConfigData || null // 节点配置信息

    // this.componentInfo = args.componentInfo || { // 节点配置信息
    //   items: [ // 不得删除
    //     {
    //       componentId: null
    //     }
    //   ]
    // }
    this.componentInfo = args.componentInfo || null

    this.jumpConditionNote = args.jumpConditionNote || null // 跳转条件 string
    this.showConditionNote = args.showConditionNote || null // string // 显示条件
    this.translationCondition = args.translationCondition || null
    this.translationConditionNote = args.translationConditionNote || null
    this.permissionConditionNote = args.permissionConditionNote || null
    this.validateCondition = args.validateCondition || null
    this.validateConditionNote = args.validateConditionNote || null
  }
}
