import Request from '@/libs/request'

export function uploadFile (file, compress = false) {
  const formData = new FormData()
  formData.append('file', file)
  formData.append('compress', compress)
  return Request.post('/mvp-manager/file/upload', formData, { headers: { 'Content-Type': 'multipart/form-data' } }).then(resp => resp.data)
}
