<template>
  <div class="check-report" v-click-outside="hideCheckResult">
    <div class="title" @click="showCheckResult">{{ title }}</div>
    <div class="check-result-show" v-show="showResult">
      <div v-if="!checkResult.length">没有未通过测试的报告</div>
      <div class="tree-container" v-else>
        <div class="tree-title">
          以下内容未通过测试：
        </div>
        <Tree
          class="tree"
          @content-click="jumpTo"
          :treeData="checkResult">
          <div slot-scope="{ parentData, data }" class="node-content">
            <div v-if="data.nodeType===0" class="check-tree-node report-node">
              <div class="type">报告</div>：
              <div class="node-code">{{ data.code }}</div>
              <div class="node-name">{{ data.title }}</div>
            </div>
            <div v-if="data.nodeType===1" class="check-tree-node chapter-node">
              <div class="type">章节</div>：
              <div class="node-code">{{ data.code }}</div>
              <div class="node-name">{{ data.title }}</div>
            </div>
            <div v-if="data.nodeType===2" class="check-tree-node code-node">
              <div class="type">代码块</div>：
              <div class="node-code">{{data.code}}</div>
            </div>
          </div>
        </Tree>
      </div>
    </div>
  </div>
</template>

<script>
import reportApi from '@/api/report'
import Tree from '@/components/simple-tree'

export default {
  props: {
    projectId: {
      type: String,
      default: ''
    },
    projectRootId: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: '检查报告测试状态'
    }
  },
  components: {
    Tree
  },
  data () {
    return {
      checkResult: [],
      showResult: false
    }
  },
  methods: {
    async refreshCheckResult () {
      let data = await reportApi.checkReport({
        projectId: this.projectId,
        projectRootId: this.projectRootId
      })
      this.handleReport(data)
      this.checkResult = data
    },
    async showCheckResult () {
      await this.refreshCheckResult()
      this.showResult = true
    },
    hideCheckResult () {
      this.showResult = false
    },
    handleReport (reportData) {
      for (let data of reportData) {
        data.nodeType = 0
        data.children = data.children || []
        if (data.outlineList && data.outlineList.length) {
          this.handleChapter(data.outlineList)
          data.children.push(...data.outlineList)
        }
      }
    },
    handleChapter (chapterData) {
      for (let data of chapterData) {
        data.nodeType = 1
        data.children = data.children || []
        if (data.codeList && data.codeList.length) {
          for (let code of data.codeList) {
            code.nodeType = 2
          }
          data.children.push(...data.codeList)
        }
      }
    },
    jumpTo (event, vNode) {
      let info = {}
      if (vNode.nodeData.nodeType === 0) {
        info.report = vNode.nodeData.id
      } else if (vNode.nodeData.nodeType === 1) {
        info.chapter = vNode.nodeData.id
        let report = vNode.$parent
        while (report.nodeData.nodeType !== 0) {
          report = report.$parent
          if (!report) return
        }
        info.report = report.nodeData.id
      } else if (vNode.nodeData.nodeType === 2) {
        info.code = vNode.nodeData.id
        let chapter = vNode.$parent
        while (chapter.nodeData.nodeType !== 1) {
          chapter = chapter.$parent
          if (!chapter) return
        }
        info.chapter = chapter.nodeData.id
        let report = chapter.$parent
        while (report.nodeData.nodeType !== 0) {
          report = report.$parent
          if (!report) return
        }
        info.report = report.nodeData.id
      }
      this.$bus.$emit('jump-to-code', info)
      this.hideCheckResult()
    }
  }
}
</script>

