/**
 * 针对iView模态框写的拖动缩放指令
 * 其他地方请使用v-drag-resize指令
 */
import dragResize from './dragResize'

function bind (el, binding, vNode) {
  let childNode = el.querySelector('.ivu-modal-content')
  if (childNode) {
    childNode.style.position = 'relative'
    childNode.style.left = '0'
    childNode.style.right = '0'
    childNode.style.top = '0'
    childNode.style.bottom = '0'
    childNode.style.width = '100%'
    childNode.style.height = '100%'
  }
  if (!binding.value) {
    binding.value = {
      width: true
    }
  }
  let node = el.querySelector('.ivu-modal')
  if (node) {
    node.style.position = 'fixed'
    dragResize.bind(node, binding, vNode)
  }
}

function unbind (el, binding, vNode) {
  let node = el.querySelector('.ivu-modal')
  if (node) {
    dragResize.unbind(node, binding, vNode)
  }
}

export default {
  bind,
  unbind
}
