<template lang="pug">
.
Layout.layout(:class="'layout-helper-' + helperPosition")
  Layout.content(:class="['content-helper-' + helperPosition, (dragging || dragHover) ? 'dragging-' + helperPosition : '']", ref="pageContent")
    Header.layout-header(v-if='!fullScreen')
      keep-alive
        PageHeader
    Content.layout-content(:class="{'full-screen': fullScreen}")
      router-view.layout-content-view
    Footer.layout-footer(v-if='!fullScreen')
      .footer
        | ©2018 - Lvpinhui (Beijing) S&T Co,.Ltd, All rights reserved. {{ $version }}
  .helper(:class="['helper-' + helperPosition, (dragging || dragHover) ? 'dragging-' + helperPosition : '']", :style="size", ref="helper")
    Helper.helper-component(@change-position="changePosition")
  PopMenu
</template>

<script>
import Header from './Header'
import PopMenu from './PopMenu'
import Helper from '@/components/helper'

import { ProdSocket } from '../libs/config'
import io from 'socket.io-client'

export default {
  components: {
    Header,
    PopMenu,
    Helper
  },
  computed: {
    fullScreen () {
      return this.$store.state.layout.fullScreen
    }
  },
  data () {
    return {
      helperPosition: 'bottom',
      size: {
        width: '300px',
        height: '300px'
      },
      dragging: false,
      dragHover: false,
      throttleMouseMove: _.throttle(this.checkMouseMove, 200)
    }
  },
  async created () {
    await this.initSocket()
  },
  mounted () {
    this.changePosition('bottom')
    document.addEventListener('mousemove', this.throttleMouseMove)
    document.addEventListener('mousedown', this.checkMouseDown)
    document.addEventListener('mouseup', this.clearDragging)
    document.addEventListener('mouseleave', this.clearDragging)
  },
  beforeDestroy () {
    document.removeEventListener('mousemove', this.throttleMouseMove)
    document.removeEventListener('mousedown', this.checkMouseDown)
    document.removeEventListener('mouseup', this.clearDragging)
    document.removeEventListener('mouseleave', this.clearDragging)
    if (typeof this.$bus.$socket.close === 'function') {
      this.$bus.$socket.close()
    }
  },
  methods: {
    async initSocket () {
      if (this.$bus.$socket && typeof this.$bus.$socket.close === 'function') {
        this.$bus.$socket.close()
      }
      this.$bus.$socket = io(ProdSocket, {
        query: {
          userInfo: JSON.stringify(this.$store.state.user.userInfo)
        },
        transports: ['websocket']
      })

      this.$bus.$socket.on('connect', () => {
        console.log('%csocket 连接成功!', 'color: green; font-weight: bold')
      })

      this.$bus.$socket.on('disconnect', () => {
        console.log('%csocket 断开连接!', 'color: red; font-weight: bold')
      })

      this.$bus.$socket.on('message', (data) => {
        console.log(`%c服务器消息：%c${data.data.payload}`, 'color: blue; font-weight: bold', 'font-weight: bold')
      })
    },
    changePosition (position) {
      this.helperPosition = position
      if (this.helperPosition === 'bottom') {
        this.size = {
          minHeight: this.storageGetItem('minHeight') || '300px'
        }
      } else {
        this.size = {
          minWidth: this.storageGetItem('minWidth') || '300px'
        }
      }
    },
    checkMouseMove (event) {
      if (this.dragging) {
        if (this.helperPosition === 'bottom') {
          let minHeight = document.body.clientHeight - event.clientY
          this.size = { minHeight: minHeight + 'px' }
          this.storageSetItem('minHeight', minHeight + 'px')
        } else {
          let minWidth = this.helperPosition === 'left' ? event.clientX : document.body.clientWidth - event.clientX
          this.size = { minWidth: minWidth + 'px' }
          this.storageSetItem('minWidth', minWidth + 'px')
        }
      } else {
        let position = this.checkMousePosition(event)
        if (position) {
          this.dragHover = true
        } else {
          this.dragHover = false
        }
      }
    },
    checkMousePosition (event) {
      let deta = 10
      let contentRect = this.$refs.pageContent.$el.getBoundingClientRect()
      if (this.helperPosition === 'bottom') {
        let minY = contentRect.bottom - deta
        let maxY = contentRect.bottom + deta
        if (event.clientY >= minY && event.clientY <= maxY) {
          return 'bottom'
        }
      } else if (this.helperPosition === 'left') {
        let minX = contentRect.left - deta
        let maxX = contentRect.left + deta
        if (event.clientX >= minX && event.clientX <= maxX) {
          return 'left'
        }
      } else if (this.helperPosition === 'right') {
        let minX = contentRect.right - deta
        let maxX = contentRect.right + deta
        if (event.clientX >= minX && event.clientX <= maxX) {
          return 'right'
        }
      }
      return null
    },
    checkMouseDown (event) {
      if (!this.dragHover) return
      this.dragging = true
      console.log('checkMouseDown')
    },
    clearDragging () {
      if (!this.dragging) return
      this.dragging = false
      this.dragHover = false
    },
    storageSetItem (attr, value) {
      let popInfo = localStorage.getItem('helperInfo')
      let data = popInfo ? JSON.parse(popInfo) : {}
      data[attr] = JSON.stringify(value)
      let strInfo = JSON.stringify(data)
      window.localStorage.setItem('helperInfo', strInfo)
    },
    storageGetItem (attr) {
      let popInfo = window.localStorage.getItem('helperInfo')
      if (!popInfo) return null
      let data = JSON.parse(popInfo)
      let value = data[attr] ? JSON.parse(data[attr]) : null
      return value
    }
  }
}
</script>

<style lang="stylus" scoped>
.layout
  width 100%
  max-width 100%
  height 100%
  max-height 100%
  overflow auto
  background-color #f2f2f2
  display flex
  &.layout-helper-left, &.layout-helper-right // layout-helper- + position
    flex-direction row
  &.layout-helper-bottom
    flex-direction column
  .content
    flex auto
    display flex
    flex-direction column
    &.content-helper-left
      order 2
    .layout-header
      background white
      height 3rem
      min-height 3rem
      box-shadow 0 0 8px 0 rgba(0, 0, 0, 0.16)
    .layout-content
      flex auto
      background-color #f2f2f2
      // z-index 1000
      position relative
      display flex
      justify-content center
      &.full-screen
        margin 0
      .layout-content-view
        flex auto
        padding-bottom 30px
        background-color #f2f2f2

    .layout-footer
      height 2rem
      z-index 1
      background-color #f2f2f2
      display flex
      flex-direction column
      justify-content center
      .footer
        text-align center
        font-size 10px
        font-weight 300
        color #A1A1A1
        white-space nowrap
        user-select none
  .helper
    z-index 2000
    position relative
    overflow auto
    display none
    &.helper-left
      order 1
      border-right 1px solid #3361D8
    &.helper-right
      border-left 1px solid #3361D8
    &.helper-bottom
      border-top 1px solid #3361D8
    .helper-component
      position absolute
      top 0
      left 0
      bottom 0
      right 0
    &.dragging-left
      box-shadow 1px 0 3px 0 #3361D8
    &.dragging-right
      box-shadow -1px 0 3px 0 #3361D8
    &.dragging-bottom
      box-shadow 0 1px 3px 0 #3361D8
  .dragging-left, .dragging-right
    cursor e-resize
    user-select none
    transition all .2s ease-in
  .dragging-bottom
    cursor s-resize
    user-select none
</style>
