import Request from '@/libs/request'

export function getTagList (params) {
  return Request.post('/mvp-manager/tag/list', params).then(resp => {
    const body = resp.data
    const data = body.data.result
    data.forEach(item => {
      item.expand = false
      item.loading = false
      item.label = item.name
      item.value = item.id
      item.children = []
    })
    body.data = data
    return body
  })
}

export function saveTag (params) {
  const url = params.id ? '/mvp-manager/tag/update' : '/mvp-manager/tag/save'
  return Request.put(url, params).then(({ data }) => {
    if (data.response.success) {
      data.data.expand = false
      data.data.loading = false
      data.data.children = []
    }
    return data
  })
}

export function deleteTag (params) {
  return Request.post('/mvp-manager/tag/delete', params).then(resp => resp.data)
}
