let breadcrumbs = window.localStorage.getItem('lawBreadcrumbs')
breadcrumbs = breadcrumbs ? JSON.parse(breadcrumbs) : []

// initial state
const state = {
  observers: {},
  breadcrumbs: breadcrumbs,
  currentLaw: breadcrumbs[1] || {},
  currentChapter: breadcrumbs.length > 2 ? breadcrumbs[breadcrumbs.length - 1] : {}
}

// mutations
const mutations = {
  navPush (state, payload) {
    state.breadcrumbs.push({
      title: payload.title,
      path: payload.path
    })

    window.localStorage.setItem('lawBreadcrumbs', JSON.stringify(state.breadcrumbs))
  },
  navReplace (state, payload) {
    state.breadcrumbs = payload
    window.localStorage.setItem('lawBreadcrumbs', JSON.stringify(state.breadcrumbs))
  },
  navPop (state) {
    state.breadcrumbs.pop()
  },
  setLaw (state, law) {
    state.currentLaw = law
  },
  setChapter (state, chapter) {
    state.currentChapter = chapter
  },
  subscribe (state, obs) {
    state.observers[obs.name] = obs.handler
  },
  unSubscribe (state, name) {
    delete state.observers[name]
  },
  publish (state, name) {
    if (state.observers[name] && typeof state.observers[name] === 'function') {
      state.observers[name].call()
    }
  }
}
// getters
const getters = {

}
export default {
  namespaced: true,
  state,
  mutations,
  getters
}
