import Request from '@/libs/request'

/**
 * 共用包
 */
export default {
  // 获取子项目
  async getChildProject (id) {
    try {
      const { data } = await Request.post('/mvp-manager/project/list', {
        id,
        type: 1
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  // 已有共用包
  async getCommon () {
    try {
      const { data } = await Request.post('/mvp-manager/project/list', {
        // id: 0,
        type: 2
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  // 设置共用包
  async setProject (config) {
    const {
      action,
      nodeId,
      projectCode,
      sharePackageName
    } = config
    try {
      const { data } = await Request.post('/mvp-manager/node/move-node', {
        action: parseInt(action),
        nodeId,
        projectCode,
        sharePackageName
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  }
}
