import Request from '@/libs/request'

/*
 * 查询微信登录状态
 */
export function wxLogin (params) {
  return Request.post('/mvp-manager/login', { ...params, loginType: 3 }).then(resp => resp.data)
}

export function bindWx (params) {
  return Request.post('/mvp-manager/bind', params).then(resp => resp.data)
}
