<script>
import ComponentMap from './list'
import LoadingComponent from './loading'
import ErrorComponent from './error'

export default {
  name: 'schemaComponent',
  props: {
    name: {
      type: String,
      required: true
    },
    schemaType: {
      type: String
    },
    apiType: {
      type: String
    },
    propsObject: {
      type: Object,
      required: true
    },
    schemaKey: {
      type: String,
      require: true
    }
  },
  data () {
    return {
      AsyncComponent: null
    }
  },
  components: {
    LoadingComponent,
    ErrorComponent
    // Additional
  },
  created () {
    console.log(this.name, '~~~~~~~')
    this.AsyncComponent = () => ({
      // 需要加载的组件 (应该是一个 `Promise` 对象)
      component: ComponentMap[this.name].component,
      // 异步组件加载时使用的组件
      loading: LoadingComponent,
      // 加载失败时使用的组件
      error: ErrorComponent,
      // 展示加载时组件的延时时间。默认值是 200 (毫秒)
      delay: 500,
      // 如果提供了超时时间且组件加载也超时了，
      // 则使用加载失败时使用的组件。默认值是：`Infinity`
      timeout: 10000
    })
  },
  render (createElement) {
    return createElement(
      'component',
      {
        is: this.AsyncComponent,
        props: {
          ...this.propsObject,
          schemaKey: this.schemaKey,
          schemaType: this.schemaType,
          apiType: this.apiType
        },
        on: {
          input: this.Input,
          commit: this.Commit,
          additional: this.Additional,
          validator: this.Validator
        }
      }
    )
  },
  methods: {
    Input (val) {
      this.$emit('input', val)
    },
    Commit (val) {
      this.$emit('commit', val)
    },
    Additional (val) {
      this.$emit('additional', val)
    },
    Validator (validator) {
      this.$emit('validator', validator)
    }
  }
}
</script>
