import Request from '@/libs/request'

export default {
  /**
   * 做题-获取项目信息
   */
  async getQuestionnaire  (projectId) {
    try {
      const { data } = await Request.get('/mvp-front/questionnaire/' + projectId)
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 做题-答案
   */
  async answer  (parame) {
    try {
      const { data } = await Request.post('/mvp-front/questionnaire', parame)
      if (data.response.code === 0) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg || '项目出错'
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 做题-答案【大表单】
   */
  async answerBig  (parame) {
    try {
      const { data } = await Request.post('/mvp-front/bigForm', parame)
      if (data.response.code === 0) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg || '项目出错'
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 获取报告
   */
  async getReport (projectId) {
    try {
      const { data } = await Request.post('/mvp-front/questionnaire/report', projectId)
      if (data.response.code === 0 || data.response.code === 202) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 获取大表单的报告
   */
  async getBigReport (projectId) {
    try {
      const { data } = await Request.post('/mvp-front/bigForm/report', projectId)
      if (data.response.code === 0 || data.response.code === 202) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  }
}
