/**
 * 指令的值必须传，为字符串或对象
 * 字符串：表示selector，为指令元素内部触发拖动的元素（拖动触发器）的选择器，当点击该元素并拖动时触发指令元素整体移动
 * 对象：{selector, vertical, horizontal}
 *      selector：选择器，拖动触发器的选择器
 *      vertical：拖动触发器垂直方向保留值，不设置默认为拖动触发器的高度，确保拖动触发器不会被移出可见区域
 *      horizontal：拖动触发器水平方向保留值
 */

function bind (el, binding, vNode) {
  let selector, vertical, horizontal
  if (typeof binding.value === 'object') {
    selector = binding.value.selector
    vertical = binding.value.vertical
    horizontal = binding.value.horizontal
  } else {
    selector = binding.value
  }
  if (!selector) return
  let node = el.querySelector(selector)
  if (!node) return
  let recordLeft, recordTop
  el._dragMouseDown = function (event) {
    event.stopPropagation()
    event.preventDefault()
    el._recordOpacity = getComputedStyle(el).getPropertyValue('opacity')
    el.style.opacity = 0.8
    recordLeft = event.clientX - el.offsetLeft
    recordTop = event.clientY - el.offsetTop
    document.removeEventListener('mousemove', el._dragMouseMove)
    document.addEventListener('mousemove', el._dragMouseMove)
    document.removeEventListener('mouseup', el._dragMouseUp)
    document.addEventListener('mouseup', el._dragMouseUp)
  }
  el._checkPosition = function () { // 防止被拖出边界
    let nodeRect = node.getBoundingClientRect()
    let elRect = el.getBoundingClientRect()
    let detaVertical = vertical || nodeRect.height
    let detaHorizontal = horizontal || nodeRect.width
    let width = document.body.clientWidth
    let height = document.body.clientHeight
    let left = elRect.left
    let right = (width - elRect.right)
    let top = elRect.top
    let bottom = (height - elRect.bottom)
    let minRight = -1 * (elRect.right - nodeRect.left - detaHorizontal)
    let minLeft = -1 * (nodeRect.right - elRect.left - detaHorizontal)
    let minBottom = -1 * (elRect.bottom - nodeRect.top - detaVertical)
    let minTop = -1 * (nodeRect.bottom - elRect.top - detaVertical)
    if (left < minLeft) {
      el.style.left = minLeft + 'px'
    } else if (right < minRight) {
      el.style.left = width - detaHorizontal + 'px'
    }
    if (top < minTop) {
      el.style.top = minTop + 'px'
    } else if (bottom < minBottom) {
      el.style.top = height - detaVertical + 'px'
    }
  }
  el._dragMouseMove = function (event) {
    let left = event.clientX - recordLeft
    let top = event.clientY - recordTop
    el.style.left = left + 'px'
    el.style.top = top + 'px'
    el.style.right = 'auto'
    el.style.bottom = 'auto'
    el._checkPosition()
    recordLeft = event.clientX - el.offsetLeft
    recordTop = event.clientY - el.offsetTop
  }
  el._dragMouseUp = function (event) {
    el._checkPosition()
    el.style.opacity = el._recordOpacity
    delete el._recordOpacity
    document.removeEventListener('mousemove', el._dragMouseMove)
    document.removeEventListener('mouseup', el._dragMouseUp)
  }
  node.addEventListener('mousedown', el._dragMouseDown)
  window.addEventListener('resize', el._checkPosition)
}

function unbind (el, binding, vNode) {
  if (!binding.value) return
  let selector = binding.value.selector || binding.value
  let node = el.querySelector(selector)
  if (node) {
    node.removeEventListener('mousedown', el._dragMouseDown)
  }
  document.removeEventListener('mousemove', el._dragMouseMove)
  window.removeEventListener('resize', el._checkPosition)
  document.removeEventListener('mouseup', el._dragMouseUp)
  delete el._dragMouseDown
  delete el._dragMouseMove
  delete el._dragMouseUp
  delete el._checkPosition
}

export default {
  bind,
  unbind
}
