
import Request from '@/libs/request'

export default {
  async getComponentList (params) {
    try {
      const { data } = await Request.post('/mvp-manager/component/list', params)
      if (data.response.success) {
        return data.data.result
      }
    } catch (err) {
      console.log(err)
    }
    return null
  },
  async registerComponent (params) {
    try {
      const { data } = await Request.put('/mvp-manager/component/save', params)
      if (data.response.success) {
        return true
      }
    } catch (err) {
      console.log(err)
    }
    return null
  },
  async updateComponent (params) {
    try {
      const { data } = await Request.put('/mvp-manager/component/updateComponent', params)
      if (data.response.success) {
        return true
      }
    } catch (err) {
      console.log(err)
    }
    return null
  },
  async deleteComponent (id) {
    try {
      const { data } = await Request.delete('/mvp-manager/component/delete' + '/' + id, id)
      if (data.response.success) {
        return true
      }
    } catch (err) {
      console.log(err)
    }
    return null
  }
}
