import NodeModel from './model'
import { getCommonNodes } from '@/api/node'

export class NodeManager {
  constructor (nodeList = [], projectId) {
    this.nodeMap = new Map()
    this.tree = []
    this.treeStack = []
    this.baseSort = 1024
    this.projectId = projectId
    this.init(nodeList)
  }

  init (nodeList) {
    const tmp = this.convertNodeList(nodeList)
    this.nodeMap = tmp.nodeMap
    this.tree = tmp.tree
    this.treeStack.push({ children: tmp.tree })
  }

  convertNodeList (nodeList = []) {
    if (!nodeList.length) {
      return {
        nodeMap: new Map(),
        tree: []
      }
    }

    const level1 = []
    const childNodes = []
    const nodeMap = new Map()

    nodeList.forEach((item, index) => {
      item.tagInfo = item.tagInfo || []
      item.lock = false
      nodeMap.set(item.uuid, item)
      item.parentId && childNodes.push(item)
      !item.parentId && level1.push(item)
      if (item.nestProjectId && item.expanded) {
        item.commonNodes = []
        this.loadCommonNodes(item)
      }
    })

    level1.sort((a, b) => a.sort - b.sort)

    nodeMap.forEach((item) => {
      const children = nodeList.filter(node => node.parentId === item.id)
      children.sort((a, b) => a.sort - b.sort)
      item.children = children
    })

    return {
      nodeMap: nodeMap,
      tree: level1
    }
  }

  getParentNode (node) {
    const parentNode = this.nodeMap.get(node.parentUuid)
    return parentNode || new NodeModel({ children: this.tree })
  }

  getPrevNode (node) {
    const parentNode = this.getParentNode(node)
    const index = parentNode.children.findIndex(item => item.id === node.id)
    return parentNode.children[index - 1]
  }

  getNextNode (node) {
    const parentNode = this.getParentNode(node)
    const index = parentNode.children.findIndex(item => item.id === node.id)
    return parentNode.children[index + 1]
  }

  removeNode (node) {
    const parentNode = this.getParentNode(node)
    if (node.parentId && node.parentUuid && node.parentId !== parentNode.id) {
      console.error(new Error(`error in data，parentId is ${node.parentId} but parentUuid is ${node.parentUuid}`))
    }
    for (const i in parentNode.children) {
      if (parentNode.children[i].id === node.id) {
        return parentNode.children.splice(i, 1)
      }
    }
  }

  insertNode (node, targetNode, position) {
    switch (position) {
      case 'next': {
        const targetParent = this.getParentNode(targetNode)
        for (const i in targetParent.children) {
          if (targetParent.children[i].id === targetNode.id) {
            targetParent.children.splice(+i + 1, 0, node)
            return
          }
        }
        break
      }
      case 'child': {
        // targetNode.children.splice(0, 0, node)
        targetNode.children.push(node)
        break
      }
      case 'prev': {
        const targetParent = this.getParentNode(targetNode)
        for (const i in targetParent.children) {
          if (targetParent.children[i].id === targetNode.id) {
            targetParent.children.splice(+i, 0, node)
            return
          }
        }
        break
      }
      default:
        break
    }
    this.nodeMap.set(node.uuid, node)
  }

  updateSort (node, prevNode, nextNode) {
    node.sort = this.baseSort // default
    if (prevNode && (prevNode.parentId === node.parentId)) {
      node.sort = +prevNode.sort + this.baseSort
    }

    if ((!prevNode || (prevNode.parentId !== node.parentId)) && nextNode && (nextNode.parentId === node.parentId)) { // no prev
      node.sort = +nextNode.sort / 2
    }

    if (prevNode && (prevNode.parentId === node.parentId) && nextNode && (nextNode.parentId === node.parentId)) { // both
      node.sort = (+prevNode.sort + +nextNode.sort) / 2
    }
  }

  initSort (nodes) {
    let tmpSort = this.baseSort
    for (const node of nodes) {
      node.sort = tmpSort
      tmpSort += this.baseSort
    }
  }

  async enterNode (node) {
    const tmpStack = this.treeStack.slice(1)
    if (tmpStack.length && node.id === tmpStack[tmpStack.length - 1].id) {
      // 如果进入的节点在栈顶，则不做任何操作
      return
    }

    if (node.nestProjectId) {
      let resp
      try {
        // 获取公用包节点
        resp = await getCommonNodes({
          nestProjectId: node.nestProjectId,
          nodeCode: node.code,
          nodeId: node.id,
          projectId: this.projectId
        })
      } catch (error) {
        console.error(error)
      }
      if (resp.data.response.success) {
        const { tree } = this.convertNodeList(resp.data.data)
        node.commonNodes = tree
      }
    }

    let index = -1
    for (const i in tmpStack) { // 检查是否点击面包屑
      if (this.treeStack[i].id === node.id) {
        index = +i // 记录点击位置
      }
    }
    if (index !== -1) {
      this.treeStack.splice(index + 1) // 点击面包馅之后出栈
    } else {
      node.expanded = true
      if (node.type === 2) {
        const parentNode = this.getParentNode(node)
        if (tmpStack.length === 0 || tmpStack[tmpStack.length - 1].id !== parentNode.id) {
          this.treeStack.push(parentNode)
        }
      }
      this.treeStack.push(this.nodeMap.get(node.uuid))
    }

    console.log(this.treeStack)
  }

  enterRoot () {
    this.treeStack.splice(1)
  }

  async loadCommonNodes (node) {
    if (node.nestProjectId && !node.crossState) {
      let resp
      try {
        // 获取公用包节点
        resp = await getCommonNodes({
          nestProjectId: node.nestProjectId,
          nodeCode: node.code,
          nodeId: node.id,
          projectId: this.projectId,
          commonCodeValue: node.commonCodeValue
        })
      } catch (error) {
        console.error(error)
      }
      if (resp.data.response.success) {
        for (const item of resp.data.data) {
          this.nodeMap.set(item.uuid, item)
        }
        const { tree } = this.convertNodeList(resp.data.data)
        node.commonNodes = tree
      }
    }
  }

  toggleExpandNode (nodes, expand) {
    const updated = []
    for (let node of nodes) {
      node.expanded = expand
      updated.push(node)
      if (!expand && node.children && node.children.length) {
        updated.push(...this.toggleExpandNode(node.children, expand))
      }
      if (!expand && node.commonNodes && node.commonNodes.length) {
        updated.push(...this.toggleExpandNode(node.commonNodes, expand))
      }
    }
    return updated
  }
}
