import Request from '@/libs/request'

export function getUserType () {
  return Request.post('/mvp-manager/user/userTypes').then(resp => {
    return resp.data
  })
}

export function addUsers (params) {
  return Request.put('/mvp-manager/user/add', params).then(resp => {
    return resp.data
  })
}
