import Request from '@/libs/request'
export default {
  async validate (schemaType, apiType) {
    if (schemaType === 'big') {
      if (apiType === 'public') {
        const { data } = Request.post('/mvp-front/bigForm/validate')
        return data
      } else {
        const { data } = Request.post('/mvp-manager/bigForm/validate')
        return data
      }
    } else {
      if (apiType === 'public') {
        const { data } = Request.post('/mvp-front/questionnaire/validate')
        return data
      } else {
        const { data } = Request.post('/mvp-manager/questionnaire/validate')
        return data
      }
    }
  }
}
