export function getNodeConditionInfo (params) {
  return Request.post('/mvp-manager/node/getNodeConditionInfo', { nodeId: params.id }).then(resp => resp.data.data)
}

export function getSearchList (params) {
  return Request.post('/mvp-manager/node/searchNodes', params).then(resp => resp.data.data)
}

export function toggleNodeCollapse (params) {
  return Request.post('/mvp-manager/node/setNodeExpandStatus', params).then(resp => resp.data.data)
}

export function saveNodes (params, handleError) {
  return Request.post('/mvp-manager/node/saveNodes', params)
    .then((resp) => {
      if (!resp.data.response.success) {
        throw new Error('保存失败！')
      } else {
        return resp
      }
    })
}

export function deleteNodes (params) {
  return Request.post('/mvp-manager/node/deleteNodes', params)
    .then((resp) => {
      if (!resp.data.response.success) {
        throw new Error('删除失败！')
      } else {
        return resp
      }
    })
}

/**
 * 获取节点数据
 * @param { params: any } 请求参数
 * @param { handleError: Function } 自定义错误处理
 *
 */
export function getNodeData (params, handleError) {
  return Request.post('/mvp-manager/node/list', params)
    .then((resp) => {
      if (!resp.data.response.success) {
        // 后端返回状态为失败，视情况编写处理逻辑，也可抛出异常，在catch中处理
      }
      if (params.expandAll) {
        resp.data.data.forEach(item => {
          item.expanded = true
        })
      }
      return resp
    })
    .catch(handleError || defaultHandleError)
}

export function getCommonNodes (params, handleError) {
  return Request.post('/mvp-manager/node/commonProjectNodes', params)
    .then((resp) => {
      if (!resp.data.response.success) {
        // 后端返回状态为失败，视情况编写处理逻辑，也可抛出异常，在catch中处理
      }
      return resp
    })
    .catch(handleError || defaultHandleError)
}

/**
 * 获取节点详细信息，包括组件相关配置
 * @param { params: any } 请求参数
 * @param { handleError: Function } 自定义错误处理
 *
 */
export function getNodeConfigData (id, handleError) {
  return Request.post('/mvp-manager/node/nodeDetail', { nodeId: id }).then((resp) => {
    if (!resp.data.response.success) {
      // 后端返回状态为失败，视情况编写处理逻辑，也可抛出异常，在catch中处理
    }
    return resp
  }).catch(handleError || defaultHandleError)
}

function defaultHandleError (err) {
  console.error(err)
  throw err
}

export function moveNodeToProject (params) {
  return Request.post('/mvp-manager/node/moveNode', params)
    .then(res => {
      if (res.data.response.success) {
        return res.data || false
      }
    })
    .catch(defaultHandleError)
}

export function dragNode (params) {
  return Request.post('/mvp-manager/node/dragNode', params).then(resp => resp.data)
}

export function saveNodeCode (params) {
  return Request.post('/mvp-manager/node/saveCondition', params).then(resp => resp.data)
}

export function saveComponentInfo (params) {
  return Request.post('/mvp-manager/node/saveComponentInfo', params).then(resp => resp.data)
}
