import Request from '../libs/request'
import Axios from 'axios'

export class LoginParamsModel {
  constructor (loginAccount, loginPassword, loginType /* 1为手机号登录，2为账号密码登录，3为微信登录 */) {
    this.loginAccount = loginAccount
    if (loginType === 2) {
      this.loginPassword = loginPassword
    } else if (loginType === 1) {
      this.smsVcode = loginPassword
    }
    this.loginType = loginType
  }
}

export function requestLogin (params) {
  return Request.post('/mvp-manager/login', params).then(resp => {
    return resp.data
  })
}

export function requestLogout () {
  return Request.post('/mvp-manager/logout').then(resp => resp.data)
}

export function getSettingInfo (params) {
  return Request.post('/mvp-manager/setting/info').then(resp => {
    if (!resp.data.response.success) {
      throw new Error(resp.data.response.msg)
    }
    return resp.data
  })
}

export function checkLogin (params) {
  return Request.post('/mvp-manager/checkLogin', params).then(resp => resp.data)
}

export function getQRCode () {
  return Axios.get('http://newsystem.laway.cn/weixinLogin/img?appid=wxad042b7ea343d689&redirect_uri=' + encodeURI('https://ceshi.lvpin100.com/login/weixin'), { timeout: 6e4 }).then(resp => resp.data)
}

export function getWxCode (params) {
  return Axios.get('http://newsystem.laway.cn/weixinLogin/check?uuid=' + params.uuid + (params.last ? '&last=' + params.last : ''), { timeout: 6e4 }).then(resp => resp.data)
}
