<template>
  <layout class="node-editor" :class="{'no-edit': !editable}">
    <Spin fix v-if="processingDrag">
      <Icon type="md-refresh" size=18 class="spin-icon-load"></Icon>
      <div>正在拖动节点...</div>
    </Spin>
    <template v-if="nodeList.nestProjectId" v-for="(node, index) in nodeList.commonNodes">
      <NodeItem :key="index" :node="node" :canEdit="false" :focusNode="focusNode"/>
    </template>
    <template v-if="!nodeList.nestProjectId" v-for="(node, index) in nodeList.children">
      <NodeItem :key="index" :node="node" :focusNode="focusNode" :canEdit="!node.lock" :canDrop="!node.lock"/>
    </template>
    <div class="plus-button" v-if="((!nodeList.children || !nodeList.children.length) && (!nodeList.commonNodes || !nodeList.commonNodes.length)) && editable" @click="addFirstNode">
      <Icon type="md-add" style="font-weight: bold"/>
    </div>
    <Modal
           v-if="editable"
           v-model="editorModal"
           class-name="config-modal node-editor-config-modal"
           :mask-closable="false"
           :closable="true"
           @on-cancel="editCancel"
           width="90%"
           :class="{'no-body-modal': !currentEditNode.title}">
      <div slot="close" @click.stop="editCancel">
        <Icon size="24" color="#999" type="ios-close"/>
      </div>
      <div slot="header" class="model-editor-top">
        <div class="model-editor-title-left">{{ currentEditNode.code }}<span v-show="currentEditNode.customVar">({{ currentEditNode.customVar }})</span></div>
        <!-- <Input id="nodeTitle" v-model="nodeComplexTitle" class="model-editor-title" placeholder="请输入问题"></Input> -->
        <Input id="nodeTitle" v-model="currentEditNode.title" class="model-editor-title" placeholder="请输入问题"></Input>
      </div>
      <Row :gutter="16">
        <Col span="24">
        <configBoard
          ref="configBoard"
          v-if="editorModal"
          :node="currentEditNode"
          :componentList="componentList"
          :nodeManager="nodeManager"
          :projectId="projectId"
          @saveToGetCode="saveToGetCode"
          @saveCode="saveCode"
          @saveComponentInfo="saveComponentInfo"
          @on-cancel="editCancel"
          @addOption="addOption"
          @updateCustomComponentList="updateCustomComponentList"
          @deleteOption="deleteOption"/>
        </Col>
      </Row>
      <div class="config-modal-footer" slot="footer">
        <Row type="flex" justify="end" v-show="!currentEditNode.title || showBottomButton">
          <Button v-show="!currentEditNode.title" @click="editCancel">
            关闭
          </Button>
          <Button v-show="currentEditNode.title" type="primary" @click="toConfig">
            保存
          </Button>
          <Button v-show="currentEditNode.title" type="primary" @click="editOk(true, true)">
            保存并关闭
          </Button>
        </Row>
      </div>
      <spin size="large" fix v-if="editorModal && savingNodes" />
    </Modal>
    <Modal
      v-if="editable"
      title="移动到项目或者共用包"
      v-model="linkProject.open"
      @on-cancel="clearLinkProjecInfo"
      :mask-closable="false"
      width="700"
      :loading="true"
      center>
      <div class="link-project" v-if="linkProject.open">
        <div class="link-project-action">
          <div class="action-title">请选择操作类型</div>
          <div class="action-buttons">
            <Button class="action-button"
              @click.stop="setLinkType(1)"
              :type="linkProject.linkType === 1 ? 'primary' : 'default'">
              移动到已有共用包
            </Button>
            <Button class="action-button"
              @click.stop="setLinkType(2)"
              :type="linkProject.linkType === 2 ? 'primary' : 'default'">
              移动到已有项目
            </Button>
            <Button
              class="action-button"
              @click.stop="setLinkType(3)"
              :type="linkProject.linkType === 3 ? 'primary' : 'default'">
              移动新建共用包
            </Button>
            <Button
              class="action-button"
              @click.stop="setLinkType(4)"
              :type="linkProject.linkType === 4 ? 'primary' : 'default'">
              移动新建项目
            </Button>
          </div>
        </div>
        <div class="link-project-data">
          <div class="action-choose" v-if="linkProject.data.action === 1">
            <span class="action-choose-title">请选择{{ linkProject.text }}：</span>
            <chooseProject
              :style="{
                width: '15rem'
              }"
              v-model="linkProject.data.projectId"
              :rootId="linkProject.rootProject"
              :type="linkProject.data.type"
              :includeRoot="linkProject.data.type === 1">
            </chooseProject>
          </div>
          <div class="action-new" v-else>
            <div class="action-input">
              <div class="action-input-title">
                请输入{{ linkProject.text }}名称
              </div>
              <Input v-model="linkProject.data.projectName"></Input>
            </div>
            <div class="action-input">
              <span class="action-input-title">
                创建引用节点
              </span>
              <Checkbox class="checkbox" v-model="linkProject.data.reference">
              </Checkbox>
            </div>
            <div class="action-input" v-if="linkProject.data.reference">
              <div class="action-input-title">
                请输入引用节点名称
              </div>
              <Input v-model="linkProject.data.newNodeName"></Input>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer" class="ling-project-footer">
        <Button @click.stop="clearLinkProjecInfo" type="text">取消</Button>
        <Button @click.stop="moveToProject" type="primary">保存</Button>
      </div>
    </Modal>
  </layout>
</template>

<script>
import uuidv4 from 'uuid/v4'
import Vue from 'vue'
import configBoard from '@/components/node-edit-board'
import NodeItem from './nodeTemplate'
import { NodeManager } from './nodeManager'
import { getNodeData, getNodeConfigData, moveNodeToProject, saveNodes, deleteNodes, dragNode, toggleNodeCollapse, saveNodeCode, saveComponentInfo } from '@/api/node'
import NodeModel from './model'
import { validateCustomVar } from './validater'
import { EmitModel } from '@/libs/EmitModel'
import chooseProject from '@/components/choose-project'

Vue.prototype.$nodeBus = new Vue()

export default {
  name: 'NodeEditor',
  components: {
    NodeItem,
    configBoard,
    chooseProject
  },
  props: {
    projectId: {
      type: Number | String,
      required: false
    },
    teamId: {
      type: String,
      required: false
    },
    editable: {
      type: Boolean,
      required: false,
      default: true
    },
    focusNode: {
      type: Number | String,
      required: false,
      default: -1
    },
    outManager: { // 外部传入的nodemanager
      type: Object,
      required: false
    },
    expandAll: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      nodeManager: {},
      operationType: 'add',
      editorModal: false,
      savingNodes: false,
      deleteOptions: [],
      currentEditParentNode: null, // 添加子节点时的父节点
      currentEditPrevNode: null, // 添加同级节点时的前驱节点
      currentEditNode: {}, // 当前在configBoard中编辑的节点
      draggingNode: null,
      validaters: {
        customVar: [
          { validator: validateCustomVar, trigger: 'change' }
        ]
      },
      linkProject: {
        lock: false,
        open: false,
        rootProject: 0,
        linkType: 1, // 操作类型
        text: '',
        data: {
          nodeId: 0, // 操作的节点ID
          action: 1, // 1-移动到已有项目或共用包 2-新建项目或共用包
          type: 1, // 1-项目 2-共用包
          projectId: 0, // action=1时有效，项目或者共用包id
          projectName: '', // action=2时有效，共用包或者项目名称
          reference: false, // 是否在此处创建节点引用之
          newNodeName: '' // action=2 && reference=true时有效，新建节点名
        },
        recordData: {}, // 刷新页面恢复数据备份,
        saveStatus: null,
        openingModal: false
      },
      processingDrag: false,
      listenFunc: {},
      shortcutKeyInfo: {
        key: 'nodeTree',
        title: '节点树操作',
        iconList: [
          ['md-add-circle', '添加同级问题'],
          ['md-add', '添加子问题'],
          ['md-trash', '删除问题'],
          ['md-link', '移动到共用包或项目']
        ]
      },
      expand: false,
      highlightBackground: '',
      showBottomButton: false
    }
  },
  provide () {
    return {
      rootInstance: this,
      rootNodes: this.nodeManager.tree,
      $nodeBus: this.$nodeBus,
      editable: this.editable
    }
  },
  created () {
    this.getNodeData(this.projectId)
    this.clearEventListener()
    this.initEventListener()
    console.log('节点编辑器已创建', this)
  },
  mounted () {
    setInterval(() => {
      this.showBottomButton = (!this.$refs.configBoard || this.$refs.configBoard.tabs !== 'config')
    }, 200)
  },
  beforeDestroy () {
    this.clearEventListener()
  },
  computed: {
    nodeList () {
      if (this.nodeManager && this.nodeManager.treeStack) {
        return this.nodeManager.treeStack[this.nodeManager.treeStack.length - 1]
      } else {
        return {}
      }
    },
    componentList () {
      return this.$store.state.component.list
    },
    componentQuestion () {
      return this.$store.state.component.question
    },
    componentOptions () {
      return this.$store.state.component.options
    },
    nodeComplexTitle: {
      get () {
        if (this.currentEditNode.hasOwnProperty('uuid')) {
          return (this.currentEditNode.title ? this.currentEditNode.title : '') + (this.currentEditNode.showCondition ? this.currentEditNode.showCondition : '')
        } else {
          return ''
        }
      },
      set (value) {
        if (this.currentEditNode.hasOwnProperty('uuid')) {
          const title = value.substr(0, value.indexOf('<?')) || value
          const showCondition = value.indexOf('<?') !== -1 ? value.substring(value.indexOf('<?')) : ''
          console.log(title)
          console.log(showCondition)
          this.currentEditNode.title = title
          this.currentEditNode.showCondition = showCondition
        }
      }
    },
    currentCompany () {
      return this.$store.state.user.currentCompany
    }
  },
  watch: {
    projectId (newValue, oldValue) {
      if (newValue !== oldValue && newValue) {
        this.getNodeData(newValue)
      }
    },
    focusNode () {
      if (!this.expand) {
        this.toggleExpandAll()
      }
    },
    outManager: {
      handler () {
        this.getNodeData(this.projectId)
      },
      deep: true
    },
    editorModal (newValue, oldValue) {
      console.log(newValue ? '打开窗口' : '关闭窗口')
    }
  },
  methods: {
    lockNode (node) {
      this.$emit('lockNode', node)
    },
    unlockNode (node) {
      this.$emit('unlockNode', node)
    },
    async saveToGetCode (close, noComponentInfo) {
      return this.editOk(close, noComponentInfo)
    },
    saveCode (data) {
      saveNodeCode(data).then(({ data, response }) => {
        if (response.success) {
          console.log('保存代码逻辑', data)
        }
      })
    },
    saveComponentInfo (data) {
      saveComponentInfo(data).then(({ data, response }) => {
        if (response.success) {
          console.log('保存输入组件配置', data)
        }
      })
    },
    initEventListener () {
      if (!this.editable) return
      this.$nodeBus.$on('toggle-collapse', this.listenFunc['toggle-collapse'] = (params) => {
        this.saveToggleCollapse(params)
      })

      this.$nodeBus.$on('edit-node', this.listenFunc['edit-node'] = (params) => {
        this.editNode(params)
      })

      this.$nodeBus.$on('add-child-node', this.listenFunc['add-child-node'] = (params) => {
        this.addChildNode(params)
      })

      this.$nodeBus.$on('add-next-node', this.listenFunc['add-next-node'] = (params) => {
        this.addNextNode(params)
      })

      this.$nodeBus.$on('delete-node', this.listenFunc['delete-node'] = (params) => {
        this.deleteNodes(params)
      })

      this.$nodeBus.$on('drag-start', this.listenFunc['drag-start'] = (params) => {
        this.handleDragStart(params)
      })

      this.$nodeBus.$on('drag-end', this.listenFunc['drag-end'] = () => {
        this.draggingNode = null
      })

      this.$nodeBus.$on('node-drop', this.listenFunc['node-drop'] = (params) => {
        this.handleDrop(params)
      })

      this.$nodeBus.$on('enter-node', this.listenFunc['enter-node'] = (params) => {
        this.handleEnterNode(params)
      })

      this.$bus.$on('link-project', this.handleMoveToProject)
      // this.$bus.$on('show-node-tree', this.showFloatNodeTree)
    },
    clearEventListener () {
      for (let eventKey in this.listenFunc) {
        this.$nodeBus.$off(eventKey, this.listenFunc[eventKey])
      }
      this.$bus.$off('link-project', this.handleMoveToProject)
      // this.$bus.$off('show-node-tree', this.showFloatNodeTree)
    },
    showFloatNodeTree () {
      this.$store.commit('node/showNodeTree')
    },
    hideFloatNodeTree () {
      this.$store.commit('node/hideNodeTree')
    },
    editCancel (e) {
      // this.$Message.warning('您所做的修改将不会被保存，是否确定关闭？')
      // this.$Modal.confirm({
      //   title: '警告！',
      //   content: '您所做的修改将不会被保存，是否确定关闭？',
      //   onCancel: () => {},
      //   onOk: () => {
      //     this.editorModal = false
      //     this.currentEditNode = {}
      //     this.currentEditParentNode = null
      //     this.currentEditPrevNode = null
      //   }
      // })
      // if (this.saveStatus === 'saved') {
      //   this.editOk()
      //   this.saveStatus = null
      // } else {
      //   this.editorModal = false
      //   this.currentEditNode = {}
      //   this.currentEditParentNode = null
      //   this.currentEditPrevNode = null
      // }

      if (this.$refs.configBoard && this.$refs.configBoard.tabs === 'edit' && this.$refs.configBoard.tabsClose) {
        this.$refs.configBoard.tabTo('config')
        return
      }

      this.unlockNode(this.currentEditNode)
      this.editorModal = false
      this.currentEditNode = {}
      this.currentEditParentNode = null
      this.currentEditPrevNode = null
      this.hideFloatNodeTree()
    },
    async editOk (close, noComponentInfo = false) {
      console.time('保存数据处理')
      // 如果标题为空，直接关闭
      if (!this.currentEditNode.title) {
        if (close) this.editorModal = false
        return
      }
      console.log('执行保存逻辑')

      // 异步关闭模态框
      // if (close) this.editorModal = false

      if (!this.currentEditNode.id && this.currentEditNode.nestProjectId) {
        this.currentEditNode.expanded = false
      }

      // 显示加载中
      this.savingNodes = true
      // 将标题为空的选项和问题类型的节点过滤掉
      const tmpChildQuestion = []
      this.currentEditNode.children = this.currentEditNode.children.filter(item => {
        if (+item.type === 1) {
          tmpChildQuestion.push(item)
        }
        return item.title.replace(/\s/g, '') !== '' && +item.type !== 1
      })

      // 更新选项的sort
      const maxSort = tmpChildQuestion.length ? tmpChildQuestion[0].sort : 0
      if (maxSort && this.currentEditNode.children.length) {
        const gap = maxSort / this.currentEditNode.children.length
        let sort = 0
        this.currentEditNode.children.forEach(item => {
          item.sort = sort
          sort = sort + gap
        })
      } else {
        // this.currentEditNode.children.sort((a, b) => a.sort - b.sort)
        this.nodeManager.initSort(this.currentEditNode.children)
      }

      // 取出子节点
      const updateNodes = [this.currentEditNode, ...this.currentEditNode.children]

      // 指定特定的options组件ID参数
      try {
        for (const n in this.currentEditNode.children) {
          if (this.currentEditNode.componentId === this.componentQuestion['singleSelect'].id) {
            this.currentEditNode.children[n].componentId = this.componentOptions['singleSelect-option'].id
          } else if (this.currentEditNode.componentId === this.componentQuestion['multiSelect'].id) {
            this.currentEditNode.children[n].componentId = this.componentOptions['multiSelect-option'].id
          } else if (this.currentEditNode.componentId === this.componentQuestion['judge'].id) {
            this.currentEditNode.children[n].componentId = this.componentOptions['judge-option'].id
          } else if (this.currentEditNode.componentId === null || this.currentEditNode.componentId === '') {
            this.currentEditNode.children[n].componentId = this.componentOptions.input.id
          }
        }
      } catch (err) {
        this.savingNodes = false
      }

      console.log(updateNodes)

      this.saveNodeConfigData(updateNodes, noComponentInfo) // 进行处理，拼凑节点自定义配置信息进行保存

      // 如果有删除选项，请求删除选项
      if (this.deleteOptions.length) {
        updateNodes.push(...this.deleteOptions.map(item => {
          item.dr = 1
          return item
        }))
      }

      // 保存节点请求
      const tmpNodes = []
      for (const item of updateNodes) {
        const tmpItem = new NodeModel(item)
        delete tmpItem.children
        delete tmpItem.commonNodes
        delete tmpItem.options
        tmpNodes.push(tmpItem)
      }
      console.log(updateNodes)
      console.log(tmpNodes)
      console.timeEnd('保存数据处理')
      saveNodes(tmpNodes)
        .then(async (resp) => {
          this.unlockNode(this.currentEditNode)
          this.saveStatus = 'saved'

          // 如果有删除选项，请求删除选项
          // if (this.deleteOptions.length) {
          //   // deleteNodes({ delete: this.deleteOptions.map(item => item.id /* 只请求有id的选项 */), update: [] })
          //   //   .catch(err => {
          //   //     console.log(err)
          //   //     this.savingNodes = false
          //   //   })
          // }

          // // 同步关闭模态框
          // if (close) this.editorModal = false

          const updatedMap = new Map()

          // 将新增的节点放入map中
          for (const node of updateNodes) {
            updatedMap.set(node.uuid, node)
            // if (close) {
            this.nodeManager.nodeMap.set(node.uuid, node)
            // }
          }

          // 修改本地数据
          for (const node of resp.data.data) {
            // updatedMap.get(node.uuid).id = node.id
            // updatedMap.get(node.uuid).code = node.code
            // updatedMap.get(node.uuid).parentId = node.parentId
            // updatedMap.get(node.uuid).componentInfo = node.componentInfo
            const localNode = updatedMap.get(node.uuid)
            for (const key in node) {
              localNode[key] = node[key]
              localNode.showConditionDesc = ''
              localNode.translateDesc = ''
            }
          }

          // if (close) {
          // 操作类型为添加
          if (this.operationType === 'add') {
            if (this.currentEditParentNode.children) {
              if (this.currentEditPrevNode) { // 如果有同级的前驱节点
                const index = this.currentEditParentNode.children.findIndex(item => item.id === this.currentEditPrevNode.id)
                this.currentEditParentNode.children.splice(index + 1, 0, this.currentEditNode) // 将节点推入前驱节点之后
              } else {
                this.currentEditParentNode.children.push(this.currentEditNode) // 直接推入数据
              }
            } else {
              this.currentEditParentNode.children = [this.currentEditNode] // 没有children的情况
            }
            this.operationType = 'edit'
          } else if (this.operationType === 'edit') { // 操作类型为编辑
            const index = this.currentEditParentNode.children.findIndex(item => item.id === this.currentEditNode.id)
            this.currentEditNode.children.push(...tmpChildQuestion)
            this.currentEditParentNode.children.splice(index, 1, this.currentEditNode) // 替换编辑后的节点（编辑的节点为deepCopy的节点）
          }
          // }

          if (this.currentEditNode.nestProjectId) {
            this.nodeManager.loadCommonNodes(this.currentEditNode)
          }

          this.savingNodes = false
          if (close) this.editorModal = false

          this.hideFloatNodeTree()
        })
        .catch((err) => {
          console.error(err)
          this.$Message.error('保存失败，请稍后重试！')
          this.savingNodes = false
        })
      this.$forceUpdate()
    },
    toConfig () {
      this.$refs.configBoard.tabTo('config')
    },
    saveNodeConfigData (nodes, noComponentInfo = false) { // node为题目或选项的node
      /**
       * 警告
       * 禁止修改该函数！
       */
      for (const n in nodes) {
        if (noComponentInfo) {
          if (nodes[n]['newComponentData']) {
            delete nodes[n]['newComponentData']
          }

          if (nodes[n]['componentInfo']) {
            delete nodes[n]['componentInfo']
          }
        } else {
          if (nodes[n]['newComponentData']) {
            console.log('newComponentData')
            console.log(nodes[n]['newComponentData'])
            nodes[n]['componentInfo'] = nodes[n]['newComponentData']
            delete nodes[n]['newComponentData']
          }

          if (nodes[n]['componentInfo'] && nodes[n]['componentInfo']['items'] && nodes[n]['componentInfo']['items'].length) { // 避免新增数据报错
          // nodes[n]['nodeComponentConfigDatas'] = nodes[n]['componentInfo']
            for (const i in nodes[n]['componentInfo']['items']) { // 常规组件只有第0个items是用于组件配置，自定义组件可能不只第0个item
              if (nodes[n]['componentInfo']['items'][i]['configs']) { // 避免新建数据污染老数据，解决选项没有componentInfo的问题。
                for (const l in nodes[n]['componentInfo']['items'][i]['configs']) { // 遍历所有组件的自定义配置项
                  // nodes[n]['componentInfo']['items'][i]['configs'][l]['id'] = null // 2018年08月14日21:00:26 加，勿删，移除config的id
                  if (nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']) { // configData是对象，第一层configData
                  // if (nodes[n]['componentInfo']['items'][i]['configs'][l]['defaultValue'] !== nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']) { // 判断数据是否和默认值相等
                  // nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['componentItemConfigCode'] = nodes[n]['componentInfo']['items'][i]['configs'][l]['code']
                  // nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['componentItemConfigId'] = nodes[n]['componentInfo']['items'][i]['configs'][l]['id']
                  // nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['componentId'] = nodes[n]['componentInfo']['items'][i]['componentId']
                  // nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['componentItemId'] = nodes[n]['componentInfo']['items'][i]['id']
                  // nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['compItemTopId'] = nodes[n]['componentInfo']['id']
                  // 删除后端不需要的字段
                  // delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['compItemConfigId']
                    delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['createTime']
                    delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['createrId']
                    // delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['dr']
                    nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['id'] = null // 禁止删除
                    // delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['nodeId']
                    delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['updateTime']
                  // delete nodes[n]['componentInfo']['items'][i]['configs'][l]['configData']['useCode']
                  // nodes[n]['nodeComponentConfigDatas'].push(this.deepCopy(nodes[n]['componentInfo']['items'][i]['configs'][l]['configData'])) // 避免空值被传入
                  // }
                  }
                }
              }
            }

          // delete nodes[n]['componentInfo'] // 避免多余数据被传输到后端
          // delete nodes[n]['newComponentData']
          }
        }

        // if (nodes[n]['children']) {
        //   delete nodes[n]['children']
        // }
      }
    },
    editNode (params) {
      const editNode = +params.params.node.type === 1 ? params.params.node : this.nodeManager.nodeMap.get(params.params.node.parentUuid)
      this.operationType = 'edit'
      this.currentEditParentNode = this.nodeManager.getParentNode(editNode)
      const tmpEditNode = this.deepCopy(editNode)
      this.lockNode(editNode)
      this.openEditModal(tmpEditNode)
    },
    addOption (params) {
      const newOption = new NodeModel({
        uuid: uuidv4(),
        title: '',
        parentId: params.params.node ? params.params.node.id : null,
        parentUuid: params.params.node ? params.params.node.uuid : null,
        projectId: params.params.node && params.params.node.projectId ? params.params.node.projectId : this.projectId,
        componentId: params.params.componentId,
        type: 2
      })

      // 跳过类型为问题的节点
      let tmpPrevIndex = params.params.node.children.length - 1
      for (const index in params.params.node.children) {
        if (+params.params.node.children[+index].type !== 1) {
          tmpPrevIndex = +index
        }
      }
      this.nodeManager.updateSort(newOption, params.params.node.children[tmpPrevIndex], params.params.node.children[tmpPrevIndex + 1])
      params.params.node.children.push(newOption)
    },
    deleteOption (params) {
      if (params.params.node.id) {
        this.deleteOptions.push(params.params.node)
      }
    },
    addFirstNode (e) {
      const parentNode = this.nodeList || null
      const params = new EmitModel({
        name: 'add-child-node',
        event: e,
        vn: this.$vnode,
        params: {
          node: parentNode || new NodeModel({ children: this.nodeManager.tree })
        }
      })
      this.addChildNode(params)
    },
    addChildNode (params) {
      this.operationType = 'add'
      const newNode = new NodeModel({
        uuid: uuidv4(),
        title: '',
        parentId: params.params.node ? params.params.node.id : null,
        // projectId: this.projectId,
        projectId: params.params.node && params.params.node.projectId ? params.params.node.projectId : this.projectId,
        parentUuid: params.params.node ? params.params.node.uuid : null,
        componentId: params.params.componentId
        // nestProjectId: params.params.node ? params.params.node.nestProjectId : null
      }) // 实例化新的节点

      this.currentEditParentNode = params.params.node
      if (params.params.node.children && params.params.node.children.length) {
        this.nodeManager.updateSort(newNode, params.params.node.children[params.params.node.children.length - 1], null)
      }

      for (let i = 0; i < 4; i++) {
        this.addOption(new EmitModel({
          name: 'addOption',
          event: null,
          vn: this.$vnode,
          params: {
            node: newNode
          }
        }))
      }
      this.openEditModal(newNode)
    },
    addNextNode (params) {
      this.operationType = 'add'
      const newNode = new NodeModel({
        uuid: uuidv4(),
        title: '',
        parentId: params && params.params && params.params.node ? params.params.node.parentId : null,
        projectId: params.params.node && params.params.node.projectId ? params.params.node.projectId : this.projectId,
        componentId: params.params.componentId,
        parentUuid: params && params.params && params.params.node ? params.params.node.parentUuid : null
      }) // 实例化新的节点

      this.currentEditParentNode = this.nodeManager.getParentNode(params.params.node)

      this.currentEditPrevNode = params && params.params.node ? params.params.node : null

      const nextNode = this.nodeManager.getNextNode(params.params.node)

      if (params && params.params.node) {
        this.nodeManager.updateSort(newNode, params.params.node, nextNode)
      }

      for (let i = 0; i < 4; i++) {
        this.addOption(new EmitModel({
          name: 'addOption',
          event: null,
          vn: this.$vnode,
          params: {
            node: newNode
          }
        }))
      }

      this.openEditModal(newNode)
    },
    deleteNodes (params) {
      if (params.params.node.children) {
        for (const item of params.params.node.children) {
          if (+item.type === 1 || (item.children && item.children.length)) {
            this.$Modal.error({
              title: '提示：',
              content: '包含子问题的节点不允许直接删除！'
            })
            return
          }
        }
      }
      this.$Modal.confirm({
        title: '警告！',
        content: '删除节点后不可恢复，是否继续删除？',
        onOk: () => {
          deleteNodes({ delete: [params.params.node.id], update: [] }).then((resp) => {
            this.nodeManager.removeNode(params.params.node)
          })
            .catch(err => {
              console.error(err)
              this.$Message.error('删除失败，请稍后重试！')
            })
        },
        onCancel: () => { }
      })
    },
    async updateCustomComponentList () {
      const relationComponentAOList = [
        {
          sourceId: this.projectId,
          sourceType: 3
        }
      ]

      if (this.teamId) {
        relationComponentAOList.push({
          sourceId: this.teamId,
          sourceType: 2
        })
      }

      if (this.currentCompany) {
        relationComponentAOList.push({
          sourceId: this.currentCompany,
          sourceType: 1
        })
      }

      await this.$store.dispatch('component/customComponentList', relationComponentAOList)
    },
    async openEditModal (node) {
      if (this.openingModal) {
        return
      }
      this.openingModal = true
      const loadingMsg = this.$Message.loading({
        content: '加载中...'
      })
      this.currentEditNode = node

      await this.updateCustomComponentList()

      if (node && node.id) {
        const nodeConfigData = await getNodeConfigData(node.id, (r) => {
          this.$log('NodeEditor', r)
        })
        this.$log('NodeEditor', '-----节点配置信息-----', nodeConfigData.data.data)
        this.$log('NodeEditor', '-----节点信息-----', this.currentEditNode)
        // this.currentEditNode.nodeConfigData = nodeConfigData.data.data

        // 更新节点的具体配置信息
        this.updateComponentInfo(this.currentEditNode, nodeConfigData.data.data)
        this.$log('NodeEditor', '-----节点信息-----', this.currentEditNode)
      } else { // 否则就是全新的数据
        this.currentEditNode.componentInfo = { // 定义最基本的层级关系
          items: [
            {
              componentId: null
            }
          ]
        }

        for (const n in this.currentEditNode.children) {
          this.currentEditNode.children[n].componentInfo = {
            items: [
              {
                componentId: null
              }
            ]
          }
        }
      }

      this.currentEditNode.type = '' + this.currentEditNode.type
      if (this.currentEditNode.remarks === null) {
        this.currentEditNode.remarks = ''
      }
      loadingMsg()

      const tmp = 4 - this.currentEditNode.children.length
      if (tmp > 0) {
        let componentId = this.componentOptions.input.id
        if (this.currentEditNode.componentId === this.componentQuestion['singleSelect'].id) {
          componentId = this.componentOptions['singleSelect-option'].id
        } else if (this.currentEditNode.componentId === this.componentQuestion['multiSelect'].id) {
          componentId = this.componentOptions['multiSelect-option'].id
        } else if (this.currentEditNode.componentId === this.componentQuestion['judge'].id) {
          componentId = this.componentOptions['judge-option'].id
        }

        for (let nn = 0; nn < tmp; nn++) {
          this.addOption(new EmitModel({
            name: 'addOption',
            event: null,
            vn: this.$vnode,
            params: {
              node: this.currentEditNode,
              componentId
            }
          }))
        }
      }

      this.editorModal = true
      this.openingModal = false
    },
    updateComponentInfo (node, configNodes) { // 将configNodes中的节点信息写入到将currentEditNode中
      // 设置题目的配置信息
      for (const n in configNodes) {
        if (configNodes[n]['id'] === node['id']) {
          for (const i in node) {
            if (i !== 'children' && configNodes[n][i] !== undefined) {
              node[i] = configNodes[n][i]
            }
          }
          for (const x in configNodes[n]) {
            if (node[x] === undefined) {
              node[x] = configNodes[n][x]
            }
          }
          // node.componentId = configNodes[n].componentId
          // node.componentInfo = this.deepCopy(configNodes[n].componentInfo)
          break
        }
      }

      // 设置选项的配置信息
      for (const n in configNodes) {
        for (const i in node['children']) {
          if (configNodes[n]['id'] === node['children'][i]['id']) {
            for (const x in node['children'][i]) {
              if (configNodes[n][x] !== undefined) {
                node['children'][i][x] = configNodes[n][x]
              }
            }
            for (const l in configNodes[n]) {
              if (node['children'][i][l] === undefined) {
                node['children'][i][l] = configNodes[n][l]
              }
            }
            // node['children'][i].componentId = configNodes[n].componentId
            // node['children'][i].point = configNodes[n].point
            // node['children'][i].componentInfo = this.deepCopy(configNodes[n].componentInfo)
          }
        }
      }
    },
    handleMoveToProject (data) {
      this.linkProject.open = true
      this.setLinkType(this.linkProject.linkType)
      this.linkProject.data.newNodeName = data.params.node.title
      this.linkProject.data.nodeId = data.params.node.id
      this.linkProject.recordData.projectId = data.params.node.projectId
      this.$bus.$emit('get-root-project', (rootProject) => {
        this.linkProject.rootProject = rootProject
      })
    },
    setLinkType (type) {
      this.linkProject.linkType = type
      this.linkProject.data.action = type <= 2 ? 1 : 2
      let bProject = (type === 2 || type === 4)
      this.linkProject.data.type = bProject ? 1 : 2
      this.linkProject.text = bProject ? '项目' : '共用包'
    },
    clearLinkProjecInfo () {
      this.linkProject.open = false
      this.linkProject.linkType = 1
      this.linkProject.text = ''
      this.linkProject.data.nodeId = 0
      this.linkProject.data.action = 1
      this.linkProject.data.type = 1
      this.linkProject.data.projectId = 0
      this.linkProject.data.projectName = ''
      this.linkProject.data.reference = false
      this.linkProject.data.newNodeName = ''
    },
    async refreshNodePageDone () {
      // watchValue观测不到的情况
      if (this.projectId === this.linkProject.recordData.projectId) {
        this.getNodeData(this.projectId)
      }
    },
    checkLinkProjectData () {
      if (this.linkProject.data.action === 1) {
        if (!this.linkProject.data.projectId) {
          this.$Message.error('请选择' + this.linkProject.text)
          return false
        }
        if (this.linkProject.data.projectId === this.linkProject.recordData.projectId) {
          this.$Message.error('不能移动到当前项目')
          return false
        }
      } else {
        if (!this.linkProject.data.projectName) {
          this.$Message.error('请填写' + this.linkProject.text + '名称')
          return false
        }
      }
      return true
    },
    async moveToProject () {
      if (!this.checkLinkProjectData()) return
      if (this.linkProject.lock) {
        this.$Message.info('已发送请求，请耐心等待')
        return
      }
      this.linkProject.lock = true
      let data = await moveNodeToProject(this.linkProject.data)
      this.linkProject.lock = false
      if (data) { // 刷新项目树，节点树，恢复现场
        this.$bus.$emit('refresh-project-tree', this.linkProject.recordData, this.refreshNodePageDone)
      } else {
        this.$Message.error('操作失败')
      }
    },
    refreshBackLinkProject () {
      if (!this.linkProject.open) return
      this.clearLinkProjecInfo()
    },
    handleDragStart (params) {
      this.draggingNode = params.params.node
    },
    async handleDrop (params) {
      this.processingDrag = true
      const targetNode = params.params.target
      const dragType = params.params.type
      const draggingNodeCopy = this.deepCopy(this.draggingNode) // 克隆一个拖动的节点用于保存请求
      // switch (dragType) {
      //   case 'top': {
      //     const firstNode = targetNode.children[0]
      //     draggingNodeCopy.parentId = null
      //     draggingNodeCopy.parentUuid = null
      //     this.nodeManager.updateSort(draggingNodeCopy, null, firstNode)

      //     try {
      //       const result = await saveNodes([draggingNodeCopy])
      //       console.log(result)
      //       this.nodeManager.removeNode(this.draggingNode)
      //       this.nodeManager.insertNode(draggingNodeCopy, targetNode, dragType)
      //     } catch (err) {
      //       this.$Message.error('拖动失败！')
      //     }
      //     break
      //   }
      //   case 'next': {
      //     this.$log('拖动到下一个')
      //     const nextNode = this.nodeManager.getNextNode(targetNode)
      //     draggingNodeCopy.parentId = targetNode.parentId
      //     draggingNodeCopy.parentUuid = targetNode.parentUuid
      //     this.nodeManager.updateSort(draggingNodeCopy, targetNode, nextNode)

      //     try {
      //       const result = await saveNodes([draggingNodeCopy])
      //       console.log(result)
      //       this.nodeManager.removeNode(this.draggingNode)
      //       this.nodeManager.insertNode(draggingNodeCopy, targetNode, dragType)
      //     } catch (err) {
      //       this.$Message.error('拖动失败！')
      //     }
      //     break
      //   }
      //   case 'child': {
      //     draggingNodeCopy.parentId = targetNode.id
      //     draggingNodeCopy.parentUuid = targetNode.uuid
      //     this.nodeManager.updateSort(draggingNodeCopy, targetNode.children[targetNode.children.length - 1], null)

      //     try {
      //       const result = await saveNodes([draggingNodeCopy])
      //       console.log(result)
      //       this.nodeManager.removeNode(this.draggingNode)
      //       this.nodeManager.insertNode(draggingNodeCopy, targetNode, dragType)
      //     } catch (err) {
      //       this.$Message.error('拖动失败！')
      //     }
      //     break
      //   }
      //   default:
      //     break
      // }
      let tmpType = 0
      switch (dragType) {
        case 'prev': {
          tmpType = 3
          this.$log('拖动到上一个')
          break
        }
        case 'next': {
          tmpType = 1
          this.$log('拖动到下一个')
          break
        }
        case 'child': {
          tmpType = 2
          this.$log('拖动到子节点')
          break
        }
        default:
          return
      }
      let resp
      try {
        resp = await dragNode({
          operateNodeId: draggingNodeCopy.id,
          position: tmpType,
          targetNodeId: targetNode.id
        })
      } catch (err) {
        console.error(err)
        this.$Message.error('操作失败！')
        this.processingDrag = false
      }
      if (resp.response.success) {
        draggingNodeCopy.sort = resp.data.sort
        draggingNodeCopy.parentId = resp.data.parentId
        draggingNodeCopy.parentUuid = resp.data.parentUuid
        this.nodeManager.removeNode(this.draggingNode)
        this.nodeManager.insertNode(draggingNodeCopy, targetNode, dragType)
      } else {
        this.$Message.error('操作失败！')
      }
      this.$forceUpdate()
      this.processingDrag = false
    },
    handleEnterNode (params) {
      this.nodeManager.enterNode(params.params.node)
    },
    toggleExpandAll () {
      this.expand = !this.expand
      const ids = []
      if (this.nodeManager.nodeMap && this.nodeManager.nodeMap.length) {
        this.nodeManager.nodeMap.forEach(item => {
          item.expanded = this.expand
          ids.push(item.id)
        })
      }
      this.saveToggleCollapse(new EmitModel({
        name: 'addOption',
        event: null,
        vn: this.$vnode,
        params: {
          nodeIds: ids,
          status: this.expand ? 1 : 0
        }
      }))
    },
    saveToggleCollapse (params) {
      toggleNodeCollapse({
        ids: params.params.nodeIds,
        expandStatus: params.params.status
      }).then(({ data, response }) => {
        console.log(response)
      }).catch(err => {
        console.error(err)
      })
    },
    // ************网络请求****************
    getNodeData (projectId) {
      if (this.outManager) {
        this.nodeManager = this.outManager
        return
      }
      if (!projectId) {
        return
      }
      getNodeData({
        id: null,
        pageNo: null,
        pageSize: null,
        projectId: projectId,
        expandAll: this.expandAll
      })
        .then((resp) => {
          if (resp.data.response.success) {
            this.nodeManager = new NodeManager(resp.data.data || [], projectId)
            this.$nextTick(() => {
              this.$emit('inited', new EmitModel({
                name: 'inited',
                event: null,
                vn: this.$vnode,
                params: {
                  nodeManager: this.nodeManager
                }
              }))
            })
            this.refreshBackLinkProject() // 共用包设置检查
          } else {
            this.$Message.error(`请求错误: ${resp.data.response.msg}`)
          }
        })
        .catch((err) => {
          console.log(err)
          this.$Message.error('网络异常，请检查网络连接！')
        })
    }
    // ************网络请求end*************
  },
  sockets: {
    // lockNode (data) {
    //   console.log(`%c锁定节点：${data.data.payload}`, 'color: red; font-weight: bold')
    //   const node = this.nodeManager.nodeMap.get(data.data.payload)
    //   if (node) {
    //     node.lock = true
    //   }
    // },
    // unlockNode (data) {
    //   console.log(`%c解锁节点：${data.data.payload}`, 'color: red; font-weight: bold')
    //   const node = this.nodeManager.nodeMap.get(data.data.payload)
    //   if (node) {
    //     node.lock = false
    //   }
    // }
  }
}
</script>

