<script>
import ComponentMap from './list'
import LoadingComponent from './loading'
import ErrorComponent from './error'
// import { getCompontent } from '@/api/component'

export default {
  name: 'configComponent',
  props: {
    componentDefaultData: {
      type: Object,
      required: false
    },
    componentDefaultDataId: {
      type: String,
      required: false
    },
    componentData: {
      type: Object,
      required: false
    },
    componentInfoData: {
      type: Object,
      required: false
    },
    componentInfoDataInx: {
      type: Number,
      required: false
    },
    cType: {
      type: Number,
      required: true
    },
    itemId: {
      type: Number,
      required: false,
      default: 0
    },
    useItem: {
      type: Boolean,
      required: false,
      default: false
    },
    componentType: {
      type: Number,
      require: false,
      default: 1
    },
    aType: { // 常规组件，其它的多填，多填的多填
      type: Boolean,
      require: false,
      default: false
    },
    componentId: {
      type: String
    }
  },
  data () {
    return {
      AsyncComponent: null,
      componentInfo: null
    }
  },
  components: {
    LoadingComponent,
    ErrorComponent
  },
  watch: {
    componentDefaultData (val) {
      console.log('强制刷新组件')
      // this.$forceUpdate()
      this.initData()
    }
  },
  created () {
    this.initData()
  },
  render (createElement) {
    // console.log(this.componentInfo.items[0])
    return createElement(
      'component',
      {
        is: this.AsyncComponent,
        props: {
          // cType: this.aType ? 3 : (this.useItem ? ((this.componentType === 2) ? 2 : 1) : ((this.cType === 1 && this.componentInfo.items[0].type === 2) ? 2 : 1)),
          cType: this.aType ? 3 : (this.useItem ? ((this.componentType === 2) ? 2 : 1) : ((this.cType === 1 && this.componentInfo.type === 2) ? 2 : 1)),
          configs: this.useItem ? this.componentInfo.configs : ((this.componentInfo.items[0].children && this.componentInfo.items[0].children.length) ? this.componentInfo.items[0].children[this.itemId + 1].configs : this.componentInfo.items[this.itemId].configs) // 只需要configs，子组件拿到后仍然需要进行一道深拷贝
        },
        on: {
          update: this.Update,
          input: this.Input // 变更时候传，value里面，必须打断双向绑定
        }
      }
    )
  },
  methods: {
    initData () {
      if (this.useItem) { // 使用Item的情况
        if (this.componentInfoData) {
          this.componentInfo = this.deepCopy(this.componentData)
        } else {
          if (!this.componentData || (this.componentDefaultData && this.componentDefaultData.uuid !== this.componentData.uuid)) {
            // if (this.componentDefaultDataId) { // 如果没有默认数据，就传id进来，从后端取
            //   this.componentDefaultData = await getCompontent(this.componentDefaultDataId)
            // }
            console.log(this.componentDefaultData)
            this.componentInfo = this.deepCopy(this.componentDefaultData)
          } else {
            this.componentInfo = this.deepCopy(this.componentData)
          }
        }

        // this.componentInfo = 'requset' // TODO: 从后端获取

        console.log('this.componentInfo')
        console.log(this.componentInfo)

        // 处理数据类型错误的情况
        for (const i in this.componentInfo.configs) {
          if (this.componentInfo.configs[i]['code'] === 'inputType') {
            this.componentInfo.configs[i]['configData']['configData'] = parseInt(this.componentInfo.configs[i]['configData']['configData'])
            this.componentInfo.configs[i]['defaultValue'] = parseInt(this.componentInfo.configs[i]['defaultValue'])
          } else if (this.componentInfo.configs[i]['code'] === 'convert') {
            this.componentInfo.configs[i]['configData']['configData'] = JSON.parse(this.componentInfo.configs[i]['configData']['configData'])
            this.componentInfo.configs[i]['defaultValue'] = JSON.parse(this.componentInfo.configs[i]['defaultValue'])
          } else if (this.componentInfo.configs[i]['code'] === 'readonly') {
            this.componentInfo.configs[i]['configData']['configData'] = JSON.parse(this.componentInfo.configs[i]['configData']['configData'])
            this.componentInfo.configs[i]['defaultValue'] = JSON.parse(this.componentInfo.configs[i]['defaultValue'])
          }
        }

        this.AsyncComponent = () => ({
          // 需要加载的组件 (应该是一个 `Promise` 对象)
          component: ComponentMap[this.componentInfo.uuid].configComponent,
          // 异步组件加载时使用的组件
          loading: LoadingComponent,
          // 加载失败时使用的组件
          error: ErrorComponent,
          // 展示加载时组件的延时时间。默认值是 200 (毫秒)
          delay: 500,
          // 如果提供了超时时间且组件加载也超时了，
          // 则使用加载失败时使用的组件。默认值是：`Infinity`
          timeout: 10000
        })
      } else {
        if (!this.componentData || this.componentDefaultData.items[0].componentId !== this.componentData.items[0].componentId) {
          this.componentInfo = this.deepCopy(this.componentDefaultData)
        } else {
          this.componentInfo = this.deepCopy(this.componentData)
        }

        // 处理数据类型错误的情况
        for (const i in this.componentInfo.items[0].configs) {
          if (this.componentInfo.items[0].configs[i]['code'] === 'inputType') {
            if (this.componentInfo.items[0].configs[i]['configData']['configData'] || this.componentInfo.items[0].configs[i]['configData']['configData'] === 0 || this.componentInfo.items[0].configs[i]['configData']['configData'] === '0') {
              this.componentInfo.items[0].configs[i]['configData']['configData'] = parseInt(this.componentInfo.items[0].configs[i]['configData']['configData'])
            }
            if (this.componentInfo.items[0].configs[i]['defaultValue'] || this.componentInfo.items[0].configs[i]['defaultValue'] === 0 || this.componentInfo.items[0].configs[i]['defaultValue'] === '0') {
              this.componentInfo.items[0].configs[i]['defaultValue'] = parseInt(this.componentInfo.items[0].configs[i]['defaultValue'])
            }
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'convert') {
            this.componentInfo.items[0].configs[i]['configData']['configData'] = JSON.parse(this.componentInfo.items[0].configs[i]['configData']['configData'])
            this.componentInfo.items[0].configs[i]['defaultValue'] = JSON.parse(this.componentInfo.items[0].configs[i]['defaultValue'])
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'readonly') {
            this.componentInfo.items[0].configs[i]['configData']['configData'] = JSON.parse(this.componentInfo.items[0].configs[i]['configData']['configData'])
            this.componentInfo.items[0].configs[i]['defaultValue'] = JSON.parse(this.componentInfo.items[0].configs[i]['defaultValue'])
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'maxLength') {
            if (this.componentInfo.items[0].configs[i]['configData']['configData'] || this.componentInfo.items[0].configs[i]['configData']['configData'] === 0 || this.componentInfo.items[0].configs[i]['configData']['configData'] === '0') {
              this.componentInfo.items[0].configs[i]['configData']['configData'] = parseInt(this.componentInfo.items[0].configs[i]['configData']['configData'])
            }
            if (this.componentInfo.items[0].configs[i]['defaultValue'] || this.componentInfo.items[0].configs[i]['defaultValue'] === 0 || this.componentInfo.items[0].configs[i]['defaultValue'] === '0') {
              this.componentInfo.items[0].configs[i]['defaultValue'] = parseInt(this.componentInfo.items[0].configs[i]['defaultValue'])
            }
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'minLength') {
            if (this.componentInfo.items[0].configs[i]['configData']['configData'] || this.componentInfo.items[0].configs[i]['configData']['configData'] === 0 || this.componentInfo.items[0].configs[i]['configData']['configData'] === '0') {
              this.componentInfo.items[0].configs[i]['configData']['configData'] = parseInt(this.componentInfo.items[0].configs[i]['configData']['configData'])
            }
            if (this.componentInfo.items[0].configs[i]['defaultValue'] || this.componentInfo.items[0].configs[i]['defaultValue'] === 0 || this.componentInfo.items[0].configs[i]['defaultValue'] === '0') {
              this.componentInfo.items[0].configs[i]['defaultValue'] = parseInt(this.componentInfo.items[0].configs[i]['defaultValue'])
            }
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'rows') {
            if (this.componentInfo.items[0].configs[i]['configData']['configData'] || this.componentInfo.items[0].configs[i]['configData']['configData'] === 0 || this.componentInfo.items[0].configs[i]['configData']['configData'] === '0') {
              this.componentInfo.items[0].configs[i]['configData']['configData'] = parseInt(this.componentInfo.items[0].configs[i]['configData']['configData'])
            }
            if (this.componentInfo.items[0].configs[i]['defaultValue'] || this.componentInfo.items[0].configs[i]['defaultValue'] === 0 || this.componentInfo.items[0].configs[i]['defaultValue'] === '0') {
              this.componentInfo.items[0].configs[i]['defaultValue'] = parseInt(this.componentInfo.items[0].configs[i]['defaultValue'])
            }
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'seleteType') {
            if (this.componentInfo.items[0].configs[i]['configData']['configData'] || this.componentInfo.items[0].configs[i]['configData']['configData'] === 0 || this.componentInfo.items[0].configs[i]['configData']['configData'] === '0') {
              this.componentInfo.items[0].configs[i]['configData']['configData'] = parseInt(this.componentInfo.items[0].configs[i]['configData']['configData'])
            }
            if (this.componentInfo.items[0].configs[i]['defaultValue'] || this.componentInfo.items[0].configs[i]['defaultValue'] === 0 || this.componentInfo.items[0].configs[i]['defaultValue'] === '0') {
              this.componentInfo.items[0].configs[i]['defaultValue'] = parseInt(this.componentInfo.items[0].configs[i]['defaultValue'])
            }
          } else if (this.componentInfo.items[0].configs[i]['code'] === 'seleteChild') {
            this.componentInfo.items[0].configs[i]['configData']['configData'] = JSON.parse(this.componentInfo.items[0].configs[i]['configData']['configData'])
            this.componentInfo.items[0].configs[i]['defaultValue'] = JSON.parse(this.componentInfo.items[0].configs[i]['defaultValue'])
          }
        }

        // console.log(ComponentMap[this.componentInfo.items[this.itemId].uuid].configComponent)

        this.AsyncComponent = () => ({
          // 需要加载的组件 (应该是一个 `Promise` 对象)
          component: ComponentMap[this.componentInfo.items[this.itemId].uuid].configComponent,
          // 异步组件加载时使用的组件
          loading: LoadingComponent,
          // 加载失败时使用的组件
          error: ErrorComponent,
          // 展示加载时组件的延时时间。默认值是 200 (毫秒)
          delay: 500,
          // 如果提供了超时时间且组件加载也超时了，
          // 则使用加载失败时使用的组件。默认值是：`Infinity`
          timeout: 10000
        })
      }
    },
    Input (val) {
      if (this.useItem) {
        if (this.componentInfoData) {
          this.componentInfo = this.componentInfoData
          this.componentInfo.items[this.componentInfoDataInx].configs = val.value
        } else {
          this.componentInfo.useItem = true
          this.componentInfo.configs = val.value
        }
      } else {
        if (this.componentInfo.items[0].children && this.componentInfo.items[0].children.length) {
          this.componentInfo.items[0].children[this.itemId + 1].configs = val.value
        } else {
          this.componentInfo.items[this.itemId].configs = val.value
        }
      }
      this.$emit('updateComponentData', this.componentInfo)
      // this.$emit('updateComponentData', val.value)
    },
    Update () {
      // 仅用于树形组件
      this.$emit('updateConfig', true)
    }
  }
}
</script>
