import Request from '@/libs/request'

export function activateUser (params) {
  return Request.post('/mvp-manager/user/activation', params).then(resp => resp.data)
}

export function checkUserExist (params) {
  return Request.post('/mvp-manager/user/exist', params).then(resp => resp.data)
}

export function validateUrl (params) {
  return Request.post('/mvp-manager/user/checkActivation', params).then(resp => resp.data)
}
