<template>
  <div
    v-global-dom class="layer"
    ref="layer"
    :class="{'moving': moveData.isMove, 'minimum': minimum, 'hide': !value}"
    :style="css"
    @mousemove="layerMouseOver">
    <div class="layer-title" @mousedown.stop="handleMoveMouseDown">
      <slot name="title" v-if="showTitle || ('' + title) !== ''">
        {{title}}
      </slot>
      <div class="layer-tool">
        <span class="show-btn" @click.stop="minimum = !minimum">
          <Icon v-if="!minimum" type="md-remove"></Icon>
          <Icon v-else type="md-resize"></Icon>
        </span>
      </div>
    </div>
    <div class="layer-content" :class="{'hide': minimum}">
      <slot>
        content
      </slot>
    </div>
    <div v-if="!minimum && showFooter" class="layer-footer">
      <slot name="footer">
        footer
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Layer',
  props: {
    value: {
      type: Boolean,
      required: false,
      default: true
    },
    showTitle: {
      type: Boolean,
      required: false,
      default: true
    },
    showFooter: {
      type: Boolean,
      required: false,
      default: true
    },
    title: {
      type: String,
      required: false
    },
    css: {
      type: Object,
      required: false
    }
  },
  data () {
    return {
      moveData: {
        isMove: false,
        layerEl: null,
        layerHeight: 0,
        layerWidth: 0,
        innerWidth: 0,
        innerHeight: 0,
        maxX: 0,
        maxY: 0,
        difX: 0,
        difY: 0
      },
      minimum: false
    }
  },
  methods: {
    layerMouseOver (e) {
      // const el = this.$refs.layer
      // const top = e.pageY - el.offsetTop - el.clientHeight
      // const left = e.pageX - el.offsetLeft - el.clientWidth
      // if (top >= -16 && left >= -16) {
      //   if (el.style.cursor !== 'nwse-resize') {
      //     el.style.cursor = 'nwse-resize'
      //   }
      // } else {
      //   el.style.cursor = ''
      // }
    },
    handleMoveMouseDown (e) {
      e.preventDefault()
      this.moveData.layerEl = this.$refs.layer
      this.moveData.layerWidth = this.moveData.layerEl.clientWidth
      this.moveData.layerHeight = this.moveData.layerEl.clientHeight
      this.moveData.innerWidth = window.innerWidth
      this.moveData.innerHeight = window.innerHeight
      this.moveData.difX = e.pageX - this.moveData.layerEl.offsetLeft
      this.moveData.difY = e.pageY - this.moveData.layerEl.offsetTop
      this.moveData.maxX = this.moveData.innerWidth - this.moveData.layerWidth
      this.moveData.maxY = this.moveData.innerHeight - this.moveData.layerHeight
      this.moveData.isMove = true
      window.addEventListener('mousemove', this.handleMoveMouseMove)
      window.addEventListener('mouseup', this.handleMoveMouseUp)
    },
    handleMoveMouseMove (e) {
      if (this.moveData.isMove) {
        const offsetLeft = e.pageX - this.moveData.difX
        const offsetTop = e.pageY - this.moveData.difY

        if (offsetLeft <= 0) {
          this.moveData.layerEl.style.left = '0px'
        } else if (offsetLeft >= this.moveData.maxX) {
          this.moveData.layerEl.style.left = `${this.moveData.maxX}px`
        } else {
          this.moveData.layerEl.style.left = `${offsetLeft}px`
        }

        if (offsetTop <= 0) {
          this.moveData.layerEl.style.top = '0px'
        } else if (offsetTop >= this.moveData.maxY) {
          this.moveData.layerEl.style.top = `${this.moveData.maxY}px`
        } else {
          this.moveData.layerEl.style.top = `${offsetTop}px`
        }
      }
    },
    handleMoveMouseUp (e) {
      this.moveData.isMove = false
      window.removeEventListener('mousemove', this.handleMoveMouseMove)
      window.removeEventListener('mouseup', this.handleMoveMouseUp)
    }
  }
}
</script>

<style lang="stylus" scoped>
.layer
  border-radius 0.3rem
  min-height 60vh
  max-height 90vh
  max-width 100vw
  min-width 20rem
  width 20rem
  // width fit-content
  // height fit-content
  z-index 999
  position fixed
  background-color rgba(255,255,255,1)
  box-shadow 0 0 4px 0 rgba(0,0,0,0.1)
  transition background-color ease-in-out .2s
  overflow hidden
  resize auto
  display flex
  flex-direction column
  // top 0
  // left 0
  &.minimum
    min-height 0
    resize none
    height auto!important
  &.hide
    display none
  &.moving
    background-color rgba(255,255,255,0.3)
  .layer-title
    font-size 1rem
    cursor move
    padding 0.2rem 0.5rem
    border-bottom 1px solid rgba(0,0,0,0.06)
    user-select none
    .layer-tool
      position absolute
      top 0
      right 0
      z-index 9999
      .show-btn
        padding 0.3rem 0.5rem 0.2rem 0.5rem
        cursor pointer
  .layer-content
    // padding 0.2rem 0.5rem
    margin 0.4rem
    height 100%
    flex 1 1 auto
    overflow auto
    &.hide
      display none
  .layer-footer
    padding 0.2rem 0.5rem
    border-top 1px solid rgba(0,0,0,0.06)
</style>
