<template>
  <div class="x-radio-group" :name="name">
    <slot/>
  </div>
</template>

<script>
import { findComponentsDownward } from './assist'
import uuidv4 from 'uuid/v4'

export default {
  name: 'xRadioGroup',
  props: {
    value: {
      type: [String, Number],
      required: true
    },
    name: {
      type: String,
      default: uuidv4()
    }
  },
  data () {
    return {
      currentValue: this.value,
      childrens: []
    }
  },
  mounted () {
    this.updateValue()
  },
  watch: {
    value () {
      if (this.currentValue !== this.value) {
        this.currentValue = this.value
        this.$nextTick(() => {
          this.updateValue()
        })
      }
    }
  },
  methods: {
    updateValue () {
      this.childrens = findComponentsDownward(this, 'xRadio')
      if (this.childrens) {
        this.childrens.forEach(child => {
          child.currentValue = this.currentValue === child.label
          child.group = true
        })
      }
    },
    change (data) {
      this.currentValue = data.value
      this.updateValue()
      this.$emit('input', data.value)
      this.$emit('on-change', data.value)
    }
  }
}
</script>

