<template>
  <div class="additional">
    <xModal
        v-model="show"
        :closable="closable"
        async
        @on-ok="ensure">
        <p style="text-align: center" slot="title">
          {{ title }}
        </p>
        <slot></slot>
        <!-- <div slot="foot">
          <Button @click="close" v-if="this.closable">关闭</Button>
          <Button @click="reset" v-else>重做上一步</Button>
          <Button type="primary" @click="ensure">确定</Button>
        </div> -->
    </xModal>
  </div>
</template>

<script>
export default {
  name: 'additional',
  model: {
    prop: 'value',
    event: 'show'
  },
  data () {
    return {
      closable: false
    }
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    value: {
      type: [Boolean, Number],
      required: false,
      default: true
    },
    additionalKey: {
      type: String,
      required: true
    }
  },
  computed: {
    show: {
      get () {
        return !!this.value
      },
      set (val) {
        this.closable = this.value !== 2
        this.$emit('show', val)
      }
    }
  },
  methods: {
    close () {
      this.$emit('show', false)
    },
    ensure () {
      this.$emit('have', this.additionalKey)

      if (this.value === 2) {
        this.$emit('commit', {})
      }

      this.$emit('show', false)
    },
    reset () {
      window.location.reload()
    }
  }
}
</script>
