import axios from 'axios'
import { getToken } from './util'
import { BASE_URL } from './config'
import iView from 'iview'
import uuidv4 from 'uuid/v4'
// import Vue from 'vue'

function requestInterceptor (config) {
  iView.LoadingBar.start()
  config.headers.common.token = getToken()
  return config
}

function responseInterceptor (response) {
  if (response.data.response.code === 777 || response.data.response.code === 999) {
    // iView.Message.error('登录过期，请重新登录!')
    window.localStorage.removeItem('token')
    window.localStorage.removeItem('userInfo')
    window.location.href = window.location.origin + '/#/login'
  }
  if (response.data.response.code === 417) {
    iView.Message.error(response.data.response.msg)
  }

  if (response && (response.data.response && !response.data.response.success)) {
    // if (iView.Message.error) {
    //   // window.Ialert('错误120: 后端错误，' + response.data.response.msg)
    //   iView.Message.error('错误120: 后端错误，' + response.data.response.msg)
    // } else {
    //   iView.Message.error('错误120: 后端错误，' + response.data.response.msg)
    // }
    // throw new Error('拦截器！！请求返回错误：' + response.data.response.msg)
  }
  iView.LoadingBar.finish()
  return response
}

function handleError (err) {
  console.error(err)
  iView.LoadingBar.error()
  return Promise.reject(err)
}

let xAccessToken = window.localStorage.xAccessToken

if (!xAccessToken) {
  xAccessToken = uuidv4()
  window.localStorage.xAccessToken = xAccessToken
}

let config = {
  baseURL: BASE_URL,
  headers: {
    common: {
      'x-access-token': xAccessToken,
      token: getToken()
    }
  }
}

// if (!Vue.prototype.$request) {
//   const instance = axios.create(config)
//   instance.interceptors.request.use(requestInterceptor)
//   instance.interceptors.response.use(responseInterceptor, handleError)
//   Vue.prototype.$request = instance
// }

// export default Vue.prototype.$request

const instance = axios.create(config)
instance.interceptors.request.use(requestInterceptor)
instance.interceptors.response.use(responseInterceptor, handleError)
export default instance
