import Request from '@/libs/request'

export default {
  async getProjectList (params) {
    try {
      const { data } = await Request.post('/mvp-manager/project/list', params)
      if (data.response.success) {
        return data.data
      }
    } catch (error) {
      console.log(error)
    }
    return null
  },
  async removeToTrash (params) {
    try {
      const { data } = await Request.post('/mvp-manager/project/delete', params)
      if (data.data.response.code === 0) {
        return true
      }
    } catch (error) {
      console.log(error)
    }
    return null
  },
  async newMindMap (params) {
    try {
      const { data } = await Request.post('/mvp-manager/project/save', params)
      if (data.status === 200 && data.data.response.code === 0) {
        return true
      }
    } catch (error) {
      console.log(error)
    }
    return null
  }
}
