function bind (el, binding, vNode) {
  let resizeAble = binding.value || { width: true, height: true }
  let oldClientX, oldClientY
  let direction = ''
  let width, height, left, right, top, bottom

  let position = getComputedStyle(el).getPropertyValue('position')
  if (position === 'static') {
    el.style.position = 'relative'
  }

  el._dragResizeMouseMove = function (event) {
    let bodyWidth = document.body.clientWidth
    let bodyHeight = document.body.clientHeight
    let detaX = 0
    let detaY = 0
    let maxLeft = left
    let maxRight = bodyWidth - right
    let minHorizontal = Math.min(0, -1 * (width - 20))
    let maxTop = top
    let maxBottom = bodyHeight - bottom
    let minVertical = Math.min(0, -1 * (height - 20))

    if (direction === 'left' || direction === 'right') {
      detaX = event.clientX - oldClientX
      detaX = direction === 'right' ? detaX : -1 * detaX
      detaX = Math.max(detaX, minHorizontal)
    } else {
      detaY = event.clientY - oldClientY
      detaY = direction === 'bottom' ? detaY : -1 * detaY
      detaY = Math.max(detaY, minVertical)
    }
    let style = {}
    if (direction === 'left') {
      detaX = Math.min(maxLeft, detaX)
      style['width'] = width + detaX
      style['left'] = left - detaX
    } else if (direction === 'right') {
      detaX = Math.min(maxRight, detaX)
      style['width'] = width + detaX
      style['left'] = left
    } else if (direction === 'top') {
      detaY = Math.min(maxTop, detaY)
      style['height'] = height + detaY
      style['top'] = top - detaY
    } else if (direction === 'bottom') {
      detaY = Math.min(maxBottom, detaY)
      style['height'] = height + detaY
      style['top'] = top
    }
    for (let key in style) {
      style[key] = Math.round(style[key])
    }
    if (style['width']) {
      el.style['width'] = style['width'] + 'px'
      el.style['left'] = style['left'] + 'px'
      el.style['right'] = 'auto'
    }
    if (style['height']) {
      el.style['height'] = style['height'] + 'px'
      el.style['top'] = style['top'] + 'px'
      el.style['bottom'] = 'auto'
    }
  }

  el._dragResizeMouseDown = function (event) {
    event.preventDefault()
    let rect = el.getBoundingClientRect()
    let element = event.target
    direction = element._directionType
    oldClientX = event.clientX
    oldClientY = event.clientY
    width = rect.width
    height = rect.height
    left = rect.left
    right = rect.right
    top = rect.top
    bottom = rect.bottom
    document.removeEventListener('mousemove', el._dragResizeMouseMove)
    document.addEventListener('mousemove', el._dragResizeMouseMove)
    document.removeEventListener('mouseup', el._dragResizeMouseUp)
    document.addEventListener('mouseup', el._dragResizeMouseUp)
  }

  el._dragResizeMouseUp = function (event) {
    document.removeEventListener('mousemove', el._dragResizeMouseMove)
    document.removeEventListener('mouseup', el._dragResizeMouseUp)
  }
  el._childList = {}
  if (resizeAble.width) {
    el._childList.left = createElement(el, 'left')
    el._childList.right = createElement(el, 'right')
  }
  if (resizeAble.height) {
    el._childList.top = createElement(el, 'top')
    el._childList.bottom = createElement(el, 'bottom')
  }
}

function unbind (el, binding, vNode) {
  for (let key in el._childList) {
    let node = el._childList[key]
    node.removeEventListener('mousedown', el._dragResizeMouseDown)
    el.removeChild(node)
  }
  delete el._childList
  document.removeEventListener('mousemove', el._dragResizeMouseMove)
  document.removeEventListener('mouseup', el._dragResizeMouseUp)
  delete el._dragResizeMouseMove
  delete el._dragResizeMouseup
  delete el._dragResizeMouseDown
}

function createElement (node, position) {
  let deta = '20px'
  let transVertical = '0'
  let transHorizontal = '0'
  let width, height, cursor, left, top
  let element = document.createElement('div')
  element._directionType = position
  width = height = '100%'
  left = top = 'auto'
  if (position === 'left' || position === 'right') {
    width = deta
    cursor = 'w-resize'
    transHorizontal = position === 'left' ? '-50%' : '50%'
    top = '0'
  } else {
    height = deta
    cursor = 'n-resize'
    transVertical = position === 'top' ? '-50%' : '50%'
    left = '0'
  }
  let style = {
    height,
    width,
    top,
    left,
    cursor,
    position: 'absolute',
    'z-index': '-1',
    transform: `translate(${transHorizontal}, ${transVertical})`
  }
  style[position] = '0'
  for (let key in style) {
    element.style[key] = style[key]
  }
  node.appendChild(element)
  element.addEventListener('mousedown', node._dragResizeMouseDown)
  return element
}

export default {
  bind,
  unbind
}
