/**
 * 针对iView模态框写的拖动指令, 建议使用header插槽并将selector传递给指令。
 * 其他地方请使用v-drag
 */
import drag from './drag'
const DOM_SELECTOR = '.ivu-modal'
const DOM_DRAG_TRIGGER = '.ivu-modal-header'

function inserted (el, binding, vNode) {
  let node = el.querySelector(DOM_SELECTOR)
  node.style.position = 'fixed'
  node.style.margin = 'auto'
  node.style.left = '0'
  node.style.right = '0'
}

function bind (el, binding, vNode) {
  if (!binding.value) {
    binding.value = DOM_DRAG_TRIGGER
  }
  let node = el.querySelector(DOM_SELECTOR)
  if (node) {
    drag.bind(node, binding, vNode)
  }
}

function unbind (el, binding, vNode) {
  if (!binding.value) {
    binding.value = DOM_DRAG_TRIGGER
  }
  let node = el.querySelector(DOM_SELECTOR)
  if (node) {
    drag.unbind(node, binding, vNode)
  }
}

export default {
  inserted,
  bind,
  unbind
}
