
let componentList = {}
let files = require.context('.', false, /\.vue$/)
files.keys().forEach(key => {
  let data = files(key).default
  if (data.notPopMenu) return
  componentList[key.replace(/\.\/|\.vue$/g, '')] = {
    title: data.showTitle || key,
    component: data,
    original: !data.notOriginal,
    props: {}
  }
})
export default componentList
