import Request from '@/libs/request'

export function getProfessionalList (params) {
  const url = params.pageNo ? '/mvp-manager/professional/list' : '/mvp-manager/professional/linkList'
  return Request.post(url, params).then(resp => resp.data)
}

export function saveProfessional (params) {
  const url = params.id ? '/mvp-manager/professional/update' : '/mvp-manager/professional/save'
  return Request.put(url, params).then(resp => resp.data)
}

export function deleteProfessional (params) {
  return Request.post('/mvp-manager/professional/delete', params).then(resp => resp.data)
}
