<template>
  <div class="container">
    <div class="tabs-title">
      <div
        class="tabs-label"
        @click="chooseTabs='first'"
        :class="chooseTabs==='first' ? 'choose' : ''">
        变量设置
        <span @click.stop="lockTab=!lockTab">
          <Icon type="md-lock" title="tab已锁定" v-if="lockTab"></Icon>
          <Icon type="md-unlock" title="tab未锁定" v-else></Icon>
        </span>
      </div>
      <div
        class="tabs-label"
        @click="chooseTabs='second'"
        :class="chooseTabs==='second' ? 'choose' : ''">
        测试结果
      </div>
    </div>
    <div
      class="tabs-content"
      v-if="codeList.length">
      <div
        class="test-set"
        v-show="chooseTabs==='first'">
        <div class="test-set-case">
          <Card
            v-for="(testData, testIdx) in testingDatas"
            :key="testIdx">
            <div class="case-title">
              测试用例{{testIdx + 1}}
            </div>
            <div
              class="question"
              v-for="(item, idx) in jsonSchema[testIdx]"
              :key="idx">
              <div v-if="!item.schema">
                {{item.variableName}}: {{item.msg}}
              </div>
              <template v-if="item.schema">
                <h3>({{item.variableName}}) {{item.schema.title}}</h3>
                <div
                  v-for="(value, key) in item.schema.properties"
                  class="schema-item"
                  :key="key">
                  <Modal
                    v-if="value.additional"
                    v-model="value.additionalShow"
                    :title="value.title || '请填写具体信息'"
                    transfer>
                    <schemaComponent
                      v-for="(addValue, addKey) in value.properties"
                      :name="addValue.uuid"
                      :propsObject="addValue"
                      :schemaKey="addKey"
                      @input="Input($event, item.variableName)"
                      @commit="()=>{}"
                      :key="addKey">
                    </schemaComponent>
                  </Modal>
                  <MultiNest
                    v-else-if="key.uuid==='00000000-0000-0000-0000-000000000012' && key.multiKey"
                    :properties="schema.properties"
                    :answers="answers"
                    :title="key.title"
                    :schemaKey="key"
                    :multiKey="key.multiKey"
                    @additional="()=>{}">
                  </MultiNest>
                  <schemaComponent
                    :name="value.uuid"
                    :propsObject="value"
                    :schemaKey="key"
                    @input="Input($event, item.variableName, testIdx, idx, key, value)"
                    @commit="Input($event, item.variableName, testIdx, idx, key, value)">
                  </schemaComponent>
                </div>
              </template>
            </div>
            <div
              class="question"
              v-if="Object.keys(testData.conditions).length">
              <div
                v-for="(value, key) of testData.conditions"
                :key="key">
                <Checkbox
                  size="small"
                  :value="value"
                  @on-change="changeCondition($event, testIdx, key)"
                  v-if="conditions[key].title">
                  <span class="condition-title">
                    ({{key}}) {{conditions[key].title}}
                  </span>
                </Checkbox>
                <div v-else>
                  {{key}}:{{ conditions[key].errorMsg }}
                </div>
              </div>
            </div>
            <Button @click="addTestCase(testIdx)">添加测试用例</Button>
            <Button
              @click="deleteTestCase(testIdx)"
              v-if="testingDatas.length > 1">删除测试用例</Button>
          </Card>
        </div>
        <div class="test-button-list">
          <Button
            class="button-test"
            type="success"
            @click="testCode">点此测试</Button>
          <Button
            type="default"
            @click="getJsonSchema(true)">清空测试用例</Button>
        </div>
      </div>
      <div
        class="test-result"
        v-show="chooseTabs === 'second'">
        <div class="test-result-content">
          <div class="test-result-items">
            <div
              class="item"
              v-for="(result, idx) in testResult"
              :key="idx">
              <div class="item-cases">
                <div
                  class="item-case"
                  v-for="(resultValue, key) in result"
                  v-if="key!=='result'"
                  :key="key">
                  <strong>{{ key }}
                    <span>({{ resultValue.title }})</span>:</strong>
                  <span v-if="typeof resultValue.value === 'boolean'">{{ resultValue.value }}</span>
                  <template v-else>
                    <span
                      class="test-data"
                      v-for="(item, itemIdx) of resultValue.value"
                      :key="itemIdx">
                      <span v-if="item.title"> {{ item.title }}: </span>{{ item.value }}
                    </span>
                  </template>
                </div>
              </div>
              <div
                class="item-result"
                v-html="result.result">
              </div>
              <Button
                class="error-link"
                type="warning"
                @click.stop="focusErrorCode"
                v-if="showErrorButton && errorCodeIdx!==-1">
                跳转到错误代码块
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import report from '@/api/report'
import schemaComponent from '@components/schemaComponent'
import Additional from '@components/additional'
import MultiNest from '@components/multiNest'
import showdown from 'showdown'

export default {
  name: 'code-test',
  showTitle: '代码测试',
  data () {
    return {
      variable: { $v: [], $q: [] },
      testingDatas: [],
      jsonSchema: [], // 变量填写json模式
      conditions: [], // 布尔变量设置
      testResult: [], // 测试结果
      chooseTabs: 'first', // tab选择
      converter: null,
      timerIndex: 0,
      errorCodeIdx: -1,
      lockTab: false,
      readyGetJsonSchema: _.debounce(this.getJsonSchema, 500),
      suspendTest: 0 // 挂起测试，待一些事情完成之后再触发
    }
  },
  props: {
    sourceId: {
      type: [String, Number],
      default: 0
    },
    sourceType: {
      type: [String, Number],
      default: 0
    },
    codeList: {
      type: Array,
      default: () => []
    },
    projectId: {
      type: [String, Number],
      default: ''
    },
    showErrorButton: {
      type: Boolean,
      default: false
    }
  },
  components: {
    schemaComponent,
    Additional,
    MultiNest
  },
  created () {
    if (!this.converter) {
      this.converter = new showdown.Converter({ literalMidWordUnderscores: true })
    }
    this.$bus.$on('code-test', this.readyTestCode)
  },
  destroyed () {
    this.$bus.$off('code-test', this.readyTestCode)
  },
  mounted () {
    if (this.projectId) {
      this.readyTestCode()
    }
  },
  methods: {
    removeAnnotation (sText) {
      var reg = /("([^\\\"]*(\\.)?)*")|('([^\\\']*(\\.)?)*')|(\/{2,}.*?(\r|\n))|(\/\*(\n|.)*?\*\/)/g // eslint-disable-line
      let text = sText.replace(reg, function (word) {
        return /^\/{2,}/.test(word) || /^\/\*/.test(word) ? '' : word
      })
      return text
    },
    analysisCode (sText) { // 提取变量
      sText = this.removeAnnotation(sText)
      let $q = /\$(qp?[\d_a-zA-Z]+)([^->\d_])/gi
      let $v = /\$(vp?[\d_a-zA-Z]+)|\$(qp?[\d_]+)(->\w+)/gi
      let qMatchList = (sText ? sText.match($q) : null) || []
      let vMatchList = (sText ? sText.match($v) : null) || []
      let data = {
        $q: [],
        $v: []
      }
      for (let match of qMatchList) {
        let qReg = /\$qp?[\d_]+/gi
        let result = qReg.exec(match)
        while (result) {
          data.$q.push(result[0].slice(1))
          result = qReg.exec(match)
        }
      }
      for (let match of vMatchList) {
        let vReg = /\$[qv]p?[\d_]+/gi
        let result = vReg.exec(match)
        while (result) {
          data.$v.push(result[0].slice(1))
          result = vReg.exec(match)
        }
      }
      return data
    },
    getVariableList () {
      this.variable = {
        $q: [],
        $v: []
      }
      let varList
      for (let code of this.codeList) {
        varList = this.analysisCode(code)
        this.variable.$q.push(...varList.$q)
        this.variable.$v.push(...varList.$v)
      }
      let func = (currentList, item) => {
        if (!currentList.includes(item)) {
          currentList.push(item)
        }
        return currentList
      }
      this.variable.$q = this.variable.$q.reduce(func, [])
      this.variable.$v = this.variable.$v.reduce(func, [])
    },
    async getJsonSchema (clear = false) {
      this.getVariableList()
      if (!this.variable.$v.length && !this.variable.$q.length) {
        if (this.suspendTest) {
          this.testCode()
          this.suspendTest = 0
        }
        return
      }
      let data = await report.getJsonSchema({
        variablesNames: [...this.variable.$v],
        conditionNames: [...this.variable.$q],
        projectId: this.projectId,
        sourceId: this.sourceId,
        sourceType: this.sourceType
      })
      if (data) {
        this.jsonSchema = data.jsonSchemaResponses ? [data.jsonSchemaResponses] : []
        this.conditions = data.conditionVariableBaseInfo || {}
        let testingDatas = data.testingDatas
        this.initTestDatas(testingDatas, clear)
      } else {
        this.$Message.error('获取变量数据错误')
      }
      if (this.suspendTest) {
        this.testCode()
        this.suspendTest = 0
      }
    },
    initTestDatas (testingDatas, clear = false) {
      let data = {
        conditions: {},
        variables: []
      }
      if (this.jsonSchema.length) {
        for (let item of this.jsonSchema[0]) {
          let info = {
            datas: [],
            name: item.variableName
          }
          data.variables.push(info)
        }
      }
      for (let item in this.conditions) {
        data.conditions[item] = false
      }
      this.testingDatas.splice(0, this.testingDatas.length, data)
      if (clear) return
      // 根据历史测试用例填值。
      if (!testingDatas || !testingDatas.length) return
      for (let index in testingDatas) {
        if (!this.testingDatas[index]) {
          this.addTestCase(index - 1)
        }
        let testCaseData = testingDatas[index]
        let schemaItems = this.jsonSchema[index]
        let testCase = this.testingDatas[index]
        let conditions = testCase.conditions
        for (let condition in conditions) {
          conditions[condition] = testCaseData.conditions[condition] || false
        }
        if (!schemaItems) continue
        let variables = testCase.variables
        for (let variable of variables) {
          let variableData = testCaseData.variables.find(item => item.name === variable.name)
          if (!variableData) continue
          for (let item of variableData.datas) {
            let schemaItem = schemaItems.find(val => val.schema.properties.hasOwnProperty(item.schemaKey))
            if (schemaItem) {
              let schemaValue = ''
              if (item.value.length === 1) {
                schemaValue = typeof item.value[0] === 'string' ? item.value[0] : JSON.stringify(item.value[0])
              } else {
                schemaValue = JSON.stringify(item.value)
              }
              schemaItem.schema.properties[item.schemaKey].value = schemaValue
              let itemData = variable.datas.find(val => val.schemaKey === item.schemaKey)
              if (itemData) {
                itemData.value = item.value
              } else {
                variable.datas.push({
                  schemaKey: item.schemaKey,
                  value: item.value
                })
              }
            }
          }
        }
      }
    },
    readyTestCode () {
      this.readyGetJsonSchema()
      this.suspendTest = 1
    },
    async testCode () {
      this.suspendTest = 0
      if (!this.codeList.length) return
      let params = {
        codes: this.codeList,
        testingCases: this.testingDatas,
        projectId: this.projectId,
        sourceId: this.sourceId,
        sourceType: this.sourceType
      }
      let resData = await report.testCode(params)
      let data
      if (resData.response.success) {
        data = resData.data
      }
      this.errorCodeIdx = -1
      if (data && data.length === 1) {
        let item = data[0]
        if (!item.result.response.success) { // 代码执行报错
          this.errorCodeIdx = item.result.data.order
        }
      }
      if (data) {
        let testResult = []
        for (let item of data) {
          let resultText = (item.result.data ? item.result.data.result : null) || item.result.response.msg || ''
          if (this.errorCodeIdx === -1) { // 错误输出不转义
            try {
              resultText = this.converter.makeHtml(resultText)
            } catch (error) {
              console.log(error)
            }
          }
          let result = { 'result': resultText }
          if (item.conditions) {
            for (let key in item.conditions) {
              result[key] = item.conditions[key]
            }
          }
          if (item.datas) {
            for (let key in item.datas) {
              result[key] = item.datas[key]
            }
          }
          testResult.push(result)
        }
        this.testResult = testResult
        if (!this.lockTab) {
          this.chooseTabs = 'second'
        }
      } else {
        this.testResult = [{ 'result': resData.response.msg }]
        if (!this.lockTab) {
          this.chooseTabs = 'second'
        } else {
          this.$Message.error('测试请求未成功，详情请查看测试结果')
        }
      }
      if (data && this.errorCodeIdx === -1) {
        this.$bus.$emit('code-test-pass', this.sourceType)
      } else {
        this.$bus.$emit('code-test-error-card', this.errorCodeIdx, this.sourceType)
      }
      this.$bus.$emit('code-test-end', this.sourceType)
    },
    addTestCase (testIdx) {
      if (!this.testingDatas.length) {
        this.initTestDatas()
      } else {
        let data = _.cloneDeep(this.testingDatas[testIdx])
        this.testingDatas.splice(testIdx + 1, 0, data)
        let schema = _.cloneDeep(this.jsonSchema[testIdx])
        if (schema) {
          this.jsonSchema.splice(testIdx + 1, 0, schema)
        }
      }
    },
    deleteTestCase (testIdx) {
      this.testingDatas.splice(testIdx, 1)
      this.jsonSchema.splice(testIdx, 1)
    },
    Input (val, name, idx, schemaIndex, key, value) {
      if (val.value) {
        let value = {
          schemaKey: val.schemaKey,
          value: Array.isArray(val.value) ? val.value : [val.value]
        }
        let variables = this.testingDatas[idx].variables
        let data = variables.find(item => item.name === name)
        if (data) {
          let oldValueIdx = data.datas.findIndex(item => item.schemaKey === val.schemaKey)
          if (oldValueIdx === -1) {
            data.datas.push(value)
          } else {
            data.datas.splice(oldValueIdx, 1, value)
          }
        }
        this.$Message.success('input')
        let answerSchema = this.jsonSchema[idx][schemaIndex].schema.properties[key].properties[value.value[0]]
        console.log(answerSchema, 'answerSChema')
        if (answerSchema.additionalKeys) {
          this.$Message.success('addtional')
          let schema = this.jsonSchema[idx][schemaIndex].schema.properties[answerSchema.additionalKeys[0]]
          schema.additionalShow = true
        }
      }
      // console.log(JSON.stringify(this.testingDatas))
    },
    changeCondition (value, testIdx, key) {
      this.testingDatas[testIdx].conditions[key] = value
      // console.log(JSON.stringify(this.testingDatas), 'changeCodition')
    },
    focusErrorCode () {
      this.$bus.$emit('code-test-focus-error-code')
    }
  },
  watch: {
    // codeList (newValue, oldValue) {
    //   this.readyGetJsonSchema()
    //   this.suspendTest = 1
    // },
    jsonSchema (newValue, oldValue) {
      if (!this.jsonSchema.length) {
        this.testingDatas.forEach((item) => {
          item.variables = []
        })
        return
      }
      let varList = this.jsonSchema[0].map(item => item.variableName)
      let excludeList = [] // testingData中
      this.testingDatas[0].variables.forEach(item => {
        if (!varList.includes(item.name)) {
          excludeList.push(item.name)
        }
      })
      for (let data of this.testingDatas) {
        // 保存旧的变量
        let lstOldVar = []
        for (let varInfo of data.variables) {
          if (!excludeList.includes(varInfo.name)) {
            lstOldVar.push(varInfo)
          }
        }
        data.variables = lstOldVar
        let lstNewVar = _.cloneDeep(varList)
        // 排除已有的变量
        for (let varInfo of data.variables) {
          let index = lstNewVar.indexOf(varInfo.name)
          if (index !== -1) {
            lstNewVar.splice(index, 1)
          }
        }
        // 添加没有的变量
        for (let name of lstNewVar) {
          let info = {
            datas: [],
            name
          }
          data.variables.push(info)
        }
      }
    }
  }
}
</script>

