<template>
  <div
    ref="menuContent"
    :class="{
      'pop-container': true,
      'pop-container-hide': !show,
      'silence': silence
    }"
    v-bind-key
    v-drag-resize
    v-drag="{
      selector: '.header',
      horizonctal: 100
    }">
    <div class="header" @dblclick.stop="silenceHelper">
      <div class="title">小助手</div>
      <div class="fill"></div>
      <div class="header-menu">
        <div
          v-if="silence"
          title="放大"
          @click.stop="silenceHelper"
          class="header-max">
          <div class="max-box">
          </div>
        </div>
        <Icon
          class="header-hide"
          title="最小化"
          @click.stop="silenceHelper"
          type="ios-remove"
          v-else>
        </Icon>
        <Icon class="close" type="ios-close" @click.stop="close"></Icon>
      </div>
    </div>
    <div class="content" v-show="!silence">
      <div :class="{
          'resource-manager': true,
          'resource-manager-hide': !showResource
        }">
        <div class="resource-list" v-show="showResource">
          <div
            :class="{
              resource: true,
              'current-menu': currentMenuList[activePage] === title
            }"
            v-for="(item, title) of componentInfo"
            @click.stop="showMenu(title, { auto: false })"
            :key="title">
            {{ item.title }}
          </div>
        </div>
        <div class="resource-show" @click.stop="switchShowResource">
          <Icon
            class="show-icon"
            type="ios-arrow-back"
            title="隐藏"
            v-if="showResource">
          </Icon>
          <Icon
            title="展开"
            type="ios-arrow-forward"
            class="show-icon"
            v-else>
          </Icon>
        </div>
      </div>
      <div
        :class="{
        'content-board': true,
        'content-board-resource-show': showResource
        }">
        <template
          v-for="(page, index) of pageList"
          v-if="page.length && show">
          <div
            :class="pageNum === 2 ? 'double-page' : 'single-page'"
            @click="changeActivePage(index)"
            :key="index+'-page'">
            <div class="tabs" v-if="currentMenuList[index]" @drop="handleDrop(index)" @dragover="handleDragOver">
              <div
                v-for="(title, idx) of pageList[index]"
                :class="{
                  'tab-item': true,
                  'current-menu': index - activePage === 0 && currentMenuList[index] === title
                }"
                @click="showMenu(title, { auto: false, page: index })"
                draggable
                @dragstart="handleDragStart(index, title)"
                @dragover="handleDragOverTab(index, title)"
                @dragleave="handleDragLeave"
                @dragend="handleDragEnd"
                :key="idx">
                <span class="tab-content">
                {{ idx + 1 }} {{ componentInfo[title].title }}
                </span>
                <Icon
                  class="close"
                  type="ios-close"
                  @click.stop="hideMenu(title, index)"
                  title="关闭">
                </Icon>
              </div>
              <div class="placeholder"></div>
              <div
                class="refresh"
                title="刷新内容"
                @click.stop="refreshContent(index)">
                <Icon type="ios-refresh" />
              </div>
              <div
                v-if="pageNum === 1 && (pageList[activePage].length > 1)"
                @click.stop="splitPage()"
                title="向右分页Ctrl+Shift+S"
                class="devide-page-button">
                <div class="devide-page-box"></div>
                <div class="devide-page-box"></div>
              </div>
              <div
                title="关闭当前分页"
                class="close-page"
                @click.stop="closePage(index)"
                v-if="pageNum > 1">
                <Icon class="close-icon" type="ios-close"></Icon>
              </div>
            </div>
            <div
              class="contents"
              v-if="currentMenuList[index] && hideContent !== index">
              <component
                class="componet"
                v-bind="componentInfo[currentMenuList[index]].props"
                :is="componentInfo[currentMenuList[index]].component">
              </component>
            </div>
          </div>
          <!-- <devide v-if="pageNum === 2 && index === 0" :key="index+'-devide'"/> -->
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import componentsList from '@/components/pop-menus'
import networkDisk from '@/components/network-disk'
// import devide from '@/components/devide'

let data = {
  title: '网盘',
  component: networkDisk,
  original: true,
  props: {}
}
Object.assign(componentsList, {
  networkDisk: data
})

export default {
  // components: {
  //   devide
  // },
  data () {
    return {
      show: false,
      showResource: true,
      currentMenu: '',
      lockCurrentMenu: false,
      /**
       * 常驻组件：不能注销，不会被反复注册
       * 普通组件：可以注销，重复注册会被顶替
       */
      componentInfo: {
        ...componentsList
      },
      conflictInfo: [
        ['codeTest', 'nodeTree']
      ],
      pageList: [[], []],
      currentMenuList: ['', ''],
      activePage: 0,
      showList: [],
      silence: false,
      showScale: false,
      dragInfo: {},
      hideContent: -1,
      openWindow: null
    }
  },
  computed: {
    pageNum () {
      let pages = 0
      for (let page of this.pageList) {
        if (page.length) {
          pages++
        }
      }
      return pages
    }
  },
  watch: {
    silence () {
      this.storageSetItem('helperSilence', this.silence)
    },
    show () {
      this.storageSetItem('showHelper', this.show)
    }
  },
  directives: {
    bindKey: {
      bind: function (el, binding, vNode) {
        el.__listenKeyUp__ = function (event) {
          let _this = el.__vue__
          if (!_this) return
          if (event.ctrlKey && event.shiftKey) {
            if (event.code === 'KeyZ') {
              event.stopPropagation()
              _this.showAssistant()
            } else if (event.code === 'KeyS') {
              _this.splitPage()
            } else if (event.code === 'KeyX') {
              _this.silenceHelper()
            } else if (event.code === 'KeyA') {
              _this.switchShowResource()
            }
          } else if (event.altKey && event.ctrlKey) {
            if (event.code === 'KeyZ') {
              event.stopPropagation()
              _this.showAssistant()
            } else if (event.code === 'KeyL') {
            }
          } else if (event.ctrlKey) {
          } else if (event.altKey) {
            if (event.code.includes('Digit')) {
              let digit = event.code.replace(/[a-zA-Z]/g, '')
              if (!isNaN(digit)) {
                let idx = Number(digit)
                if (idx > 0 && idx <= _this.pageList[_this.activePage].length) {
                  _this.showMenu(_this.pageList[_this.activePage][idx - 1], { auto: false })
                }
              }
            }
          }
        }
        el.__listenKeyDown__ = function (event) {
          let _this = el.__vue__
          if (!_this) return
          if (event.ctrlKey && event.shiftKey) {
            if (event.code === 'KeyZ') {
            } else if (event.code === 'ArrowLeft') {
              _this.throttleChangeSize('width', -1)
            } else if (event.code === 'ArrowRight') {
              _this.throttleChangeSize('width', 1)
            } else if (event.code === 'ArrowUp') {
              _this.throttleChangeSize('height', -1)
            } else if (event.code === 'ArrowDown') {
              _this.throttleChangeSize('height', 1)
            }
          } else if (event.ctrlKey && event.altKey) {
            if (event.code.includes('Arrow')) {
              // let rect = el.getBoundingClientRect()
              let maxTop = document.body.clientHeight - 100
              let maxLeft = document.body.clientWidth - 100
              if (event.code === 'ArrowLeft') {
                _this.throttleChangeSize('left', -3, { min: 0, max: maxLeft })
              } else if (event.code === 'ArrowRight') {
                _this.throttleChangeSize('left', 3, { min: 0, max: maxLeft })
              } else if (event.code === 'ArrowUp') {
                _this.throttleChangeSize('top', -3, { min: 0, max: maxTop })
              } else if (event.code === 'ArrowDown') {
                _this.throttleChangeSize('top', 3, { min: 0, max: maxTop })
              }
            }
          }
        }
        document.addEventListener('keyup', el.__listenKeyUp__)
        document.addEventListener('keydown', el.__listenKeyDown__)
      },
      unbind: function (el, bindding, vNode) {
        document.removeEventListener('keyup', el.__listenKeyUp__)
        document.removeEventListener('keydown', el.__listenKeyDown__)
        delete el.__listenKeyUp__
        delete el.__listenKeyDown__
      }
    }
  },
  created () {
    this.$bus.$on('pop-helper-show', this.showAssistant)
    this.$bus.$on('pop-helper-show-menu', this.showMenu)
    this.$bus.$on('pop-helper-hide-menu', this.hideMenu)
    this.$bus.$on('pop-helper-update-prop', this.updateProps)
    this.$bus.$on('pop-helper-register-component', this.registerComponent)
    this.throttleChangeSize = _.throttle(this.changeSize, 100)
  },
  mounted () {
    this.silence = this.storageGetItem('helperSilence') === true
    if (this.storageGetItem('showHelper') && this.silence) {
      this.showAssistant()
    }
    this.registerObserver()
  },
  beforeDestroy () {
    this.removeObserver()
    this.$bus.$off('pop-helper-show-menu', this.showMenu)
    this.$bus.$off('pop-helper-hide-menu', this.hideMenu)
    this.$bus.$off('pop-helper-update-prop', this.updateProps)
    this.$bus.$off('pop-helper-register-component', this.registerComponent)
  },
  methods: {
    registerComponent (type, options) {
      if (this.componentInfo[type] && this.componentInfo[type].original) return
      let data = {
        title: options.title || '',
        component: options.component,
        props: options.props || {}
      }
      this.componentInfo[type] = data
    },
    unRegisterComponet (type) {
      if (!this.componentInfo[type]) return
      if (this.componentInfo[type].original) return
      this.hidePagesMenu(type)
      delete this.componentInfo[type]
    },
    updateProps (type, props) {
      if (this.componentInfo[type]) {
        for (let key in props) {
          this.$set(this.componentInfo[type].props, key, props[key])
        }
      }
      // for (let idx in this.currentMenuList) {
      //   if (type === this.currentMenuList[idx]) {
      //     this.refreshContent(idx)
      //   }
      // }
    },
    showMenu (type, options = {}) {
      this.showAssistant()
      let page = this.activePage
      if (options.hasOwnProperty('page')) {
        page = options.page
      }
      if (!options.hasOwnProperty('drag')) {
        for (let idx in this.pageList) {
          if (this.pageList[idx].includes(type)) {
            page = idx
            break
          } else if (this.conflictShow(idx, type)) {
            page = idx ^ 1
          }
        }
      }
      if (page >= this.pageList.length) {
        if (this.pageList.length >= 2) {
          page = 0
        } else {
          this.pageList.push([])
        }
      }
      if (!this.componentInfo[type]) return
      let position = options.hasOwnProperty('position') ? options.position : null
      if (!this.pageList[page].includes(type)) {
        if (position !== null) {
          this.pageList[page].splice(position, 0, type)
        } else {
          this.pageList[page].push(type)
        }
      } else {
        let oldIndex = this.pageList[page].indexOf(type)
        if (position && position !== oldIndex) {
          if (oldIndex < position) {
            position -= 1
          }
          this.pageList[page].splice(oldIndex, 1)
          this.pageList[page].splice(position, 0, type)
        }
      }
      this.changeActivePage(page)
      this.currentMenuList.splice(page, 1, type)
    },
    hideMenu (type, idx = null) {
      if (idx === null) {
        idx = this.activePage
      }
      let index = this.pageList[idx].indexOf(type)
      if (index !== -1) {
        this.pageList[idx].splice(index, 1)
      }
      if (this.currentMenuList[idx] === type) { // 如果是当前选择的菜单
        this.currentMenuList[idx] = ''
        let nextShow = (index !== -1 && this.pageList[idx].length > index) ? index : index - 1
        if (nextShow !== -1) {
          let showType = this.pageList[idx][nextShow]
          this.showMenu(showType, { auto: false })
        } else {
          this.closePage(idx)
        }
      }
    },
    conflictShow (idx, menu) {
      for (let list of this.conflictInfo) {
        if (!list.includes(menu)) continue
        for (let item of list) {
          if (menu !== item && this.pageList[idx].includes(item)) {
            return true
          }
        }
      }
      return false
    },
    hidePagesMenu (type) {
      for (let index = 0; index < this.pageList.length; index++) {
        this.hideMenu(type, index)
      }
    },
    refreshContent (idx) {
      this.hideContent = Number(idx)
      this.$nextTick(() => {
        this.hideContent = -1
      })
    },
    showInNewWindow () {
      if (!this.openWindow || this.openWindow.closed) {
        const { href } = this.$router.resolve({ name: 'Helper' })
        this.openWindow = window.open(href, 'helper', 'width=600,height=800,toolbar')
        this.openWindow.onunload = (event) => {
          localStorage.setItem('lockOpen', new Date().getTime())
          setTimeout(() => {
            localStorage.removeItem('lockOpen')
          }, 1000)
        }
        this.openWindow.blur()
      }
      if (window.setTargetWindow) {
        window.setTargetWindow(this.openWindow)
      }
    },
    showAssistant () {
      let lockOpen = localStorage.getItem('lockOpen')
      if (lockOpen) {
        let time = new Date().getTime()
        if (time - lockOpen < 1000) return
        localStorage.removeItem('lockOpen')
      }
      let helperShowType = window.localStorage.getItem('helperShowType') || '1'
      if (helperShowType === '1') { // 新窗口打开
        if (this.show) {
          this.show = false
        }
        this.showInNewWindow()
      } else {
        if (this.openWindow) {
          this.openWindow.close()
        }
        this.show = true
      }
      if (this.show && this.silence) {
        this.$refs.menuContent.animate([
          { boxShadow: '0 0 2px 3px #4878F6' },
          { boxShadow: '0 0 1px 1px #78A4FA' },
          { boxShadow: '0 0 2px 3px #4878F6' },
          { boxShadow: '0 0 1px 1px #78A4FA' },
          { boxShadow: '0 0 2px 3px #4878F6' },
          { boxShadow: '0 0 1px 1px #78A4FA' }
        ], 500)
      }
    },
    close () {
      this.recordPosition()
      this.show = false
    },
    recordPosition () {
      let attrName = this.silence ? 'silenceStyle' : 'activeStyle'
      let style = getComputedStyle(this.$refs.menuContent)
      let recordStyle = {
        width: style.getPropertyValue('width'),
        height: style.getPropertyValue('height'),
        left: style.getPropertyValue('left'),
        top: style.getPropertyValue('top')
      }
      this.storageSetItem(attrName, recordStyle)
    },
    recoveryPosition () {
      let attrName = this.silence ? 'silenceStyle' : 'activeStyle'
      let recordStyle = this.storageGetItem(attrName) || {}
      for (let attr in recordStyle) {
        this.$refs.menuContent.style[attr] = recordStyle[attr]
      }
    },
    silenceHelper () {
      this.silence = !this.silence
      this.recoveryPosition()
    },
    storageSetItem (attr, value) {
      let popInfo = localStorage.getItem('popMenuInfo')
      let data = popInfo ? JSON.parse(popInfo) : {}
      data[attr] = JSON.stringify(value)
      let strInfo = JSON.stringify(data)
      window.localStorage.setItem('popMenuInfo', strInfo)
    },
    storageGetItem (attr) {
      let popInfo = window.localStorage.getItem('popMenuInfo')
      if (!popInfo) return null
      let data = JSON.parse(popInfo)
      let value = data[attr] ? JSON.parse(data[attr]) : null
      return value
    },
    splitPage () {
      let type = this.currentMenuList[this.activePage]
      let idx = this.pageList[0].length ? 0 : 1
      this.hideMenu(type, idx)
      if (!this.pageList[0].length) {
        this.pageList.splice(0, 1)
        this.pageList.push([])
        this.currentMenuList[0] = this.currentMenuList[1]
        this.currentMenuList[1] = ''
      }
      this.showMenu(type, { page: 1 })
    },
    closePage (idx) {
      let list = this.pageList[idx]
      if (list) {
        list.splice(0, list.length)
        this.currentMenuList[idx] = ''
      }
    },
    changeActivePage (idx) {
      idx = Number(idx)
      this.activePage = idx
    },
    switchShowResource () {
      this.showResource = !this.showResource
    },
    handleDrop (index) {
      if (!this.dragInfo.dragging) return
      let drag = this.dragInfo.drag
      let drop = this.dragInfo.onTab
      this.dragInfo = {}
      if (drag.index === index && drop && drop.title === drag.title) return
      if (drop && !this.pageList[index].includes(drop.title)) return
      let showOptions = {
        page: index,
        drag: true
      }
      if (drop) {
        let dropIndex = this.pageList[index].indexOf(drop.title)
        if (drop.position === 'after') {
          dropIndex += 1
        }
        showOptions.position = dropIndex
      }
      if (index !== drag.index) {
        this.hideMenu(drag.title, drag.index)
      }
      this.showMenu(drag.title, showOptions)
    },
    handleDragOver (event) {
      if (this.dragInfo.dragging) {
        event.preventDefault()
      }
    },
    handleDragStart (index, title) {
      this.dragInfo.dragging = true
      this.dragInfo.drag = {
        index,
        title
      }
    },
    handleDragOverTab (index, title) {
      this.dragInfo.onTab = {
        title,
        index
      }
      let rect = event.target.getBoundingClientRect()
      if (event.clientX > rect.left + rect.width / 2) {
        this.dragInfo.onTab.position = 'after'
      } else {
        this.dragInfo.onTab.position = 'before'
      }
    },
    handleDragLeave () {
      delete this.dragInfo.onTab
    },
    handleDragEnd () {
      this.dragInfo = {}
    },
    registerObserver () {
      let MutationObserver = window.MutationObserver || window.WebKitMutationObserver || window.MozMutationObserver
      this.observer = new MutationObserver(() => {
        this.recordPosition()
      })
      this.observer.observe(this.$refs.menuContent, { attributes: true, attributeFilter: ['style'], subtree: true })
    },
    removeObserver () {
      if (this.observer) {
        this.observer.disconnect()
        this.observer.takeRecords()
        this.observer = null
      }
    },
    changeSize (sizeAttr, changeWeight, options = {}) {
      let rect = this.$refs.menuContent.getBoundingClientRect()
      let oldValue = rect[sizeAttr]
      let detaValue = changeWeight * 20
      let newValue = oldValue + detaValue
      if (options.hasOwnProperty('min')) {
        newValue = Math.max(options.min, newValue)
      }
      if (options.hasOwnProperty('max')) {
        newValue = Math.min(options.max, newValue)
      }
      this.$refs.menuContent.animate([
        { [sizeAttr]: oldValue + 'px' },
        { [sizeAttr]: newValue + 'px' }
      ], 100)
      this.$refs.menuContent.style[sizeAttr] = newValue + 'px'
    }
  }
}
</script>

