<template lang="pug">
.page-page(:class="{'full-screen': fullScreen}", v-auto-size='')
  layer(v-model='showNodeTree', title='节点列表', :showFooter='false', :style="{right: '0.5rem', top: '4rem', 'z-index': '99999999999'}")
    nodeEditor(v-if='nodeManager', ref='nodeEditor', :outManager='nodeManager', :editable='false', :key="'floatNodeList'")
  .online(style='color: #777') {{projectOnlines.length ? projectOnlines.length + '人正在编辑该项目' : '您正在编辑该项目'}}
    template(v-for='(user, index) in projectOnlines')
      Tooltip(:content='user.name' placement="bottom")
        Avatar(class='online-avatar' icon='ios-person' size='small' :src='$avatarUrl.replace("avatarID", user.avatar)')
  #scroll(:class="{'full-screen': fullScreen, 'auto-size': autoSize}")
    Row#page-container(:class="{'full-screen': fullScreen}")
      .project-tree(:class="autoSize ? 'auto-size' : ''")
        projectTree(:projectID='rootProjectId', :currentProject="projectId", @nodeClick='treeNodeClick')
      .mind-map(:class="{\
      'full-screen': fullScreen\
      }")
        .tools
          .tool-breadcrumb
            .title-input(v-if='!nodeBreadcrumb || !nodeBreadcrumb.length')
              input(type='text', placeholder='请输入标题', v-model='currentProject.name', ref='title', @change='titleChanged=true', @keyup.enter.stop='updateTitle', @blur='updateTitle', :maxlength='200')
            Breadcrumb.breadcrumb(separator='>', v-if='nodeBreadcrumb.length')
              BreadcrumbItem.breadcrumb-item(@click.native='nodeManager.enterRoot') 首页
              template(v-for='(nodeItem, index) in nodeBreadcrumb')
                BreadcrumbItem.breadcrumb-item(:class="nodeItem.type === 1 ? 'question' : 'answer'", @click.native='goNode(nodeItem)', :key='index')
                  | {{nodeItem.title}}
          .tool
            template(v-for='(item, index) of toolBar')
              Tooltip(placement='top', :transfer='true', :key='index')
                span.tool-item(@click='toolClick($event, item.$fn)')
                  // <i :class="item.icon"></i>
                  icon(:type='item.icon')
                div(slot='content')
                  | {{item.title}}
        .node-page
          .search-panel(v-if='isSearch')
            Card.search-card(:padding='0')
              Row(slot='title', class-name='search-input', type='flex')
                Input.input-box(size='small', placeholder='请输入关键字', autofocus='', v-model='searchInput', @on-change='debounceInput')
              .search-result-panel(v-if='searchList && searchList.length')
                .search-result-row(v-for='(nodeItem, index) in searchList', :key='index', @click='jumpTo(nodeItem)')
                  | {{nodeItem.title}}
          .page-content(ref='nodePage')
            nodeEditor(ref='nodeEditor', :projectId='projectId', :teamId='teamId', @inited='nodeEditorInited', @lockNode='lockNode' @unlockNode='unlockNode')
</template>

<script>
import board from '@/components/node-edit-board'
import projectTree from '@/components/project-tree'
import NodeEditor from '@/components/node-editor'
import { getSearchList } from '@/api/node'
import project from '@/api/project'
import { EmitModel } from '@/libs/EmitModel'

export default {
  name: 'page-page',
  components: {
    board,
    projectTree,
    NodeEditor
  },
  data () {
    return {
      // 项目树
      projectLoading: false, // 项目树加载圈
      projectId: null,
      teamId: null,
      rootProjectId: null,
      isSearch: false,
      searchInput: '',
      keyWords: '',
      searchList: [],
      errMessage: '',
      errMessageShow: false,
      toolBar: [
        {
          title: '设置',
          icon: 'md-settings',
          $fn: () => {
            this.$bus.$emit('show-project-setting')
            // this.showProjectSetting = true
          }
        },
        {
          title: '折叠/展开',
          icon: 'md-code-working',
          $fn: () => {
            this.$refs.nodeEditor.toggleExpandAll()
          }
        },
        {
          title: '测试',
          icon: 'md-play',
          $fn: () => {
            const { href } = this.$router.resolve({
              name: 'Question',
              query: {
                projectId: this.projectId,
                rootId: this.rootProjectId,
                reset: 1
              }
            })
            window.open(href, '_blank') // 在新页面打开
          }
        },
        {
          title: '做题记录',
          icon: 'md-book',
          $fn: () => {
            this.$router.push({
              name: 'AnswerRecord',
              query: {
                rootId: this.rootProjectId,
                projectId: this.projectId
              }
            })
          }
        },
        {
          title: '开始咨询',
          icon: 'md-analytics',
          $fn: () => {
            const { href } = this.$router.resolve({
              name: 'PublicQuestion',
              query: {
                projectId: this.projectId,
                rootId: this.rootProjectId,
                reset: 1
              }
            })
            window.open(href, '_blank') // 在新页面打开
          }
        },
        {
          title: '大表单',
          icon: 'md-list',
          $fn: () => {
            const { href } = this.$router.resolve({
              name: 'PublicQuestionBig',
              query: {
                projectId: this.projectId,
                rootId: this.rootProjectId,
                reset: 1
              }
            })
            window.open(href, '_blank') // 在新页面打开
          }
        },
        {
          title: '报告',
          icon: 'md-document',
          $fn: () => {
            const { href } = this.$router.resolve({
              name: 'Report',
              query: { projectId: this.currentProject.id || this.projectId, rootId: this.rootProjectId }
            })
            window.open(href, '_blank')
          }
        },
        {
          title: '全屏',
          icon: 'md-expand',
          $fn: this.toggleFullScreen
        },
        {
          title: '搜索',
          icon: 'md-search',
          $fn: this.toggleSearch
        }
      ],
      currentProject: {}, // 当前项目
      searchNodeEl: null,
      nodeManager: {},
      searchTarget: null,
      titleChanged: false,
      autoSize: false,
      projectOnlines: []
    }
  },
  directives: {
    autoSize: {
      bind (el, binding, vNode) {
        el.__autoSizeListen = function (event) {
          console.log(event)
          let _this = el.__vue__
          if (event.ctrlKey && event.altKey && (event.code === 'KeyA' || event.code === 'KeyK')) {
            _this.switchAutoSize()
          }
        }
        document.addEventListener('keyup', el.__autoSizeListen)
      },
      unbind (el, binding, vNode) {
        document.removeEventListener('keyup', el.__autoSizeListen)
        delete el.__autoSizeListen
      }
    }
  },
  computed: {
    nodeBreadcrumb () {
      if (this.nodeManager.treeStack) {
        return this.nodeManager.treeStack.slice(1)
      } else {
        return []
      }
    },
    showNodeTree () {
      return this.$store.state.node.showNodeTree
    },
    fullScreen () {
      return this.$store.state.layout.fullScreen
    }
    // componentList () {
    //   return this.$store.state.component.list
    // },
    // componentQuestion () {
    //   return this.$store.state.component.question
    // },
    // componentOptions () {
    //   return this.$store.state.component.options
    // }
  },
  // 节点搜索
  watch: {
    keyWords (newval, oldval) {
      this.errMessageShow = false
      this.getSearchList()
    },
    isSearch (newVal, oldVal) {
      if (!newVal && this.searchNodeEl) {
        this.searchNodeEl.style.border = 'none'
      }
    },
    projectId (newVal, oldVal) {
      this.$bus.$emit('pop-helper-update-prop', 'nodeTree', {
        rootId: this.rootProjectId,
        currentId: this.projectId
      })
      if (oldVal) {
        this.$bus.$socket.emit('leave', oldVal)
        this.$bus.$socket.emit('join', this.projectId)
      }
    }
  },
  created () {
    this.rootProjectId = this.$route.params.projectId
    this.projectId = this.$route.query.projectId || this.$route.params.projectId
    this.$bus.$on('get-root-project', (callback) => callback(this.rootProjectId))
    this.$bus.$on('show-node-tree', this.showPopNodeTree)
    // this.$bus.$off('refresh-node-page')
    // this.$bus.$on('refresh-node-page', this.refreshNodePage)
  },
  mounted () {
    this.$bus.$socket.on('online', (data) => {
      // this.$Notice.info({
      //   title: '信息',
      //   desc: `目前共有${data.data.payload.clients.length}人在编辑该项目`
      // })
      console.log('当前在线：' + data.data.payload.clients.length + '人')
      console.log('房间信息', data)

      const users = data.data.payload.users
      const realUsers = new Map()
      if (users) {
        for (let item of users) {
          realUsers.set(item.id, item)
        }
      }

      this.projectOnlines = [...realUsers.values()]
      // this.projectOnlines = users
    })

    this.$bus.$socket.on('lockNode', (data) => {
      console.log(`%c锁定节点：${data.data.payload}`, 'color: red; font-weight: bold')
      const node = this.nodeManager.nodeMap.get(data.data.payload)
      if (node) {
        node.lock = true
      }
    })
    this.$bus.$socket.on('unlockNode', (data) => {
      console.log(`%c解锁节点：${data.data.payload}`, 'color: red; font-weight: bold')
      const node = this.nodeManager.nodeMap.get(data.data.payload)
      if (node) {
        node.lock = false
      }
    })
  },
  beforeDestroy () {
    this.$bus.$off('get-root-project')
    this.$bus.$off('show-node-tree', this.showPopNodeTree)

    this.$bus.$socket.emit('leave', this.projectId)
  },
  destroyed () {
  },
  methods: {
    lockNode (node) {
      this.$bus.$socket.emit('lockNode', {
        projectID: node.projectId,
        nodeUUID: node.uuid
      })
    },
    unlockNode (node) {
      this.$bus.$socket.emit('unlockNode', {
        projectID: node.projectId,
        nodeUUID: node.uuid
      })
    },
    toggleFullScreen () {
      this.$store.commit('layout/toggleFullScreen')
    },
    async updateTitle () {
      if (!this.titleChanged) return
      if (this.currentProject && this.currentProject.id) {
        let resData = await project.updateProject(this.currentProject)
        if (resData) {
          this.$Message.success('标题更新成功')
          this.titleChanged = false
        } else {
          this.$Message.error('标题更新失败')
        }
      }
    },
    /** **********************搜索***********************/
    toggleSearch () {
      this.isSearch = !this.isSearch
    },
    debounceInput: _.debounce(function () {
      this.keyWords = this.searchInput
    }, 1000),
    async getSearchList () {
      this.onProjectLoding()
      this.searchList = []
      if (this.keyWords !== '') {
        let data = await getSearchList({
          keywords: this.keyWords,
          projectId: this.rootProjectId
        })
        if (data) {
          this.searchList = data
        } else {
          this.errMessage = '未找到匹配的节点'
          this.errMessageShow = true
        }
      }
      this.offProjectLoading()
    },
    /** ******************搜索结果跳转参数************************/
    jumpTo (target) {
      if (target.projectId !== this.projectId) {
        this.searchTarget = target
        this.$bus.$emit('refresh-project-tree', { projectId: target.projectId })
      }
      this.$nextTick(() => {
        if (this.searchNodeEl) {
          this.searchNodeEl.style.border = 'none'
        }
        this.searchNodeEl = document.querySelector(`#node-title-${target.uuid}`)
        const nodePanelEl = document.querySelector(`#node-${target.id}`)
        const nodeScrollPage = this.$refs.nodePage
        if (nodePanelEl) {
          const scrollY = nodePanelEl.getBoundingClientRect().top - nodeScrollPage.getBoundingClientRect().top
          if (scrollY > nodeScrollPage.scrollTop) {
            nodeScrollPage.scrollTo({
              top: scrollY,
              behavior: 'smooth'
            })
          }
        }
        if (this.searchNodeEl) {
          this.searchNodeEl.style.border = 'solid 2px #ffeb3b'
          this.searchTarget = null
        }
      })
      // console.log(target.uuid, 'miao', this.projectId)
    },
    /** **********************搜索 end ***********************/
    onProjectLoding () {
      this.projectLoading = true
    },
    offProjectLoading () {
      this.projectLoading = false
    },
    // async getComponentList () {
    //   this.onProjectLoding()
    //   this.offProjectLoading()
    // },
    async treeNodeClick (node, e) {
      // 切换项目
      this.projectId = node.id
      this.teamId = node.orgId
      this.currentProject = node
    },
    // ***************************节点操作事件end***********************************
    toolClick (e, fn) {
      fn.call(this, e)
    },
    nodeEditorInited (params) {
      this.nodeManager = params.params.nodeManager
      if (this.searchTarget) {
        this.jumpTo(this.searchTarget)
      }

      this.$bus.$socket.emit('join', this.projectId)
    },
    goNode (node) {
      this.$refs.nodeEditor.handleEnterNode(new EmitModel(
        {
          name: 'go-node',
          event: null,
          vn: this.$vnode,
          params: {
            node: node
          }
        }
      ))
    },
    switchAutoSize () {
      this.autoSize = !this.autoSize
    },
    showPopNodeTree () {
      this.$bus.$emit('pop-helper-update-prop', 'nodeTree', {
        rootId: this.rootProjectId,
        currentId: this.projectId
      })
      this.$bus.$emit('pop-helper-show-menu', 'nodeTree')
    }
  }
}
</script>

