import { getUserInfo, getToken, setUserInfo, setToken, delToken, delUserInfo } from '@/libs/util'
import orgApi from '@/api/org'

// initial state
const state = {
  userInfo: getUserInfo(),
  token: getToken(),
  companyList: [],
  currentCompany: null,
  currentCompanyName: null
}

// getters
const getters = {
  userInfo (state) {
    if (state.userInfo && JSON.stringify(state.userInfo).replace(/\s/g, '') === '{}') {
      return null
    } else {
      return state.userInfo
    }
  },
  token (state) {
    return state.token
  }
}

// actions
const actions = {
  async getCompanyList ({ commit }) {
    const data = await orgApi.getCompanyList()
    if (data) {
      const orgs = {
        companyList: data,
        currentCompany: data.length ? data[0].id : '',
        currentCompanyName: data.length ? data[0].name : ''
      }
      commit('setOrg', orgs)
    }
  }
}

// mutations
const mutations = {
  setUserInfo (state, userInfo) {
    state.userInfo = userInfo
    setUserInfo(userInfo)
  },
  setToken (state, token) {
    state.token = token
    setToken(token)
  },
  clearToken (state) {
    state.token = ''
    delToken()
  },
  clearUserInfo (state) {
    state.userInfo = null
    delUserInfo()
  },
  setOrg (state, { companyList, currentCompany, currentCompanyName }) {
    state.companyList = companyList
    state.currentCompany = currentCompany
    state.currentCompanyName = currentCompanyName
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
