import Request from '@/libs/request'

export default {
  /**
   * 组件-节点组件元素配置信息
   */
  async saveCompontentItemConfigData  (config) {
    const {
      code,
      uuid,
      configData,
      nodeId
    } = config
    try {
      const { data } = await Request.post('/mvp-manager/component/saveCompontentItemConfigData', {
        code,
        uuid,
        configData,
        nodeId
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * type
   * 1 常规组件
   * 2 自定义组件
   * 不传则所有组件
   */
  async getList () {
    try {
      let res = await Request.post('/mvp-manager/component/list', { type: null })
      if (res.status === 200 && res.data.response.code === 0) {
        return res.data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  }
}

