window.componentCache = []
export const getCompontentCache = async (componentId) => {
  let r = window.componentCache.find((item) => {
    return item.id === componentId
  })

  if (!r) {
    r = await getCompontent(componentId)
    window.componentCache.push(r)
  }

  return r
}

export const clearCompontentCache = () => {
  window.componentCache = []
}

export const getCompontent = async (conponentId) => {
  try {
    const { data } = await Request.post('/mvp-manager/component/getComponent?componentId=' + conponentId)
    if (data.response.code === 0) {
      return data.data
    } else {
      return null
    }
  } catch (err) {
    console.log(err)
    return null
  }
}

export const delCompontent = async (val) => {
  try {
    const { data } = await Request.delete('/mvp-manager/component/delete/' + val)
    if (data.response.code === 0) {
      if (data.data) {
        return true
      } else {
        return false
      }
    } else {
      return null
    }
  } catch (err) {
    console.log(err)
    return null
  }
}

export const checkName = async (val) => {
  try {
    const { data } = await Request.post('/mvp-manager/component/check?name=' + val)
    if (data.response.code === 0) {
      if (data.data && data.data.id) {
        return false
      } else {
        return true
      }
    } else {
      return null
    }
  } catch (err) {
    console.log(err)
    return null
  }
}
