<template>
  <div class="pop-tree" v-show="projectId">
    <div class="tree-title">
      <span>当前项目: </span>
      <project-tree
        :style="{
          'min-width': '10rem',
          'max-width': '15rem'
        }"
        :rootId="rootId"
        v-model="projectId"
        size="small">
      </project-tree>
    </div>
    <div class="tree-content" v-if="showTree">
      <node-tree :projectId="projectId" :editable="false"></node-tree>
    </div>
  </div>
</template>

<script>
import nodeTree from '@/components/node-editor'
import projectTree from '@/components/choose-project'

export default {
  showTitle: '节点树',
  components: {
    'node-tree': nodeTree,
    'project-tree': projectTree
  },
  props: {
    rootId: {
      type: [Number, String],
      default: 0
    },
    currentId: {
      type: [Number, String],
      default: 0
    }
  },
  data () {
    return {
      projectId: 0,
      showTree: true
    }
  },
  mounted () {
    this.updateShow()
  },
  methods: {
    updateShow () {
      this.projectId = this.currentId || this.rootId
    }
  },
  watch: {
    rootId () {
      this.updateShow()
    }
  }
}
</script>

