import Request from '@/libs/request'

export function getDocumentList (params) {
  return Request.post('/mvp-manager/commonDocument/list', params).then(resp => resp.data)
}

export function getDocumentDetail (params) {
  return Request.post(`/mvp-manager/commonDocument/detail?id=${params.id}`).then(resp => resp.data)
}

export function saveDocument (params) {
  const url = params.id ? '/mvp-manager/commonDocument/update' : '/mvp-manager/commonDocument/save'
  return Request.put(url, params).then(resp => resp.data)
}

export function deleteDocument (params) {
  return Request.post('/mvp-manager/commonDocument/delete', params).then(resp => resp.data)
}
