<template>
  <div :class="{'show': showModal, 'leave': closing}" class="x-modal" v-global-dom>
    <div class="x-modal-body">
      <div class="x-modal-close" v-if="closable" @click="onCancel">
        <div class="close-icon"></div>
      </div>
      <div class="x-modal-title">
        <slot name="title"></slot>
      </div>
      <div class="x-modal-content">
        <slot/>
      </div>
      <div class="x-modal-foot">
        <slot name="foot">
          <div class="default-btn">
            <button class="x-modal-ok-btn" @click="onOk">确定</button>
          </div>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
import globalDom from './global-dom'
export default {
  name: 'xModal',
  directives: {
    'global-dom': globalDom
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    async: {
      type: Boolean,
      default: false
    },
    closable: {
      type: Boolean,
      default: true
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  data () {
    return {
      showModal: this.value,
      closing: false,
      closeTimer: null
    }
  },
  watch: {
    value (val) {
      console.log('value更新', val)
      if (val) {
        this.showModal = true
      } else {
        this.close()
      }
    }
  },
  methods: {
    onOk () {
      this.$emit('on-ok')
      if (!this.async) {
        this.close()
      }
    },
    onCancel () {
      this.close('on-cancel')
    },
    close (event) {
      if (this.closeTimer) {
        return
      }
      event && this.$emit(event)
      this.$emit('change', false)
      this.closing = true
      this.closeTimer = setTimeout(() => {
        this.closing = false
        this.showModal = false
        clearTimeout(this.closeTimer)
        this.closeTimer = null
      }, 200)
    }
  }
}
</script>

