<template lang="pug">
#app
  router-view
  MessageHandle
</template>

<script>
import MessageHandle from '@/components/message-handler'
export default {
  name: 'app',
  components: {
    MessageHandle
  },
  async created () {
    window.Ialert = this.$Message.error
    await this.initData()
  },
  mounted () {
    document.addEventListener('compositionstart', this.markInputing)
    document.addEventListener('compositionend', this.markInputing)
  },
  beforeDestroy () {
    document.removeEventListener('compositionstart', this.markInputing)
    document.removeEventListener('compositionend', this.markInputing)
  },
  methods: {
    async initData () {
      await Promise.all([this.$store.dispatch('component/init'), this.$store.dispatch('user/getCompanyList')])
    },
    markInputing () {
      this.$bus.inputing = !(this.$bus.inputing)
    }
  }
}
</script>

