import usualApi from './util'
import Request from '@/libs/request'

export default {
  getMessageList (params) {
    return usualApi(Request.post, '/mvp-manager/msg/list', params, [])
  },
  getMessageDetail (params) {
    return usualApi(Request.post, '/mvp-manager/msg/detail', params, {})
  },
  removeMessage (params) {
    return usualApi(Request.post, '/mvp-manager/msg/remove', params, true)
  },
  readMessage (params) {
    return usualApi(Request.post, '/mvp-manager/msg/read', params, true)
  }
}
