// 项目和共用包
import Request from '@/libs/request'
import usualApi from './util'

export default {
  getProjectList (params) {
    return usualApi(Request.post, '/mvp-manager/project/list', params, [])
  },
  getProjectDetail (params) {
    return usualApi(Request.post, '/mvp-manager/project/detail?id=' + params.id, {}, {})
  },
  getRootProjectList (params = {}) { // 获取当前用户能查看所有的项目列表
    return usualApi(Request.post, '/mvp-manager//project/getAllRoots', params, [])
  },
  addProject (params) {
    return usualApi(Request.post, '/mvp-manager/project/save', params, {})
  },
  deleteProject (params) {
    return usualApi(Request.post, '/mvp-manager/project/delete', params, {})
  },
  updateProject (params) {
    return usualApi(Request.post, '/mvp-manager/project/update', params, {})
  },
  getProjectMembers (params) {
    return usualApi(Request.post, '/mvp-manager/project/getUsers', params, [])
  },
  setMemberPermission (params) {
    return usualApi(Request.post, '/mvp-manager/project/setUserType', params, {})
  },
  addProjectMembers (params) {
    return usualApi(Request.post, '/mvp-manager/project/addUsers', params, {})
  },
  removeProjectMember (params) {
    return usualApi(Request.post, '/mvp-manager/project/delUsers', params, {})
  },
  getUserListByKeyword (params) {
    return usualApi(Request.post, '/mvp-manager/project/searchUsers', params, [])
  },
  getProjectReadyTeamList (params) {
    return usualApi(Request.post, '/mvp-manager/project/getOrgs', params, [])
  },
  setOrgInfo (params) {
    return usualApi(Request.post, '/mvp-manager/project/setOrg', params, {})
  },
  setToCompany (params) {
    return usualApi(Request.post, '/mvp-manager/project/setCompany', params)
  }
  // projectDetail (params, id) {
  //   return this.usualApi(Request.get, '/mvp-manager/project/detail' + '/' + id, params)
  // }
}
