<template>
  <Row class-name="node" :class="{'dragging': dragging}" :id="`node-${node.id}`" @drop.native="handleNodeDrop">
    <Row class="node-panel"
      ref="nodeRow"
      :id="`node-title-${node.uuid}`"
      :class="{'focus': focusNode === node.code, 'dragover': dragPosition}"
      :data-id="node.uuid"
      @mouseover.native.stop="handleMouseOver($event)"
      @mouseout.native.stop="handleMouseOut($event)"
      @dragenter.native="handleDragEnter(node, $event)"
      @dragover.native="handleDragOver(true, $event)"
      @dragleave.native="handleDragLeave"
      >
      <div class="drop-indicator-wrap" v-if="dragPosition">
        <div
          class="drop-indicator prev"
          v-if="+node.type === 1 || ($nodeBus.draggingNode && $nodeBus.draggingNode.type === 2)"
          :class="{'highlight': highlightIndicator === 'prev'}"
          @drop="handleDrop(node, 'prev', $event)"
          @dragover="highlightIndicator = 'prev'"
          >
          拖到{{node.code}}节点之前
        </div>
      </div>
      <div class="node-body">
        <div class="collapse" v-if="!node.crossState && ((node.children && node.children.length) || node.nestProjectId) && (!node.expanded || showControl)" @click.stop="toggleCollapse">
          <Icon type="md-add" v-if="!node.expanded"></Icon>
          <Icon type="md-remove" v-else></Icon>
        </div>
        <div class="bullet"
          :draggable="editable && canEdit"
          @dragstart="handleDragStart(node, $event)"
          @dragend="handleDragEnd"
          @click="handleEnterNode(node, $event)">
          <span class="dot"></span>
        </div>
        <div class="node-content">
          <span class="node-info">
            <Icon v-if="!node.nestProjectId && node.type === 1 && componentName.uuid === COMPONENT_KEYS.radioSelect" type="ios-radio-button-on" @mouseover.native.stop="showPop(componentName.title, $event)" />
            <Icon v-else-if="!node.nestProjectId && node.type === 1 && componentName.uuid === COMPONENT_KEYS.multiSelect" type="ios-checkbox-outline" @mouseover.native.stop="showPop(componentName.title, $event)" />
            <Icon v-else-if="!node.nestProjectId && node.type === 1 && componentName.uuid === COMPONENT_KEYS.singleBlank" type="ios-create-outline" @mouseover.native.stop="showPop(componentName.title, $event)" />
            <Icon v-else-if="!node.nestProjectId && node.type === 1 && componentName.uuid === COMPONENT_KEYS.multiInput" type="ios-grid-outline" @mouseover.native.stop="showPop(componentName.title, $event)" />
            <Icon v-else-if="!node.nestProjectId && node.type === 1 && componentName.uuid === COMPONENT_KEYS.integerInput" type="md-bowtie" @mouseover.native.stop="showPop(componentName.title, $event)" />
            <Icon v-else-if="!node.nestProjectId && node.type === 1 && componentName.uuid === COMPONENT_KEYS.judge" type="md-checkmark" @mouseover.native.stop="showPop(componentName.title, $event)" />
            <Icon v-else-if="node.crossState" type="md-cube" @mouseover.native.stop="showPop('跨项目共用包', $event)" />
            <Icon v-else-if="node.nestProjectId" type="md-cube" @mouseover.native.stop="showPop('共用包', $event)" />
            <Icon v-else-if="node.type === 1" type="logo-codepen"/>
            <!-- <span v-else style="display: inline-block; width: 16px; height: 16px;"></span> -->
          </span>
          <span class="node-code" v-click-copy="`已复制'${node.code}'到剪贴板`">{{node.code}}</span>
          <span class="node-title" :class="{'question': +node.type === 1, 'answer': +node.type === 2, 'append': node.additional, 'node-package': node.nestProjectId}" @click.stop="handleClickEdit">{{node.title}}</span>
          <span class="node-package" v-if="+node.type === 1 && node.crossState">跨项目共用包</span>
          <span class="node-package" v-else-if="+node.type === 1 && node.nestProjectId">共用包</span>
          <!-- <span class="node-showCondition" v-if="node.showCondition" v-for="(item, index) in parseShowCondition" :key="index">𝐓: {{item}}</span> -->
          <span class="node-showCondition" v-if="checkCode(node.showCondition)" @mouseenter.stop="getTranslation">𝐒</span>
          <span class="node-showCondition" v-if="checkCode(node.translationCondition)" @mouseenter.stop="getTranslation">𝐓</span>
        </div>
        <div class="node-buttons" v-if="editable && canEdit && !node.lock">
          <!-- <span class="edit-node" @click.stop="handleClickEdit" v-if="+node.type === 1">
            <Tooltip content="编辑">
              <Icon type="edit"></Icon>
            </Tooltip>
          </span> -->
          <span class="add-next-node" @click.stop="handleClickNext" v-if="+node.type === 1">
            <!-- <Tooltip content="添加同级问题">
              <Icon type="md-add-circle"></Icon>
            </Tooltip> -->
            <Icon type="md-add-circle" @mouseover.native.stop="showPop('添加同级问题', $event)"></Icon>
          </span>
          <span class="add-child-node" @click.stop="handleClickAddChild" v-if="!node.additional">
            <!-- <Tooltip content="添加子问题">
              <Icon type="md-add"></Icon>
            </Tooltip> -->
            <Icon type="md-add" @mouseover.native.stop="showPop('添加子级问题', $event)"></Icon>
          </span>
          <span class="delete-node" @click.stop="handleClickDelete">
            <!-- <Tooltip content="删除">
              <Icon type="md-trash"></Icon>
            </Tooltip> -->
            <Icon type="md-trash" @mouseover.native.stop="showPop('删除', $event)"></Icon>
          </span>
          <span class="link-project" @click.stop="handleClickMove" v-if="+node.type === 1">
            <!-- <Tooltip
              content="移动到共用包或项目"
              :transfer="true">
              <Icon type="md-link"></Icon>
            </Tooltip> -->
            <Icon type="md-link" @mouseover.native.stop="showPop('移动到共用包或项目', $event)"></Icon>
          </span>
        </div>
      </div>
      <div class="drop-indicator-wrap" v-if="dragPosition">
        <div
          class="drop-indicator next"
          v-if="+node.type === 1 || ($nodeBus.draggingNode && $nodeBus.draggingNode.type === 2)"
          :class="{'highlight': highlightIndicator === 'next'}"
          @drop="handleDrop(node, 'next', $event)"
          @dragover="highlightIndicator = 'next'"
          >
          拖到{{node.code}}节点之后
        </div>
        <div
          v-if="!node.additional && !($nodeBus.draggingNode && $nodeBus.draggingNode.type === 2)"
          type="warning"
          class="drop-indicator child"
          :class="{'highlight': highlightIndicator === 'child'}"
          @drop="handleDrop(node, 'child', $event)"
          @dragover="highlightIndicator = 'child'">
          拖到{{node.code}}节点的子节点
        </div>
      </div>
    </Row>
    <Row
      class="node-children"
      :class="{'collapse': !node.expanded, 'highlight': rootInstance.highlightBackground === node.uuid}">
      <div class="left-line" @click="rootInstance.highlightBackground = rootInstance.highlightBackground === node.uuid ? '' : node.uuid"></div>
      <template v-if="node.nestProjectId && node.commonNodes" v-for="(child, index) in node.commonNodes">
        <NodeItem :canEdit="false" :key="index" :node="child" :focusNode="focusNode" :canDrop="false"/>
      </template>
      <template v-for="(child, index) in node.children">
        <NodeItem v-if="!(node.nestProjectId && +child.type === 2)" :canEdit="!node.lock && canEdit" :key="index" :node="child" :focusNode="focusNode" :canDrop="dropable && !node.lock && canEdit"/>
      </template>
    </Row>
  </Row>
</template>

<script>
import { EmitModel } from '@/libs/EmitModel'
import components, { COMPONENT_KEY } from '../../components/list'
import { getNodeConditionInfo } from '@/api/node'
export default {
  name: 'NodeItem',
  props: {
    node: {
      type: Object,
      required: true
    },
    focusNode: {
      type: Number | String,
      required: false,
      default: -1
    },
    canDrop: {
      type: Boolean,
      required: false,
      default: true
    },
    canEdit: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  inject: ['rootInstance', 'rootNodes', '$nodeBus', 'editable'],
  data () {
    return {
      showControl: false,
      dragPosition: '',
      drop: true,
      dragging: false,
      highlightIndicator: '',
      COMPONENT_KEYS: COMPONENT_KEY
    }
  },
  computed: {
    dropable () {
      return this.canDrop ? this.drop : this.canDrop
    },
    parseShowCondition () {
      if (this.node.showCondition) {
        const showCondition = this.node.showCondition
        const reg = /echo +.*;/g
        const matches = showCondition.match(reg)
        const titles = matches ? matches.map(item => {
          const tmp = item.replace('echo ', '').replace(';', '')
          return tmp.substr(1, tmp.length - 2)
        }) : []
        return titles
      } else {
        return []
      }
    },
    componentList () {
      return this.$store.state.component.list
    },
    componentName () {
      const componentId = this.node.componentId
      const item = this.$store.state.component.list.find(it => it.id === componentId)
      let com = null
      if (item) {
        com = components[item.uuid]
      }
      if (com) {
        return {
          uuid: item.uuid,
          title: com.title
        }
      } else {
        return {}
      }
    }
  },
  created () {
    this.clearEventListener()
    this.initEvent()
  },
  beforeDestroy () {
    this.clearEventListener()
  },
  watch: {
    focusNode (e) {
      if ((this.focusNode === this.node.code) && !this.node.expended) {
        this.toggleCollapse(true)
        this.rootInstance.$emit('current-focus', new EmitModel({
          event: e,
          vd: this.$vnode,
          params: {
            node: this.node
          }
        }))
      }
    }
    // 'node.expanded' () {
    //   if (this.node.nestProjectId && this.node.expanded) {
    //     this.rootInstance.nodeManager.loadCommonNodes(this.node)
    //   }
    // }
  },
  methods: {
    checkCode (code) {
      if (code) {
        return code.replace(/<\?/g, '').replace(/\?>/g, '').replace(/\s/g, '') !== ''
      } else {
        return false
      }
    },
    initEvent () {
      this.$nodeBus.$on('node-drag-end', () => {
        this.dragging = false
        this.drop = true
        this.$nodeBus.dragging = false
        this.$nodeBus.draggingNode = null
        this.dragPosition = false
      })
    },
    clearEventListener () {
      this.$nodeBus.$off('node-drag-success')
    },
    handleMouseOut (e) {
      this.showControl = false
    },
    handleMouseOver (e) {
      this.showControl = true
    },
    handleEnterNode (node, e) {
      // if (this.node.type !== 1) {
      //   return
      // }
      if (this.node.projectId !== this.rootInstance.projectId) {
        this.$bus.$emit('refresh-project-tree', { projectId: this.node.projectId, type: 2 })
        return
      }
      this.$nodeBus.$emit('enter-node', new EmitModel({
        event: e,
        vd: this.$vnode,
        params: {
          node: node
        }
      }))
    },
    handleClickEdit (e) {
      if (!this.editable || !this.canEdit || this.node.lock) {
        if (this.node.projectId !== this.rootInstance.projectId) {
          this.$bus.$emit('refresh-project-tree', { projectId: this.node.projectId, type: 2 })
        }
        return
      }
      this.$nodeBus.$emit('edit-node', new EmitModel({
      // this.rootInstance.$emit('edit-node', new EmitModel({
        name: 'edit-node',
        event: e,
        vn: this.$vnode,
        params: {
          node: this.node
        }
      }))
    },
    handleClickAddChild (e) {
      this.$nodeBus.$emit('add-child-node', new EmitModel({
      // this.rootInstance.$emit('add-child-node', new EmitModel({
        name: 'add-child-node',
        event: e,
        vn: this.$vnode,
        params: {
          node: this.node
        }
      }))
    },
    handleClickNext (e) {
      this.$nodeBus.$emit('add-next-node', new EmitModel({
      // this.rootInstance.$emit('add-next-node', new EmitModel({
        name: 'add-next-node',
        event: e,
        vn: this.$vnode,
        params: {
          node: this.node
        }
      }))
    },
    handleClickDelete (e) {
      this.$nodeBus.$emit('delete-node', new EmitModel({
      // this.rootInstance.$emit('delete-node', new EmitModel({
        name: 'delete-node',
        event: e,
        vn: this.$vnode,
        params: {
          node: this.node
        }
      }))
    },
    handleClickMove (e) {
      this.$bus.$emit('link-project', {
        name: 'link-project',
        event: e,
        vn: this.$vnode,
        params: {
          node: this.node
        }
      })
    },
    handleDragStart (node, e) {
      e.dataTransfer.effectAllowed = 'move'
      this.dragging = true
      this.drop = false
      this.$nodeBus.dragging = true
      this.$nodeBus.draggingNode = node
      this.$nodeBus.$emit('drag-start', new EmitModel({
        name: 'drag-start',
        event: e,
        vn: this.$vnode,
        params: {
          node: node
        }
      }))
      e.dataTransfer.setData('dragData', this.node.id)
    },
    handleDragEnter (node, e) {
      // if (!this.canDrop || !this.drop) {
      //   return
      // }
    },
    handleDragOver (pos, e) {
      if ((!this.canDrop || !this.drop) || !this.$nodeBus.dragging) {
        return
      }
      if (this.$nodeBus.draggingNode.type === 2 && this.$nodeBus.draggingNode.parentUuid !== this.node.parentUuid) {
        return
      }
      e.preventDefault()
      this.dragPosition = pos
    },
    handleDragLeave (e) {
      if (e.target.dataset.id && e.target.dataset.id === this.node.uuid) {
        this.dragPosition = ''
        this.highlightIndicator = ''
      }
    },
    handleDrop (node, type, e) {
      if (!this.$nodeBus.dragging) {
        return
      }
      this.dragPosition = ''
      this.$nodeBus.$emit('node-drop', new EmitModel({
        name: 'node-drop',
        event: e,
        vn: this.$vnode,
        params: {
          target: node,
          type: type
        }
      }))
    },
    handleDragEnd (e) {
      console.log('拖动结束', this.node.title)
      this.dragging = false
      this.drop = true
      this.$nodeBus.dragging = false
      this.$nodeBus.draggingNode = null
      this.dragPosition = false
      this.$nodeBus.$emit('node-drag-end')
    },
    handleNodeDrop () {
      this.$nodeBus.$emit('node-drag-end')
    },
    showPop (msg, e) {
      this.$poptip.show({
        content: msg
      }, e)
    },
    getTranslation (e) {
      if ((this.node.showCondition && this.node.showConditionDesc) || (this.node.translationCondition && this.node.translateDesc)) {
        if (this.node.translateDesc) {
          this.showPop(this.node.translateDesc, e)
        } else {
          this.showPop(this.node.showConditionDesc, e)
        }
        return
      }
      getNodeConditionInfo({ id: this.node.id }).then((data) => {
        if (data) {
          this.node.showConditionDesc = data.showCondition
          this.node.translateDesc = data.translateCondition
          if (this.node.translateDesc) {
            this.showPop(this.node.translateDesc, e)
          } else {
            this.showPop(this.node.showConditionDesc, e)
          }
        }
      })
    },
    async toggleCollapse (e) {
      const updated = this.rootInstance.nodeManager.toggleExpandNode([this.node], typeof e === 'boolean' ? e : !this.node.expanded)
      if (this.node.nestProjectId && this.node.expanded && (!this.node.commonNodes || !this.node.commonNodes.length)) {
        await this.rootInstance.nodeManager.loadCommonNodes(this.node)
      }
      this.$forceUpdate()
      this.$nodeBus.$emit('toggle-collapse', new EmitModel({
        name: 'toggle-collapse',
        event: e,
        vn: this.$vnode,
        params: {
          status: this.node.expanded ? 1 : 0,
          nodeIds: updated.map(node => node.id)
        }
      }))
    }
  }
}
</script>

<style lang="stylus" scoped>
.node
  white-space nowrap
  transition background-color .2s ease-in-out, border-radius .2s ease-in-out
  min-width 572px
  &.highlight
    background-color rgba(0,0,0,0.1)
  &.dragging
    border-radius 0.3rem
    background-color rgba(0,0,0,0.1)
  .node-panel
    border-radius 0.2rem
    padding-left 1rem
    padding-top 0.1rem
    padding-bottom 0.1rem
    &.focus
      background-color #f5e9818c
    &:hover
      .node-body
        background-color rgba(0,0,0,0.1)
      .node-body
        .node-buttons
          opacity 1!important
    &.dragover
      .node-body
        background-color rgba(243,145,87,0.2)
    .node-body
      display flex
      width fit-content
      border-radius 0.3rem
      align-items center
      flex-wrap nowrap
      .collapse
        color rgba(0,0,0,0.6)
        position absolute
        left 0.1rem
        cursor pointer
        font-weight bold
      .bullet
        color rgba(0,0,0,0.5)
        cursor pointer
        height 1rem
        width 1rem
        padding 0.25rem
        border-radius 50%
        transition background-color .2s ease-in-out
        &:hover
          background-color rgba(0,0,0,0.2)
          .dot
            background-color rgba(0,0,0,0.5)
        .dot
          display block
          width 0.5rem
          height 0.5rem
          border-radius 50%
          background-color rgba(0,0,0,0.3)
      .node-content
        margin-left 0.2rem
        padding 0 0.1rem
        color #fff
        font-family sans-serif
        // flex 1 1 auto
        >span
          padding 0 0.2rem
          border 1px solid rgba(0,0,0,0.1)
          border-radius 4px
          margin-left 0.3rem
          &.node-info
            border none
            border-radius 0
            color #ff9640
            margin-left 0
          &.node-code
            user-select all
            cursor pointer
            color #212121
          &.node-package
            background-color #ff0000
          &.node-title
            cursor pointer
            &.question
              // background-color rgba(255,153,0,0.2)
              background-color #ff7400
            &.answer
              // background-color rgba(45,140,240,0.2)
              background-color #009999
            &.append
              background-color #57a3f3
            &.node-package
              // background-color rgba(255,87,34, 0.2)
              background-color #ff0000
          &.node-showCondition
            // background-color rgba(0, 188, 212, 0.52)
            background-color #00cc00
      .node-buttons
        opacity 0
        padding 0 0.1rem
        >span
          padding 0 0.2rem
          margin-left 0.3rem
          border 1px solid rgba(0,0,0,0.1)
          border-radius 4px
          cursor pointer
          &.delete-node
            color rgb(237,60,23)
            border 1px solid rgba(237,60,23,0.2)
          &:hover
            box-shadow 0 0 4px 2px rgba(45,140,240,0.2)
            &.delete-node
              box-shadow 0 0 4px 2px rgba(237,60,23,0.2)
          &:active
            box-shadow none

  .node-children
    margin-left 1.5rem
    // border-left 1px solid rgba(0,0,0,0.2)
    .left-line
      width 1px
      height 100%
      background-color #ccc
      position absolute
      left 0
      top 0
      cursor pointer
      z-index 2
      &:hover
        background-color #666
        width 2px
    &.highlight
      background-color rgba(0,0,0,0.1)
      >.left-line
        background-color #666
        width 2px
    &.collapse
      display none

  .drop-indicator-wrap
    min-height 0.2rem
    display: flex;
    flex-direction: row;
    padding 0.1rem 0.2rem 0.1rem 1rem
    justify-content: flex-start;
    flex-wrap: nowrap;
    .drop-indicator
      padding 0.4rem
      border 1px dotted rgba(0,0,0,0.2)
      border-radius 0.2rem
      color #495060
      font-size 0.6rem
      &.highlight
        font-weight bold
        color #ff9800
        border-color #ff9800
      &.next

      &.child
        margin-left 0.2rem
</style>
