import Request from '@/libs/request'

export function saveAccountInfo (params) {
  return Request.post('/mvp-manager/user/edit', params).then(resp => resp.data)
}

export function savePhone (params) {
  return Request.post('/mvp-manager/user/editPhone', params).then(resp => resp.data)
}

export function saveEmail (params) {
  return Request.post('/mvp-manager/user/editEmail', params).then(resp => resp.data)
}
