import Vue from 'vue'
import message from '@/api/message'

const WEEK_TIME = 7 * 24 * 3600 * 1000

let loading = false

const sortMessage = function (list) {
  list.sort((a, b) => {
    if (a.msgCreateTime > b.msgCreateTime) return -1
    if (a.msgCreateTime === b.msgCreateTime) return 0
    return 1
  })
  for (let index = list.length - 1; index > 0; index--) {
    if (list[index].msgId === list[index - 1].msgId) {
      list.splice(index, 1)
    }
  }
}

const state = {
  messageList: [], // previousTime~newTime之间的消息记录,前闭后开[)
  newTime: 0, // 获取最新消息记录的时间
  previousTime: 0// 获取最旧消息记录的时间
}

const actions = {
  async refreshNewRecord (context) {
    if (loading) {
      Vue.prototype.$Message.info('正在加载消息，请稍候')
      return
    }
    if (context.state.previousTime && !context.state.newTime) return
    let endTime = new Date().getTime()
    let startTime = context.state.newTime || endTime - WEEK_TIME
    loading = true
    let list = await message.getMessageList({
      'msgCreateTime_Start': String(startTime),
      'msgCreateTime_End': String(endTime)
    })
    loading = false
    context.commit('refreshNewRecord', { list, endTime })
  },
  async refreshPreviousRecord (context) {
    if (loading) {
      Vue.prototype.$Message.info('正在加载消息，请稍候')
      return
    }
    if (context.state.newTime && !context.state.previousTime) return
    let endTime = context.state.previousTime || new Date().getTime()
    let startTime = endTime - WEEK_TIME
    loading = true
    let list = await message.getMessageList({
      'msgCreateTime_Start': String(startTime),
      'msgCreateTime_End': String(endTime)
    })
    loading = false
    context.commit('refreshPreviousRecord', { list, startTime })
  },
  async removeMessage (context, id) {
    let res = await message.remmoveMessage({ id: id })
    if (res) {
      context.commit('removeMessage', id)
      Vue.prototype.$Message.success('刪除成功')
    } else {
      Vue.prototype.$Message.success('刪除失败')
    }
  },
  async readMessage (context, ids) {
    let res = await message.readMessage(ids)
    if (res) {
      context.commit('readMessage', ids)
    }
  }
}

const mutations = {
  refreshNewRecord (state, data) {
    let list = data.list
    let newTime = data.endTime
    if (list) {
      state.messageList.unshift(...list)
      if (!state.newTime && !state.previousTime) { // 第一次请求
        state.previousTime = newTime - WEEK_TIME
      }
      state.newTime = newTime
      sortMessage(state.messageList)
    }
  },
  refreshPreviousRecord (state, data) {
    let list = data.list
    let previousTime = data.startTime
    if (list) {
      state.messageList.push(...list)
      if (!state.newTime && !state.previousTime) { // 第一次请求
        state.newTime = previousTime + WEEK_TIME
      }
      state.previousTime = previousTime
      sortMessage(state.messageList)
    }
  },
  removeMessage (state, id) {
    let idx = state.messageList.findIndex(item => String(item.id) === String(id))
    if (idx !== -1) {
      state.messageList.splice(idx, 1)
    }
  },
  readMessage (state, ids) {
    for (let message of state.messageList) {
      message.msgStatus = 1
    }
  }
}

export default {
  namespaced: true,
  actions,
  mutations,
  state
}
