<template>
  <div class="x-select">
    <div class="select-bar" @click="showSelectModal = true">
      {{selected.length ? selected.map(item => item.label).join('/') : ''}}
      <span v-if="!selected.length" class="placeholder">{{placeholder}}</span>
    </div>

    <xModal class="x-selece-modal" v-model="showSelectModal">
      <div class="select-title" slot="title">{{title}}</div>
      <div class="select-body">
        <div class="data-box">
          <!-- 第一层 -->
          <ul class="option-list">
            <li class="option-item" v-for="(item, index) in optionChildren.default" :class="{'selected': hasSelected(item.value)}" :key="index" @click="select(0, item)">
              <span class="option-text">{{item.label}}</span>
              <img class="item-loading-img" v-if="item.loading" src="./loading.png">
            </li>
          </ul>
          <!-- 子集 -->
          <ul class="option-list" v-for="(children, key) in childrenLists" :key="key">
            <li class="option-item" v-for="(item, index) in children" :class="{'selected': hasSelected(item.value)}" :key="index" @click="select(key + 1, item)">
              <span class="option-text">{{item.label}}</span>
              <img class="item-loading-img" v-if="item.loading" src="./loading.png">
            </li>
          </ul>
        </div>
      </div>
      <div class="select-footer" slot="foot">
        <button class="confirm-btn" @click="onOk">确定</button>
        <button class="cancel-btn" @click="onCancel">返回</button>
      </div>
    </xModal>
  </div>
</template>

<script>
import xModal from '../xModal/xModal'
export default {
  components: {
    xModal
  },
  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    title: {
      type: [String, Number],
      default: ''
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    data: {
      type: [Object, Array],
      default: () => []
    },
    'load-data': {
      type: Function
    }
  },
  computed: {
    childrenLists () { // 获取需要显示的子集列表
      const list = []
      this.selected.forEach(item => {
        const children = this.optionChildren[item.value]
        if (children && children.length) {
          list.push(children)
        }
      })
      return list
    }
  },
  created () {
    this.optionChildren.default = this.data // 将第一级设置为default
    console.log(typeof this.fetchData)
  },
  data () {
    return {
      showSelectModal: false,
      // 存放选项列表，第一层选项key为'default'，其它子集key为父节点id
      optionChildren: {},
      selected: []
    }
  },
  methods: {
    onOk (e) {
      this.showSelectModal = false
    },
    onCancel (e) {
      // this.select(0)
      this.showSelectModal = false
    },
    select (index, item) {
      console.log('选择第' + index + '层')
      this.selected.splice(index)

      if (item) {
        this.selected.push(item)
        if (item.children && item.children.length) {
          this.$set(this.optionChildren, item.value, item.children)
        } else if (item.children) {
          this.$set(item, 'loading', true)
          this.fetchData(item)
        }
      }

      this.$emit('on-change', this.selected.map(item => item.value), [...this.selected])
    },
    fetchData (item) {
      if (typeof this.loadData === 'function') {
        this.loadData(item, () => {
          this.$set(this.optionChildren, item.value, item.children)
          this.$set(item, 'loading', false)
        })
      }
    },
    hasSelected (value) {
      for (const item of this.selected) {
        if (item.value === value) {
          return true
        }
      }
      return false
    }
  }
}
</script>

