import Request from '@/libs/request'

// 保存法律门类
export function saveCategory (params) {
  const url = params.id ? '/mvp-manager/lawCategory/updateLawCategory' : '/mvp-manager/lawCategory/saveLawCategory'
  return Request.post(url, params).then(resp => resp.data)
}

// 获取法律门类列表
export function getCategoryList (params) {
  return Request.post('/mvp-manager/lawCategory/listLawCategory', params).then(resp => resp.data)
}

// 删除法律门类
export function deleteCategory (params) {
  return Request.post('/mvp-manager/lawCategory/removeLawCategory?id=' + params.id).then(resp => resp.data)
}

