// 保存法条
export function saveLawItem (params) {
  const url = params.id ? '/mvp-manager/lawArticles/updateLawArticle' : '/mvp-manager/lawArticles/saveLawArticles'
  return Request.post(url, params).then(resp => resp.data)
}

// 获取法条列表
export function getLawItemList (params) {
  const lawId = params.lawId
  const parentId = params.parentId === -1 ? null : params.parentId
  const url = parentId ? `/mvp-manager/lawArticles/listLawArticles?lawId=${lawId}&parentId=${parentId}` : `/mvp-manager/lawArticles/listLawArticles?lawId=${lawId}`
  return Request.post(url).then(({ data }) => {
    if (data.response.success) {
      data.data = data.data || []
      data.data.forEach(item => {
        item.tagInfo = item.tagInfo ? item.tagInfo : []
      })
    }
    return data
  })
}

// 获取法条详细信息
export function getLawItemDetail (params) {
  return Request.post('/mvp-manager/lawArticles/detailLawArticles?id=' + params.id).then(resp => resp.data)
}

// 删除法条
export function deleteLawItem (params) {
  return Request.post('/mvp-manager/lawArticles/deleteLawArticles?id=' + params.id).then(resp => resp.data)
}
