<template lang="pug">
.report-tree-page
  .report-menu
    .return-project
      .project-name(@click="pushToPage") {{ projectInfo.name }}
      | 的报告
    .show-all
      Select(v-model="showType", size="small", style="width:150px")
        Option(:value="0") 显示当前项目报告
        Option(:value="1") 显示根项目报告
    .check-report-test
      checkReport.check-report-button(:projectId="projectId", :projectRootId="rootId")
  .report-content
    div(:class="{\
    'layout-left': true,\
    'layout-hide': !(showLayout & 1),\
    'layout-full-screen': !(showLayout & 2)\
    }")
      .layout-show(@click.stop='showLayout=showLayout^1')
        Icon.show-Icon.icon-button(type='ios-arrow-back', title='隐藏报告树和章节树', v-if='showLayout & 1')
        Icon.show-icon(title='展开报告树和章节树', type='ios-arrow-forward', v-else='')
      // 节点树、报告树
      .report(v-show='showLayout & 1')
        .layout-title 报告树
        Tree.tree(@tree-drop="handleDrop('report', arguments)", :allowDrag="allowDrag", :allowDrop="allowDrop", :treeData="reportData", v-if='this.reportData.length', @content-click="handleContentClick('report', arguments)", :draggable="true", :indentLine="true", :indentLimit="40")
          .node-content(slot-scope="{parentData, data}", :class="{choose: currentReport === data.id}", @dblclick.stop="tryUpdateNode('report', data)")
            .node-code(v-click-copy='', :class="data.conditionCheckState ? 'node-name-tested' : 'node-name-untested'") {{data.code}}
            .node-name(:title="data.title") {{data.title}}
            .node-divide
            .node-menu-icon
              Icon(type="md-add-circle", title="添加同级报告", @click.stop="tryAddBrother('report', data, parentData)")
              Icon(type="md-add", title="添加子级报告", @click.stop="tryAddChild('report', data)")
              Icon(type="md-trash", title="删除报告", @click.stop="tryDeleteNode(parentData, data, 'report')")
        .add-root-button(v-else='')
          Button.root-button(@click="tryAddRootNode('report')")
            | 添加主报告
        .report-condition(v-if="currentReportData")
          .mask
          .condition-title
            | 当前报告条件(只读)
          .show-condition-editor(v-if="currentReportData")
            Editor(:projectId='rootId', :currentProject='projectId', v-model='currentReportData.condition', :sourceType="testType.REPORT_CODE_CONDITION", :maxHeight="200", :customOptions="{ readOnly: true }")
          Button.show-condition-button(v-else="")
      .chapter(v-show='showLayout & 1')
        .layout-title 章节树
        Tree.tree(@tree-drop="handleDrop('chapter', arguments)", :treeData="chapterData", v-if='this.chapterData.length', @content-click="handleContentClick('chapter', arguments)", :draggable="true", :indentLine="true", :indentLimit="40")
          .node-content(slot-scope="{parentData, data}", :class="{choose: currentChapter === data.id}", @dblclick.stop="tryUpdateNode('chapter', data)")
            .node-code(v-click-copy='', :class="data.conditionCheckState ? 'node-name-tested' : 'node-name-untested'") {{data.code}}
            .node-name(:title="data.title") {{data.title}}
            .node-divide
            .node-menu-icon
              Icon(type="md-add-circle", title="添加同级章节", @click.stop="tryAddBrother('chapter', data, parentData)")
              Icon(type="md-add", title="添加子级章节", @click.stop="tryAddChild('chapter', data)")
              Icon(type="md-trash", title="删除章节", @click.stop="tryDeleteNode(parentData, data, 'chapter')")
        .add-root-button(v-else-if='this.currentReport')
          Button.root-button(@click="tryAddRootNode('chapter')")
            | 添加章节
        .chapter-condition(v-if="currentChapterData")
          .mask
          .condition-title
            | 当前章节条件(只读)
          .show-condition-editor(v-if="currentReportData")
          Editor(:projectId='rootId', :currentProject='projectId', v-model='currentChapterData.condition', :sourceType="testType.REPORT_OUTLINE_CODE_CONDITION", :maxHeight="200", )
    div(:class="{\
    'layout-right': true,\
    'layout-hide': !(showLayout & 2),\
    'layout-full-screen': !(showLayout & 1)\
    }")
      .layout-show(@click.stop='showLayout=showLayout^2')
        Icon.show-Icon.icon-button(type='ios-arrow-forward', title='隐藏代码块', v-if='showLayout & 2')
        Icon.show-icon(title='展开代码块', type='ios-arrow-back', v-else='')
      .layout-title(v-show='showLayout & 2') 代码区
        .test-all(v-if="codeList.length")
          div(:class="testPass ? 'test-pass' : 'test-not-pass'", :title="testPass ? '所有代码块已测试通过' : '代码未完全通过测试，未通过测试的代码块将不会生效'")
          .test-button(@click.stop="openTestBord(codeList.length-1)") 测试所有代码块
      .code-list(v-show='showLayout & 2')
        template(v-if='currentChapter')
          .editor(v-for='(item, idx) in codeList', @keydown.capture='codeCardKeyDown($event, item, idx)', :key='idx', :id="'code' + idx")
            //
              <div>条件</div>
              <Editor v-model="item.condition">
              </Editor>
            .code-name
              span.code-checked(title='该代码块已通过测试', v-if='item.checkState')
              span.code-unchecked(title='该代码块未通过测试，将不会生效', v-else='')
              span.code-name-title
                | 编号:
              span(title='点击可复制', v-click-copy='')
                | {{item.code}}
            Input(type='textarea', :rows='5', v-model='item.note', @on-change='recordDirty(item.id, false)', placeholder='逻辑说明')
            Editor.code-editor(:sourceType="testType.REPORT_BLOCK_CODE" :class="errorCodeIDx === idx ? 'error' : ''", :error="errorCodeIDx === idx" ref='editor', :fillAll='true', :projectId='rootId', :currentProject='projectId', v-model='item.content', :updateCodeTest='false', @input='recordDirty(item.id, false)')
            .btns
              Button.button(@click='addCodeCard(item)', :title="'alt+shift+enter'")
                Icon.icon-button(type='md-add')
              Button.button(@click='tryDeleteCode(item)', :title="'ctrl+D'")
                Icon.icon-button(type="md-remove")
              //- Button.button(@click='openTestBord(idx)', :title="'ctrl+enter'")
              //-   | 测试
              Button.button(@click='openTestBord(idx)', :title="'ctrl+S'", :type="dirtyCode.includes(item.id) ? 'warning' : 'default'")
                | 保存
          Button.root-button(@click='addCodeCard(null)', v-if='codeList.length === 0 && this.currentChapter')
            Icon.icon-button(type='md-add')
  Modal(v-modal-drag='', v-modal-resize='', :title='editingNode.title', v-model='editingNode.show', :scrollable='true', :mask-closable='false', width='60', center='', :transfer="true")
    .dialog-header(slot='header')
      | {{ editingNode.title }}
    .dialog-content
      .dialog-title
        .report-title-set
          div 标题
          Input.dialog-right(@on-change='editingNode.changed=true', v-model='editingNode.data.title', @on-enter='editingDone', ref="focusInput")
        .report-project-set(v-if='editingNode.showProjectList')
          div 所属项目
          chooseProject(:style="{ width: '10rem' }", v-model='editingNode.data.projectId', :rootId='rootId', :type="1")
      .dialog-condition
        .dialog-editor
          .editor-title
            span {{ (editingNode.type === 'report' ? '报告' : '章节') + '条件' }}
          Editor(v-if='editingNode.show', :projectId='rootId', :currentProject='projectId', :fillAll='true', @input='editingNode.changed=true', v-model='editingNode.data.condition', :sourceType="editingNode.type === 'report' ? testType.REPORT_CODE_CONDITION : testType.REPORT_OUTLINE_CODE_CONDITION", ref="codeEditor")
    span(slot='footer')
      Button(@click='editingCancle') 取 消
      Button(type='primary', @click='editingDone') 确 定
</template>

<script>
import report from '@/api/report'
import project from '@/api/project'
import schemaComponent from '@components/schemaComponent'
import Editor from '@/components/editor'
import Tree from '@/components/simple-tree'
import chooseProject from '@/components/choose-project'
import { TEST_TYPE } from '@/libs/defines'
import checkReport from '@/components/report-check'

export default {
  name: 'report-tree-page',
  components: {
    schemaComponent,
    Editor,
    Tree,
    chooseProject,
    checkReport
  },
  data () {
    return {
      reportData: [], // 报告树
      chapterData: [], // 章节
      codeList: [], // 代码块
      currentReport: 0,
      currentChapter: 0,
      turnToReport: 0,
      turnToChapter: 0,
      turnToCode: 0,
      testCodeIdx: -1, // 测试代码块ID
      dirtyCode: [],
      editingNode: {
        show: false,
        showProjectList: false,
        title: '',
        data: {
          condition: '',
          title: '',
          projectId: '',
          conditionCheckState: false
        },
        callback: {}
      },
      errorCodeIDx: -1,
      showLayout: 1 | 2, // &1-显示报告章节树 &2-显示代码区
      timerIndex: 0,
      detaCheckTime: 5 * 1000,
      showReportCondition: true,
      testType: TEST_TYPE,
      projectInfo: {},
      showType: 0
    }
  },
  watch: {
    testList () {
      this.$bus.$emit('pop-helper-update-prop', 'codeTest', {
        codeList: this.testList,
        sourceId: this.codeSourceId,
        sourceType: this.testType.REPORT_BLOCK_CODE,
        projectId: this.projectId,
        showErrorButton: true
      })
      // this.$bus.$emit('code-test')
    },
    condition (oldValue, newValue) {
      this.editingNode.data.conditionCheckState = false
    },
    showType () {
      this.refreshPage()
    }
  },
  computed: {
    testList () {
      let codeList = []
      for (let idx = 0; idx <= this.testCodeIdx; idx++) {
        let content = this.codeList[idx].content
        codeList.push(content)
      }
      return codeList
    },
    codeSourceId () {
      let code = this.codeList[this.testCodeIdx]
      return code ? code.id : 0
    },
    testCondition () {
      return [this.editingNode.data.condition]
    },
    projectId () {
      return this.$route.query.projectId || ''
    },
    rootId () {
      return this.$route.query.rootId || ''
    },
    testPass () {
      return !this.codeList.find(item => !item.checkState)
    },
    currentReportData () {
      return this.findNodeInTree(this.reportData, this.currentReport)
    },
    currentChapterData () {
      return this.findNodeInTree(this.chapterData, this.currentChapter)
    },
    condition () {
      return this.editingNode.data.condition
    }
  },
  mounted () {
    this.getReportTree()
    this.getProjectDetail()
    this.timerIndex = setTimeout(this.checkCodeSave, this.detaCheckTime)
    this.$bus.$on('code-test-error-card', this.errorNotify)
    this.$bus.$on('code-test-pass', this.codeTestPass)
    this.$bus.$on('code-test-focus-error-code', this.focusErrorCode)
    this.$bus.$on('code-test-end', this.codeTestEnd)
    this.$bus.$on('jump-to-code', this.jumpToCode)
  },
  beforeDestroy () {
    if (this.timerIndex) {
      this.checkCodeSave()
      clearTimeout(this.timerIndex)
    }
    this.$bus.$emit('pop-helper-hide-menu', 'codeTest')
    this.$bus.$emit('pop-helper-hide-menu', 'nodeTree')
    this.$bus.$off('code-test-error-card', this.errorNotify)
    this.$bus.$off('code-test-pass', this.codeTestPass)
    this.$bus.$off('code-test-focus-error-code', this.focusErrorCode)
    this.$bus.$off('code-test-end', this.codeTestEnd)
    this.$bus.$off('jump-to-code', this.jumpToCode)
  },
  methods: {
    codeCardKeyDown (event, item, idx) {
      let stop = true
      let prevent = true
      if (event.ctrlKey) {
        if (event.code === 'Enter') {
          if (event.shiftKey) {
            this.$bus.$emit('pop-helper-show-menu', 'nodeTree')
          } else {
            this.openTestBord(idx)
          }
        } else if (event.code === 'KeyS') { // ctrl + S
          this.checkCodeSave(1)
        } else if (event.code === 'KeyD') { // ctrl + D
          this.recordCodeInfo(item)
        } else {
          stop = prevent = false
        }
      } else {
        stop = prevent = false
      }
      if (stop) {
        event.stopPropagation()
      }
      if (prevent) {
        event.preventDefault()
      }
    },
    async getProjectDetail () {
      this.projectInfo = await project.getProjectDetail({ id: this.projectId })
    },
    refreshPage () {
      this.getProjectDetail()
      this.turnToReport = this.currentReport
      this.turnToChapter = this.currentChapter
      this.getReportTree()
    },
    async getReportTree () {
      this.reportData = []
      this.chapterData = []
      this.codeList = []
      let data = await report.getReportTree({ projectRootId: this.rootId })
      if (!data) {
        this.$Message.error('获取报告树失败')
        return
      }
      let nodeList = []
      if (this.showType === 0) {
        nodeList = data.result.filter(item => item.projectId === this.projectId)
      } else {
        nodeList = data.result
      }
      if (nodeList) {
        this.listToTree(this.reportData, nodeList)
        if (this.reportData.length) {
          this.chooseTreeNode('report')
        } else {
          // this.autoCreate()
        }
      }
    },
    async getChapterTree () {
      this.chapterData = []
      this.codeList = []
      if (!this.currentReport) return
      let data = await report.getChapterTree({ reportId: this.currentReport })
      if (!data) {
        this.$Message.error('获取章节树失败')
        return
      }
      let nodeList = data.result
      if (nodeList) {
        this.listToTree(this.chapterData, nodeList)
        if (this.chapterData.length) {
          this.chooseTreeNode('chapter')
        }
      }
    },
    async getCodeList () {
      this.codeList = []
      this.testCodeIdx = -1
      if (!this.currentChapter) return
      let data = await report.getCodeList({ reportOutlineId: this.currentChapter })
      if (!data) {
        this.$Message.error('获取代码列表失败')
      }
      this.codeList = data.result
      this.codeList.sort((a, b) => a.sort - b.sort)
      let turnIndex = this.codeList.findIndex(code => code.id === this.turnToCode)
      if (turnIndex !== -1) {
        this.$nextTick(() => {
          this.$refs.editor[turnIndex].$el.scrollIntoView()
          this.$refs.editor[turnIndex].focusEditor()
        })
      }
    },
    jumpToCode (data) {
      if (data.report) {
        this.turnToReport = data.report
      }
      if (data.chapter) {
        this.turnToChapter = data.chapter
      }
      if (data.code) {
        this.turnToCode = data.code
      }
      console.log(data)
      this.getReportTree()
    },
    listToTree (treeData, listData) {
      if (!listData) return
      treeData.splice(0, treeData.length)
      let compare = function (a, b) {
        return a.sort - b.sort
      }
      for (let data of listData) {
        if (!data.parentId) {
          treeData.push(data)
          treeData.sort(compare)
          continue
        }
        for (let _data of listData) {
          if (_data.id === data.parentId) {
            let childList = _data.children || []
            childList.push(data)
            childList.sort(compare)
            this.$set(_data, 'children', childList)
          }
        }
      }
    },
    findNodeInTree (treeData, id) {
      if (!treeData.length) return null
      for (let data of treeData) {
        if (data.id === id) return data
        let res = this.findNodeInTree(data.children || [], id)
        if (res) return res
      }
      return null
    },
    chooseTreeNode (type, preChoose) {
      let attrTurn = type === 'report' ? 'turnToReport' : 'turnToChapter'
      let attrCurrent = type === 'report' ? 'currentReport' : 'currentChapter'
      let treeData = type === 'report' ? this.reportData : this.chapterData
      let choose = preChoose || this[attrTurn]
      let node = null
      if (choose) {
        node = this.findNodeInTree(treeData, choose)
      }
      console.log(node, preChoose, this[attrTurn], '~~~~~~~~~~~~~~~~~choose')
      if (!node && treeData.length) {
        node = treeData[0]
      }
      this[attrCurrent] = node ? node.id : 0
      if (type === 'report') {
        this.getChapterTree()
      } else {
        this.getCodeList()
      }
    },
    async autoCreate () {
      this.editingNode.data = {
        condition: '',
        title: '默认创建的报告',
        projectId: this.projectId,
        parentId: null,
        note: '',
        projectRootId: this.rootId
      }
      let result = await this.addRootNode('report')
      if (!result) {
        this.$Message.error('创建示例报告失败')
        return
      }
      this.editingNode.data = {
        condition: '',
        title: '默认创建的章节',
        projectId: '',
        parentId: null,
        note: '',
        reportId: this.currentReport
      }
      result = await this.addRootNode('chapter')
      if (!result) {
        this.$Message.error('创建示例章节失败')
        return
      }
      result = await this.addCodeCard(null, true)
      if (!result) {
        this.$Message.error('创建示例代码块失败')
        return
      }
      let data = this.codeList[0]
      let defaultCode = '<?\n// 逻辑内容\n' + '\techo "Hello World!";' + '\n\n\n\n?>'
      data.content = defaultCode
      this.checkCodeSave()
    },
    openEditing (data, title, showProject, callback, ...args) {
      this.editingNode.title = title
      this.editingNode.showProjectList = showProject
      this.$set(this.editingNode, 'data', data)
      this.editingNode.data = data
      this.editingNode.callback = {
        func: callback,
        args: args
      }
      this.editingNode.show = true
    },
    editingDone () {
      this.editingDoneCallback()
      // if (!this.checkEditingNode()) return
      // if (this.editingNode.conditionCheckState) {
      //   this.editingDoneCallback()
      //   return
      // }
      // this.$refs.codeEditor.openTest()
    },
    async editingDoneCallback () {
      if (!this.editingNode.show) return // 页面都关了还存什么存
      if (!this.checkEditingNode()) return
      let func = this.editingNode.callback.func
      let args = this.editingNode.callback.args
      let res = await func(...args)
      if (res) {
        this.clearEditingInfo()
      }
    },
    editingCancle () {
      this.clearEditingInfo()
    },
    clearEditingInfo () {
      this.editingNode = {
        show: false,
        data: {
          condition: '',
          title: '',
          projectId: ''
        },
        callback: {},
        showProjectList: false
      }
    },
    checkEditingNode () {
      if (!this.editingNode.data.title) {
        this.$Message.warning('标题不能为空！')
        return false
      }
      return true
    },
    newTreeNode (mergeInfo, type) {
      let data = {
        title: '',
        condition: '',
        parentId: null,
        note: '',
        sort: 0,
        type: 1,
        conditionCheckState: false
      }
      Object.assign(data, mergeInfo)
      if (type === 'report') {
        data.projectRootId = this.rootId
      } else {
        data.reportId = this.currentReport
      }
      return data
    },
    addTreeNode (type, data) {
      if (type === 'report') {
        return report.addReportNode(data)
      } else {
        return report.addChapterNode(data)
      }
    },
    tryAddRootNode (type) {
      let data = {}
      let title, showProjectList
      if (type === 'report') {
        data.projectId = this.projectId
        title = '添加根级报告'
        showProjectList = true
      } else {
        title = '添加根级章节'
        showProjectList = false
      }
      let node = this.newTreeNode(data, type)
      this.openEditing(node, title, showProjectList, this.addRootNode, type)
    },
    async addRootNode (type) {
      let changeData = type === 'report' ? this.reportData : this.chapterData
      let nodeData = this.newTreeNode(this.editingNode.data, type)
      let newNode = await this.addTreeNode(type, nodeData)
      if (newNode) {
        newNode.children = []
        changeData.push(newNode)
        this.chooseTreeNode(type, newNode.id)
        return true
      } else {
        this.$Message.error('添加失败')
        return false
      }
    },
    tryAddBrother (type, data, parentData) {
      let info = {}
      let title, showProjectList
      if (type === 'report') {
        info.projectId = data.projectId || this.projectId
        title = '添加同级报告'
        showProjectList = !parentData.id
      } else {
        title = '添加同级章节'
        showProjectList = false
      }
      info.parentId = parentData.id || null
      info.sort = data.sort + 1
      let node = this.newTreeNode(info, type)
      this.openEditing(node, title, showProjectList, this.addBrotherNode, type, parentData)
    },
    async addBrotherNode (type, parentData) {
      let newNode = await this.addTreeNode(type, this.editingNode.data)
      if (newNode) {
        parentData.children.push(newNode)
        parentData.children.sort((a, b) => a.sort > b.sort)
        this.chooseTreeNode(type, newNode.id)
        return true
      } else {
        this.$Message.error('添加失败')
        return false
      }
    },
    tryAddChild (type, data) {
      if (!data.children) {
        this.$set(data, 'children', [])
        data.children = []
      }
      let info = {}
      let title
      if (type === 'report') {
        title = '添加子级报告'
        info.projectId = data.projectId
      } else {
        title = '添加子级章节'
      }
      info.parentId = data.id
      info.sort = data.children.length
      let node = this.newTreeNode(info, type)
      this.openEditing(node, title, false, this.addChildNode, type, data)
    },
    async addChildNode (type, data) {
      let newNode = await this.addTreeNode(type, this.editingNode.data)
      if (newNode) {
        data.children.push(newNode)
        this.chooseTreeNode(type, newNode.id)
        return true
      } else {
        this.$Message.error('添加失败')
        return false
      }
    },
    tryDeleteNode (parentData, data, type) {
      let title = type === 'report' ? '报告' : '章节'
      let text = `确定删除${data.title || title}吗？`
      this.$confirm(text, this.deleteNode, type, parentData, data)
    },
    async deleteNode (type, parentData, data) {
      let func = type === 'report' ? report.deleteReportNode : report.deleteChapterNode
      let res = await func({ ids: [data.id] })
      if (res) {
        let idx = parentData.children.findIndex(item => item.id === data.id)
        if (idx !== -1) {
          parentData.children.splice(idx, 1)
        }
        if ((type === 'report' && this.currentReport === data.id) || (type === 'chapter' && this.currentChapter === data.id)) {
          this.currentChapter = 0
          this.chooseTreeNode(type)
        }
      } else {
        this.$Message.error('删除失败')
      }
    },
    tryUpdateNode (type, data) {
      if (data.type === 2) {
        this.$Message.warning('全局变量不允许编辑')
        return
      }
      let info = _.cloneDeep(data)
      let title, showProjectList
      if (type === 'report') {
        title = '编辑报告'
        showProjectList = Boolean(data.projectId)
      } else {
        title = '编辑章节'
        showProjectList = false
      }
      this.openEditing(info, title, showProjectList, this.updateNode, type, data)
    },
    async updateNode (type, data) {
      let params = {}
      if (type === 'report') {
        params = {
          reportCodeMap: {
            oldObject: data,
            newObject: this.editingNode.data
          }
        }
      } else {
        params = this.editingNode.data
      }
      let func = type === 'report' ? report.updateReportNode : report.updateChapterNode
      let res = await func(params)
      this.refreshPage()
      if (res) {
        this.$Message.success('更新成功')
        return true
      } else {
        this.$Message.error('更新失败')
        return false
      }
    },
    allowDrag (data) {
      return data.type - 2
    },
    allowDrop (dragVNode, dropVNode, position) {
      return dropVNode.nodeData.type - 2
    },
    async handleDrop (type, eventArgs) {
      let dragVNode = eventArgs[0]
      let dropVNode = eventArgs[1]
      let dropType = eventArgs[2]
      let data = dragVNode.nodeData
      let params = { id: data.id }
      if (dropType === 'inner') {
        params.parentId = dropVNode.nodeData.id
        params.targetId = null
      } else {
        params.parentId = dropVNode.parentData.id || null
        if (dropType === 'before') {
          params.targetId = dropVNode.nodeData.id
        } else {
          let children = dropVNode.parentData.children
          let index = children.findIndex(d => d.id === dropVNode.nodeData.id)
          params.targetId = children[index + 1] ? children[index + 1].id : null
        }
      }
      let resData = null
      if (type === 'report') {
        resData = await report.moveReportNode(params)
      } else {
        resData = await report.moveChapterCodeOutline(params)
      }
      this.refreshPage()
      if (!resData) {
        this.$Message.error('更新失败了')
      }
    },
    handleContentClick (type, args) {
      let id = args[1].nodeData.id
      this.chooseTreeNode(type, id)
    },
    returnToProject () {
      this.$router.push({ name: 'Page', query: { docID: this.projectId } })
    },
    async addCodeCard (item, manual = false) {
      let defaultCode = '<?\n// 逻辑内容\n\n\n\n\n\n?>'
      if (item) {
        let index = this.codeList.findIndex(data => data.id === item.id)
        if ((item.content === defaultCode) || (this.codeList.length > index + 1 && this.codeList[index + 1].content === defaultCode)) {
          if (!manual) {
            this.$Message.error('添加失败，当前存在空代码块！')
          }
          return true
        }
      }
      let data = await report.addCode({
        reportCodeBlockAO: {
          condition: defaultCode,
          content: defaultCode,
          note: '',
          title: '新建代码块',
          sort: 0, // 当targetId为null时才读
          reportOutlineId: this.currentChapter
        },
        targetId: item ? item.id : null
      })
      if (data) {
        let index = this.codeList.findIndex(data => data.id === item.id)
        if (index !== -1) {
          this.codeList.splice(index + 1, 0, data)
        } else {
          this.codeList.push(data)
        }
        if (!manual) {
          this.$Message.success('添加成功')
        }
        return true
      } else {
        if (!manual) {
          this.$Message.error('添加失败')
        }
        return false
      }
    },
    tryDeleteCode (item) {
      let sText = `确定删除代码块${item.code}吗？`
      this.$confirm(sText, this.deleteCode, item)
    },
    async deleteCode (item) {
      let data = await report.deleteCodeCard({ ids: [item.id] })
      if (data) {
        let index = this.codeList.findIndex(code => code.id === item.id)
        if (index !== -1) {
          this.codeList.splice(index, 1)
          if (index === this.testCodeIdx) {
            this.testCodeIdx = -1
          }
          this.$Message.success('删除成功')
        }
      } else {
        this.$Message.error('删除失败')
      }
    },
    openTestBord (idx) {
      this.testCodeIdx = idx
      this.openTest()
    },
    openTest () {
      this.$bus.$emit('pop-helper-update-prop', 'codeTest', {
        codeList: this.testList,
        sourceId: this.codeSourceId,
        sourceType: this.testType.REPORT_BLOCK_CODE,
        projectId: this.projectId,
        showErrorButton: true
      })
      this.$bus.$emit('pop-helper-show-menu', 'codeTest')
      this.$bus.$emit('code-test')
    },
    closeTest () {
      this.$bus.$emit('pop-helper-hide-menu')
    },
    recordDirty (id, checkState) {
      if (!this.dirtyCode.includes(id)) {
        this.dirtyCode.push(id)
      }
      let item = this.codeList.find(codeItem => codeItem.id === id)
      if (item) {
        item.checkState = checkState
      }
    },
    async checkCodeSave (manual) {
      if (this.timerIndex) {
        clearTimeout(this.timerIndex)
      }
      if (!this.dirtyCode.length) {
        if (manual) {
          this.$Message.success('当前页面所有代码块已保存')
        }
        this.timerIndex = setTimeout(this.checkCodeSave, this.detaCheckTime)
        return true
      }
      let unSaveList = []
      let right = true
      for (let id of this.dirtyCode) {
        let idx = this.codeList.findIndex(item => item.id === id)
        if (idx === -1) continue
        let code = this.codeList[idx]
        let result = await this.saveCode(code.id)
        if (!result) {
          unSaveList.push(code.id)
          this.$Message.error('代码块' + code.id + '保存失败')
          right = false
        }
      }
      // if (right && this.dirtyCode.length) {
      //   this.$Message.success('代码保存成功')
      // }
      this.dirtyCode = unSaveList
      this.timerIndex = setTimeout(this.checkCodeSave, this.detaCheckTime)
      return right
    },
    async saveCode (id) {
      let idx = this.codeList.findIndex(code => code.id === id)
      if (idx === -1) return
      let code = this.codeList[idx]
      let data = await report.saveCode.call(this, code)
      return data !== null
    },
    errorNotify (errorCodeIDx) {
      this.setTestCodeState(errorCodeIDx, false)
      this.errorCodeIDx = errorCodeIDx
    },
    codeTestPass (sourceType) {
      if (sourceType === this.testType.REPORT_BLOCK_CODE) {
        this.setTestCodeState()
      } else if (sourceType === this.testType.REPORT_OUTLINE_CODE_CONDITION || sourceType === this.testType.REPORT_CODE_CONDITION) {
        // this.editingNode.conditionCheckState = true
        // this.editingDoneCallback()
      }
    },
    setTestCodeState (start = 0, pass = true) {
      if (pass) {
        this.errorCodeIDx = -1
      }
      for (let idx = start; idx <= this.testCodeIdx; idx++) {
        if (this.codeList[idx] && this.codeList[idx].checkState !== pass) {
          this.recordDirty(this.codeList[idx].id, pass)
        }
      }
    },
    focusErrorCode () {
      if (this.errorCodeIDx !== -1) {
        this.$nextTick(() => {
          let node = document.getElementsByClassName('error code-editor').item(0)
          if (node) {
            node.scrollIntoView()
            node.__vue__.focusEditor()
          }
        })
      }
    },
    codeTestEnd (sourceType) {
      if (sourceType === this.testType.REPORT_BLOCK_CODE) {
        this.checkCodeSave(1)
      } else if (sourceType === this.testType.REPORT_OUTLINE_CODE_CONDITION || sourceType === this.testType.REPORT_CODE_CONDITION) {
        // this.editingDone()
      }
    },
    pushToPage () {
      this.$router.push({
        name: 'Page',
        params: {
          projectId: this.rootId
        },
        query: {
          currentProject: this.projectId
        }
      })
    }
  }
}
</script>

