<template>
  <Row class-name="header-row"
       type="flex"
       align="middle"
       justify="start">
    <div class="logo">
      <router-link :to="{name: 'Home'}">
        <img class="logo-img"
             src="../assets/logo.png">
      </router-link>
    </div>
    <Col class-name="nav-bar">
    <Row class-name="nav-bar-row"
         type="flex"
         align="middle"
         justify="space-between">
      <Col class-name="nav-bar-left">
        <span @click="$router.go(-1)"
                title="上一页"
                class="return-button">
          <Icon type="ios-arrow-back"></Icon>
        </span>
        <span @click="$router.go(1)"
                title="下一页"
                class="return-button">
          <Icon type="ios-arrow-forward"></Icon>
        </span>
        <slot name="custom-nav"></slot>
      </Col>

      <Col class-name="nav-bar-right">
      <Row class-name="nav-bar-right-row"
           type="flex"
           align="middle"
           justify="end">
        <span>
          当前服务器：
          <Select style="width:10rem"
                  @on-change="chooseServer"
                  v-model="serverAddress">
            <Option :title="item.domain"
                    v-for="(item, key) in domains"
                    :value="key"
                    :key="key">
              {{ item.name }}
            </Option>
          </Select>
        </span>
        <span class="company">
          公司：
          <Select style="width:10rem"
                  v-model="currentCompany">
            <Option v-for="(item, key) of companyList"
                    :key="key"
                    :value="item.id"
                    :title="item.name">
              {{ item.name }}
            </Option>
          </Select>
        </span>
        <div
          class="message"
          :class="showMessageAbstract ? 'show-abstract' : ''"
          @click.stop="showMessageAbstract=true"
          v-click-outside="hideMessageAbstract">
          <div class="message-icon">
            <Icon type="ios-notifications-outline" />
          </div>
          <div class="message-abstract" v-if="showMessageAbstract">
            <div class="update-new-message message-content">
              <span
                @click.stop="updateNewMessage"
                class="update-button">
                刷新最新消息
              </span>
              <!-- (每10s会自动刷新一次,也可手动刷新) -->
              <Icon
                v-if="loading & 1"
                type="ios-loading"
                class="loading"
                size=18>
              </Icon>
            </div>
            <div
              class="message-content"
              v-for="(msg, idx) in messageList"
              :key="idx">
              {{ msg.msgContent }}
              {{ formatTime(msg.msgCreateTime) }}
            </div>
            <div
              @click.stop="updatePreviousMessage"
              class="update-previous-message message-content">
              加载更早一周的消息
              <Icon
                v-if="loading & 2"
                type="ios-loading"
                class="loading"
                size=18>
              </Icon>
            </div>
            <div class="message-menu">
              <div class="mark-all-read">
                <Icon type="md-checkmark-circle" />
                全部标记为已读
              </div>
              <div class="look-all">
                查看全部
                <Icon type="ios-keypad" />
              </div>
            </div>
          </div>
        </div>
        <div class="account nav-right-menu">
          <Poptip placement="bottom">
            <span>{{userInfo.name}}</span>
            <Icon type="arrow-down-b"></Icon>
            <template slot="content">
              <div class="account-content">
                <img class="avator account-content-item"
                     :src="avatarUrl">
                <div class="account-content-item">{{userInfo.name}}</div>
                <div class="account-content-item">{{userInfo.phone}}</div>
                <div class="account-content-item">{{userInfo.email}}</div>
              </div>
              <div class="account-set">
                <Button class="account-set-item"
                        @click="accountSetting()">账号设置</Button>
                <Button class="account-set-item"
                        @click="bindWx()">绑定微信</Button>
                <Button class="account-set-item"
                        @click="signOut()">退出登录</Button>
              </div>
            </template>
          </Poptip>
        </div>
        <div
          @mouseenter="showHelperSettingButton=true"
          @mouseleave="showHelperSettingButton=false"
          class="nav-helper">
          <Icon
            type="ios-help-circle-outline"
            class="help-button"
            title="小助手"
            @click="showHelper"
            :size="24" />
            <div
              class="helper-setting"
              v-if="showHelperSettingButton"
              @click.stop="openHelperSetting">
              设置
            </div>
            <Modal
              title="设置默认打开方式"
              v-model="showHelperSetting"
              @on-cancel="settingHelper()"
              transfer>
              <div @click="settingHelper(1)" class="helper-setting-button">
                <Button :type="helperShowType === '1' ? 'primary' : 'default'">新窗口打开（适合多屏幕）</Button>
              </div>
              <div @click="settingHelper(2)" class="helper-setting-button">
                <Button :type="helperShowType === '2' ? 'primary' : 'default'">当前窗口打开（在当前页面悬浮一个小助手）</Button>
              </div>
              <div slot="footer"></div>
            </Modal>
        </div>
      </Row>
      </Col>
    </Row>
    </Col>
  </Row>
</template>

<script>
import { Domains } from '@/libs/config'
import { requestLogout } from '@/api/login'

export default {
  name: 'page-header',
  data () {
    return {
      serverAddress: localStorage.server || 'e',
      domains: Domains,
      showHelperSettingButton: false,
      showHelperSetting: false,
      helperShowType: 0,
      openAfterSetting: false,
      showMessageAbstract: false,
      timeIndex: 0,
      loading: 0 // &1 - 加载最近消息 &2 - 加载前一周消息
    }
  },
  methods: {
    signOut () {
      requestLogout().then(resp => {
        if (resp.response.success) {
          this.$store.commit('user/clearToken')
          this.$store.commit('user/clearUserInfo')
          this.$router.push({ name: 'Login', params: { logout: 'true' } })
        }
      })
    },
    accountSetting () {
      this.$router.push({ name: 'AccountSetting' })
    },
    bindWx () {
      this.$router.push({ name: 'BindWX' })
    },
    chooseServer (value) {
      if (localStorage.server === value) {
        return
      }
      localStorage.server = value
      location.reload()
    },
    showHelper () {
      this.$bus.$emit('pop-helper-show')
    },
    checkBeforeOpenHelper (callback, args) {
      let helperShowType = window.localStorage.getItem('helperShowType')
      if (!helperShowType) {
        this.openHelperSetting(true)
      }
    },
    settingHelper (value) {
      this.showHelperSetting = false
      if (value) {
        window.localStorage.setItem('helperShowType', value)
      }
      if (this.openAfterSetting) {
        this.showHelper()
        this.openAfterSetting = false
      }
    },
    openHelperSetting (openAfterSetting) {
      this.helperShowType = window.localStorage.getItem('helperShowType')
      this.showHelperSetting = true
      this.openAfterSetting = openAfterSetting
    },
    async updateNewMessage () {
      if (this.timeIndex) {
        clearTimeout(this.timeIndex)
      }
      // this.timeIndex = setTimeout(this.updateNewMessage, 10 * 1000)
      this.loading = this.loading | 1
      await this.$store.dispatch('message/refreshNewRecord')
      this.loading = this.loading ^ 1
    },
    async updatePreviousMessage () {
      this.loading = this.loading | 2
      await this.$store.dispatch('message/refreshPreviousRecord')
      this.loading = this.loading ^ 2
    },
    hideMessageAbstract () {
      this.showMessageAbstract = false
    },
    formatTime (timeStamp) {
      let formatNum = (num) => {
        return num < 10 ? '0' + num : num
      }
      let date = new Date(timeStamp)
      let year = date.getFullYear()
      let month = formatNum(date.getMonth() + 1)
      let day = formatNum(date.getDate())
      let hour = formatNum(date.getHours())
      let minute = formatNum(date.getMinutes())
      let sText = `${year}-${month}-${day} ${hour}:${minute}`
      return sText
    }
  },
  mounted () {
    this.$bus.$on('check-before-open', this.checkBeforeOpenHelper)
    this.$bus.$on('update-new-message', this.updateNewMessage)
    this.$bus.$on('update-previous-message', this.updatePreviousMessage)
    this.updateNewMessage()
  },
  beforeDestroy () {
    this.$bus.$off('check0before-open-helper', this.checkBeforeOpenHelper)
    this.$bus.$off('update-new-message')
    this.$bus.$off('update-previous-message')
    if (this.timeIndex) {
      clearTimeout(this.timeIndex)
    }
  },
  computed: {
    messageList () {
      return this.$store.state.message.messageList
    },
    avatarUrl () {
      if (this.userInfo.avatar) {
        return this.$avatarUrl.replace('avatarID', this.userInfo.avatar)
      } else {
        return null
      }
    },
    userInfo () {
      return this.$store.getters['user/userInfo'] || {}
    },
    companyList () {
      return this.$store.state.user.companyList
    },
    currentCompany () {
      return this.$store.state.user.currentCompany
    }
  }
}
</script>

