export default {
  /**
   * 做题-获取项目信息
   */
  async getQuestionnaire  (projectId) {
    try {
      const { data } = await this.$request.get('/mvp-manager/questionnaire/' + projectId)
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 做题-答案
   */
  async answer (parame) {
    try {
      const { data } = await this.$request.post('/mvp-manager/questionnaire', parame)
      if (data.response.code === 0) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg || '项目出错'
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 做题-答案【大表单】
   */
  async answerBig  (parame) {
    try {
      const { data } = await this.$request.post('/mvp-manager/bigForm', parame)
      if (data.response.code === 0) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg || '项目出错'
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 获取报告
   */
  async getReport (params) {
    try {
      const { data } = await this.$request.post('/mvp-manager/questionnaire/report', params)
      if (data.response.code === 0 || data.response.code === 202) {
        return {
          status: 1,
          result: data.data
        }
      } else if (data.response.success === false) {
        return {
          status: data.response.code,
          msg: data.response.msg
        }
      } else {
        console.log(data.response)
        return {
          status: 2,
          msg: data.response.msg
        }
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 做题记录
   */
  async getAnswerRecord (params) {
    try {
      let { data } = await this.$request.post('/mvp-manager/answerRecord/list', params)
      if (data.response.success) {
        return data.data || []
      }
    } catch (error) {
      console.log(error)
    }
    return null
  },

  /**
   * 历史做题记录列表
   */
  async getHistoryAnswerRecord (params) {
    try {
      let { data } = await this.$request.post('/mvp-manager/answerRecord/infos', params)
      if (data.response.success) {
        return data.data || []
      }
    } catch (error) {
      console.log(error)
    }
    return null
  },
  /**
   * 根据做题记录获取报告
   */
  async getReportContent (params) {
    try {
      let { data } = await this.$request.post('/mvp-manager/answerRecord/report', params)
      if (data.response.success) {
        return data.data
      }
    } catch (error) {
      console.log(error)
    }
    return ''
  }
}
