<template>
  <div class="project-tree-page">
    <div class="tab-title">
      <div
        class="tab-item"
        :class="treeType === typeMap.project ? 'current-tab' : ''"
        @click="chooseTreeType(typeMap.project, true)">
        项目
      </div>
      <div
        @click="chooseTreeType(typeMap.package, true)"
        :class="treeType === typeMap.package ? 'current-tab' : ''"
        class="tab-item">
        共用包
      </div>
      <div
        @click="chooseTreeType(typeMap.search, true)"
        :class="treeType === typeMap.search ? 'current-tab' : ''"
        class="tab-item">
        搜索
      </div>
    </div>
    <div class="contents">
      <div
        class="content"
        v-if="treeType === typeMap.project">
        <my-tree
          :allowDrag="allowDrag"
          :allowDrop="allowDrop"
          @tree-drop="handleDrop"
          @content-click="handleContentClick"
          :treeData="projectData"
          :expandDefault="false"
          :indentLine="true"
          :indentLimit="40"
          class="tree"
          draggable>
          <div
            slot-scope="{ parentData, data }"
            @dblclick="handleUI('编辑项目', data, updateNode, [data])"
            :class="{
              'node-content': true,
              'choose': data.id === chooseProject.id
            }">
            <div class="node-code" v-click-copy :class="data.type === 1 ? 'node-name-project' : 'node-name-package'">{{ data.code }}</div>
            <div class="node-name" :title="data.name">{{ data.name }}</div>
            <div class="node-divide"></div>
            <div class="node-menu-icon">
              <Icon
                type="md-add-circle"
                v-if="String(data.id) !== String(projectID)"
                @click="handleUI('添加项目', null, addBrother, [parentData, data])"
                title="添加同级项目"/>
              <Icon
                type="md-add"
                @click="handleUI('添加子项目', null, addChild, [data])"
                title="添加子级项目"/>
              <Icon
                type="md-trash"
                v-if="String(data.id) !== String(projectID)"
                @click="tryDeleteNode(parentData, data)"
                title="删除项目"/>
            </div>
          </div>
        </my-tree>
      </div>
      <div
        class="content"
        v-else-if="treeType === typeMap.package">
        <my-tree
          :allowDrag="allowDrag"
          :allowDrop="allowDrop"
          @content-click="handleContentClick"
          @tree-drop="handleDrop"
          :treeData="packageData[0].children"
          :expandDefault="false"
          class="tree"
          :indentLine="true"
          :indentLimit="40"
          draggable
          v-if="packageData[0].children && packageData[0].children.length">
          <div
            slot-scope="{ parentData, data}"
            @dblclick="handleUI('编辑共用包', data, updateNode, [data])"
            :class="{
              'node-content': true,
              'choose': data.id === chooseProject.id
            }">
            <div
              class="node-code"
              :class="data.type === 1 ? 'node-name-project' : 'node-name-package'"
              v-click-copy>
              {{ data.code }}
            </div>
            <div class="node-name" :title="data.name">{{ data.name }}</div>
            <div class="node-divide"></div>
            <div class="node-menu-icon">
              <Icon
                type="md-add-circle"
                v-if="String(data.id) !== String(projectID)"
                @click="handleUI('添加共用包', null, addBrother, [parentData, data])"
                title="添加同级共用包"/>
              <Icon
                type="md-add"
                @click="handleUI('添加子共用包', null, addChild, [data])"
                title="添加子级共用包"/>
              <Icon
                type="md-trash"
                v-if="String(data.id) !== String(projectID)"
                @click="tryDeleteNode(parentData, data)"
                title="删除共用包"/>
            </div>
          </div>
        </my-tree>
        <div
          class="add-package"
          v-else>
          <Button
            class="button"
            type="success"
            @click.stop="handleUI('添加共用包', null, addChild, [packageData[0]])">
            添加共用包
          </Button>
        </div>
      </div>
      <div
        class="content"
        v-else>
        <div class="search-tab tab-content">
          <div class="md-search">
            <i class="fas fa-search"></i>
            <input type="text" class="input-search" placeholder="请输入项目或者共用包名称" v-model="searchWord" />
          </div>
          <div class="search-result">
            <template v-if="this.searchResult.length">
              <div
                v-for="(data, idx) of this.searchResult"
                class="result-item"
                @click.stop="clickSearchResult(data)"
                :class="data.id === chooseProject.id ? 'choose' : ''"
                :key="idx">
                <div class="result-abstract">
                  {{ data.name }}
                </div>
                <Tooltip class="result-jump" :transfer="true" content="跳转" placement="right">
                  <Icon class="jump" @click.stop="turnToProject(data)" type="md-reply"></Icon>
                </Tooltip>
              </div>
            </template>
            <div v-else-if="this.searchWord !== ''">
              {{ searchNotice }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <Modal
      :title="editingNode.title"
      v-model="editingNode.show"
      :mask-closable="false"
      width="30%">
      <div slot="header">项目设置</div>
      <div @keyup.ctrl.enter="handleCallBack">
        <span>名称</span>
        <Input v-model="editingNode.data.name" style="margin-bottom: 10px" ref='focusInput'></Input>
        <span>显示名称</span>
        <Input v-model="editingNode.data.showName" style="margin-bottom: 10px"></Input>
        <span>英文名称</span>
        <Input v-model="editingNode.data.enName" style="margin-bottom: 10px"></Input>
        <span>项目描述</span>
        <Input type="textarea" v-model="editingNode.data.remarks" :autosize="{ minRows: 4 }" style="margin-bottom: 10px"></Input>
      </div>
      <div slot="footer">
        <Button @click.stop="clearHandleInfo">取消</Button>
        <Button type="primary" @click.stop="handleCallBack">保存</Button>
      </div>
    </Modal>
    <Modal
      @on-ok="deleteProject"
      @on-cancel="clearDeleteNodeInfo"
      v-model="showDeleteNotice"
      :title="'删除'"
      width="30%">
      <span>确定删除{{ this.deleteNodeInfo.data ? this.deleteNodeInfo.data.name : '' }}吗</span>
    </Modal>
  </div>
</template>
<script>
import project from '@/api/project'
import Tree from '@/components/simple-tree'

export default {
  name: 'project-tree',
  components: {
    'my-tree': Tree
  },
  props: {
    projectID: '',
    currentProject: ''
  },
  data () {
    return {
      settingData: {
        id: 0,
        name: '',
        showName: '',
        enName: ''
      },
      show: true,
      treeType: 0,
      typeMap: {
        project: 0,
        package: 1,
        search: 2
      },
      projectData: [],
      packageData: [],
      preChooseProject: 0,
      preChoosePackage: 0,
      projectExpanded: [],
      packageExpanded: [],
      editingNode: {
        changed: false,
        title: '',
        show: false,
        type: '',
        data: {
          name: '',
          showName: '',
          enName: '',
          remarks: ''
        },
        callback: {}
      },
      showDeleteNotice: false,
      deleteNodeInfo: {},
      chooseProject: {},
      dropDom: null,
      searchWord: '',
      searchResult: [],
      searchNotice: '',
      loadingData: false
    }
  },
  created () {
    this.checkSearch = _.debounce(this.searchByWord, 1000)
    this.$bus.$on('refresh-project-tree', this.refreshTree)
    this.$bus.$on('show-project-setting', this.showProjectSetting)
  },
  async mounted () {
    this.loadingData = true
    let packagePromise = this.getPackageTree()
    let projectPromise = this.getProjectTree()
    await packagePromise
    await projectPromise
    this.loadingData = false
    this.chooseProjectNode(this.projectData[0])
  },
  beforeDestroy () {
    this.$bus.$off('refresh-project-tree', this.refreshTree)
    this.$bus.$off('show-project-setting', this.showProjectSetting)
  },
  methods: {
    async getProjectTree () {
      let data = await project.getProjectList({ id: this.projectID, type: 1 })
      if (data) {
        let project = null
        if (!project && this.preChooseProject) {
          project = data.find(item => item.id === this.preChooseProject)
          this.preChooseProject = 0
        }
        this.defaultExpand(this.projectExpanded, data)
        this.listToTree(this.projectData, data)
        this.chooseProjectNode(project || this.projectData[0])
      } else {
        this.$Message.error('获取项目列表失败')
      }
    },
    async getPackageTree () {
      let data = await project.getProjectList({ id: this.projectID, type: 2 })
      if (data) {
        let project = null
        if (this.preChoosePackage) {
          project = data.find(item => item.id === this.preChoosePackage)
          this.preChoosePackage = 0
        }
        this.listToTree(this.packageData, data)
        this.defaultExpand(this.packageExpanded, data)
        if (this.packageData[0].children && this.packageData[0].children.length) {
          this.chooseProjectNode(project || this.packageData[0].children[0])
        }
      } else {
        this.$Message.error('获取共用包列表失败')
      }
    },
    listToTree (treeData, listData) {
      treeData.splice(0, treeData.length)
      if (!listData) return
      let rootProject = null
      for (let data of listData) {
        if (!data.parentId) {
          rootProject = data
          continue
        }
        for (let _data of listData) {
          if (_data.id === data.parentId) {
            let childList = _data.children || []
            childList.push(data)
            childList.sort((a, b) => {
              return a.order - b.order
            })
            this.$set(_data, 'children', childList)
          }
        }
      }
      treeData.push(rootProject)
    },
    chooseTreeType (type, manual = false) {
      this.treeType = type
      if (manual) {
        if (type === this.typeMap.project) {
          this.getProjectTree()
        } else if (type === this.typeMap.package) {
          this.getPackageTree()
        }
      }
    },

    /**
     * 刷新树并选择某一节点
     * params
     *  {
     *    type,
     *    projectId
     *  }
     * type 1-项目 2-共用包 3-all
     */
    async refreshTree (params, callback, callbackData) {
      let type = params.type || 3
      let data = null
      if (type & 1) {
        await this.getProjectTree()
        data = await this.getProjectData(params.projectId, this.projectData)
      }
      if (type & 2) {
        await this.getPackageTree()
        if (!data) {
          data = await this.getProjectData(params.projectId, this.packageData)
        }
      }
      if (data) {
        this.treeType = params.type === 2 ? this.typeMap.package : this.typeMap.project
        this.turnToProject(data)
      }
      if (callback) {
        callback(callbackData)
      }
    },
    async getProjectData (projectId, list) { // eslint-disable-next-line
      for (let data of list) {
        if (data.id === projectId) {
          return data
        }
        if (data.children) {
          return this.getProjectData(projectId, data.children)
        }
      }
      return null
    },
    defaultExpand (listExpand, listData) {
      if (!listData) return
      for (let data of listData) {
        if (data.expanded) {
          listExpand.push(data.id)
        }
      }
    },
    handleUI (title, data, callback, lst) {
      if (data) {
        Object.assign(this.editingNode.data, data)
      } else {
        this.clearHandleInfo()
      }
      this.editingNode.callback = {
        func: callback,
        varList: lst
      }
      this.editingNode.show = true
      this.$nextTick(() => {
        if (this.$refs.focusInput) {
          this.$refs.focusInput.focus()
        }
      })
    },
    async handleCallBack () {
      let res = true
      if (this.editingNode.callback.func) {
        res = await this.editingNode.callback.func.apply(this, this.editingNode.callback.varList || [])
      }
      if (res) {
        this.clearHandleInfo()
      }
    },
    clearHandleInfo () {
      this.editingNode = {
        changed: false,
        title: '',
        show: false,
        type: '',
        data: {
          name: '',
          showName: '',
          enName: '',
          remarks: ''
        },
        callback: {}
      }
    },
    async updateNode (data) {
      let resData = await project.updateProject({
        id: data.id,
        ...this.editingNode.data
      })
      if (resData) {
        data.name = resData.name
        data.enName = resData.enName
        data.showName = resData.showName
        data.remarks = resData.remarks
        return true
      } else {
        this.$Message.error('网络请求失败')
        return false
      }
    },
    async addBrother (parentData, data) {
      if (String(data.id) === String(this.projectID)) {
        this.$Message.error('不能给该节点添加兄弟节点')
        return
      }
      if (parentData.isRoot) parentData = this.packageData[0]
      const children = parentData.children
      const index = children.findIndex(d => d.id === data.id)
      let params = {
        parentId: parentData.id,
        sort: data.sort + 1,
        type: this.treeType + 1,
        ...this.editingNode.data
      }
      let newChild = await project.addProject(params)
      if (newChild) {
        newChild.children = []
        children.splice(index + 1, 0, newChild)
        this.turnToProject(newChild)
        // this.$nextTick(() => {
        //   let oNode = document.getElementById('project' + newChild.id)
        //   if (oNode) {
        //     oNode.focus()
        //   }
        // })
        return true
      } else {
        this.$Message.error('添加失败')
        return false
      }
    },
    async addChild (data) {
      let params = {
        parentId: data.id,
        sort: data.children ? data.children.length + 1 : 1,
        type: this.treeType + 1,
        ...this.editingNode.data
      }
      let newChild = await project.addProject(params)
      if (newChild) {
        newChild.children = []
        if (!data.children) {
          this.$set(data, 'children', [])
        }
        data.children.push(newChild)
        this.turnToProject(newChild)
        return true
        // this.$nextTick(() => {
        //   let oNode = document.getElementById('project' + newChild.id)
        //   if (oNode) {
        //     oNode.focus()
        //   }
        // })
      } else {
        this.$Message.error('添加失败')
        return false
      }
    },
    async deleteProject () {
      let { parentData, data } = this.deleteNodeInfo
      let resData = await project.deleteProject({ ids: [data.id] })
      if (resData) {
        const children = parentData.children
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      }
      if (data.type === 1) {
        this.getProjectTree()
      } else {
        this.getPackageTree()
      }
      this.clearDeleteNodeInfo()
    },
    tryDeleteNode (parentData, data) {
      this.deleteNodeInfo = {
        parentData,
        data
      }
      this.showDeleteNotice = true
    },
    clearDeleteNodeInfo () {
      this.showDeleteNotice = false
      this.deleteNodeInfo = {}
    },
    recordInfo (data) {
      this.oldProjectName = data.name
    },
    allowDrag (data) {
      return String(data.id) !== String(this.projectID)
    },
    allowDrop (dragVNode, dropVNode, position) {
      if (String(dropVNode.nodeData.id) === String(this.projectID) && position !== 'inner') {
        return false
      }
      return true
    },
    async handleDrop (dragVNode, dropVNode, dropType) {
      let data = dragVNode.nodeData
      if (dropType === 'inner') {
        data.parentId = dropVNode.nodeData.id
      } else {
        data.parentId = dropVNode.nodeData.parentId
        if (dropType === 'before') {
          data.sort = dropVNode.nodeData.sort - 1
        } else {
          data.sort = dropVNode.nodeData.sort + 1
        }
      }
      await project.updateProject(data)
      if (this.chooseProject.id) {
        if (this.chooseProject.type === 1) {
          this.preChooseProject = this.chooseProject.id
        } else {
          this.preChoosePackage = this.chooseProject.id
        }
      }
      if (this.treeType === 0) {
        this.getProjectTree()
      } else {
        this.getPackageTree()
      }
    },
    handleContentClick (event, vNode) {
      let data = vNode.nodeData
      this.chooseProjectNode(data)
    },
    chooseProjectNode (data) {
      if (this.loadingData || !data) return
      this.chooseProject = data
      this.$emit('nodeClick', data)
    },
    searchDataFromList (keyWord, list) {
      let lstResult = []
      for (let data of list) {
        if (data.name.includes(keyWord)) {
          lstResult.push(data)
        }
        if (data.children) {
          let result = this.searchDataFromList(keyWord, data.children)
          lstResult.push(...result)
        }
      }
      return lstResult
    },
    searchByWord () {
      if (!this.searchWord) return
      let lstProject = this.searchDataFromList(this.searchWord, this.projectData)
      this.searchResult = [...lstProject]
      if (this.packageData.length && this.packageData[0].children) {
        let lstPackage = this.searchDataFromList(this.searchWord, this.packageData[0].children)
        this.searchResult.push(...lstPackage)
      }
      if (!this.searchResult.length) {
        this.searchNotice = '无搜索结果'
      }
    },
    clickSearchResult (data) {
      this.chooseProjectNode(data)
    },
    turnToProject (data) {
      this.chooseProjectNode(data)
      this.treeType = data.type - 1
    },
    showProjectSetting () {
      this.handleUI('编辑项目', this.chooseProject, this.updateNode, [this.chooseProject])
    }
  },
  watch: {
    searchWord (newValue, oldValue) {
      this.searchNotice = '正在等待输入'
      this.checkSearch()
    }
  }
}
</script>

