import Poptip from './poptip'

Poptip.install = (vue, options = {}) => {
  vue.prototype.$poptip = (() => {
    const PopComponent = vue.extend(Poptip)
    let instance = {}
    instance = new PopComponent().$mount()
    document.body.appendChild(instance.$el)

    function mouseout (e) {
      hide()
      e.target.removeEventListener('mouseout', mouseout)
      instance.$el.removeEventListener('mouseout', mouseout)
    }

    function show (opts, e) {
      const rect = e.target.getBoundingClientRect()
      const position = {
        left: rect.left - rect.width / 2 + 'px',
        top: rect.top + rect.height + 4 + 'px'
      }
      e.target.addEventListener('mouseout', mouseout)
      instance.$el.addEventListener('mouseout', mouseout)

      instance.$data.content = opts.content
      instance.$data.position = position
      instance.$data.show = true
    }

    function hide () {
      instance.$data.show = false
      instance.$data.content = ''
      instance.$data.position.top = '-99999px'
      instance.$data.position.left = '-99999px'
    }
    return {
      show: show,
      hide: hide
    }
  })()
}

export default Poptip
