export default async function usualApi (func, path, params, defaultReturn) {
  try {
    let { data } = await func(path, params)
    if (data.response.success) {
      return data.data || defaultReturn || data.response
    }
  } catch (error) {
    console.log(error)
  }
  return (typeof defaultReturn === 'boolean') ? !defaultReturn : null
}
