import Request from '@/libs/request'
import usualApi from './util'

export default {
  /**
   * 增加代码块
   */
  getCodeList (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/listReportCodeBlock', params)
  },
  addCode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/saveReportCodeBlock', params)
  },
  updateCodeCard (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/udpateReportCodeBlock', params)
  },
  deleteCodeCard (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/deleteReportCodeBlock', params, true)
  },
  async testCode (params) {
    let { data } = await Request.post('/mvp-manager/test/codeTesting', params)
    return data
  },
  saveCode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/updateReportCodeBlock', params)
  },
  getReportTree (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/listReportCode', params)
  },
  getChapterTree (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/listReportCodeOutline', params)
  },
  addReportNode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/saveReportCode', params)
  },
  addChapterNode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/saveReportCodeOutline', params)
  },
  updateReportNode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/updateReportCode', params)
  },
  updateChapterNode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/updateReportCodeOutline', params)
  },
  moveReportNode (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/moveReportCode', params, {})
  },
  moveChapterCodeOutline (params) {
    return usualApi(Request.put, '/mvp-manager/reportCode/moveReportCodeOutline', params, {})
  },
  deleteReportNode (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/deleteReportCode', params, true)
  },
  deleteChapterNode (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/deleteReportCodeOutline', params, true)
  },
  getTokenExplain (params) {
    return usualApi(Request.post, 'mvp-manager/test/variableInfo', params)
  },
  getJsonSchema (params) {
    return usualApi(Request.post, '/mvp-manager/test/componentInfo', params)
  },
  checkReport (params) {
    return usualApi(Request.post, '/mvp-manager/reportCode/checkReportState', params, [])
  }
}
