<template>
  <div class="project-tree" v-click-outside="hideProjectTree" @mouseleave="hideProjectTree">
    <div class="choose-button" @mouseenter="openProjectTree" ref="chooseButton">
      <Button
        :size="size"
        :title="currentData.name"
        class="button">
        <span class="node-name"  :class="currentData.type === 1 ? 'node-name-project' : 'node-name-package'" v-if="currentData.code">
          {{ currentData.code }}
        </span>
        {{ currentData.name || '未选择'}}
        <span
          @click.stop="clearChoose"
          class="clear-node-name"
          v-if="currentData.name && clearAble">
          <Icon type="md-close"></Icon>
        </span>
      </Button>
    </div>
    <div
      @mouseleave="hideProjectTree"
      class='pop-container'
      ref="popContainer"
      v-show="showTree">
      <my-tree
        class="tree"
        @content-click="handleNodeClick"
        :indentLimit="40"
        :treeData="projectData">
        <div
          slot-scope="{ parentData, data}"
          :class="{
            'node-content': true,
            'invalid-choose': excludeList.includes(data.id)
          }">
          <div class="node-code" :class="data.type === 1 ? 'node-name-project' : 'node-name-package'">{{data.code}}</div>
          <div class="node-name" :title="data.name">{{data.name}}</div>
        </div>
      </my-tree>
    </div>
  </div>
</template>

<script>
import Tree from '@/components/simple-tree'
import project from '@/api/project'

export default {
  name: 'chooseProject',
  components: {
    'my-tree': Tree
  },
  props: {
    rootId: {
      type: String | Number,
      default: 0
    },
    value: {
      type: String | Number,
      default: 0
    },
    chooseAble: {
      type: Boolean,
      default: true
    },
    size: {
      type: String,
      default: 'default'
    },
    type: {
      type: Number,
      default: 0 // 0-all 1-项目 2-共用包
    },
    includeRoot: {
      type: Boolean,
      default: true
    },
    allowNull: {
      type: Boolean,
      default: false
    },
    excludeList: { // 注意数组值必须与树节点的数据类型一致（通常为数字）
      type: Array,
      default: () => []
    },
    clearAble: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    currentProject () {
      return this.value ? this.value : (this.allowNull ? null : this.rootId)
    }
  },
  data () {
    return {
      projectData: [],
      projectList: [],
      currentData: {},
      showTree: false,
      showPosition: 'bottom'
    }
  },
  created () {
    this.tryGetProject = _.debounce(this.getProjectTree, 100)
  },
  mounted () {
    this.tryGetProject()
  },
  methods: {
    async getProjectTree () {
      if (!this.rootId || (Number(this.rootId) === 0)) return
      let params = { id: this.rootId }
      if (this.type) {
        params.type = this.type
      }
      let data = await project.getProjectList(params)
      this.projectData = []
      if (data) {
        this.projectList = data
        this.listToTree(this.projectData, data)
        if (!this.includeRoot && this.projectData.length) {
          this.projectData = this.projectData[0].children || []
          let index = this.projectList.findIndex(item => item.id - this.rootId === 0)
          if (index !== -1) {
            this.projectList.splice(index, 1)
          }
        }
        this.checkCurrentProject()
      }
    },
    checkCurrentProject () {
      let data = this.projectList.find(item => item.id === this.currentProject)
      this.currentData = data || { id: null }
    },
    listToTree (treeData, listData) {
      if (!listData) return
      for (let data of listData) {
        if (!data.parentId) {
          treeData.push(data)
          continue
        }
        for (let _data of listData) {
          if (_data.id === data.parentId) {
            let childList = _data.children || []
            childList.push(data)
            childList.sort((a, b) => {
              return a.type === b.type ? a.order - b.order : a.type - b.type
            })
            this.$set(_data, 'children', childList)
          }
        }
      }
    },
    openProjectTree () {
      if (this.chooseAble) {
        this.showTree = true
        this.tryGetProject()
        let rectButton = this.$refs.chooseButton.getBoundingClientRect()
        let bottomDis = document.body.clientHeight - (rectButton.bottom + 5)
        let maxHeight = bottomDis
        this.$refs.popContainer.style.maxHeight = Math.round(maxHeight) + 'px'
      }
    },
    hideProjectTree () {
      this.showTree = false
    },
    handleNodeClick (event, vNode) {
      let data = vNode.nodeData
      if (this.excludeList.includes(data.id)) {
        this.$Message.error('禁止选择')
        return
      }
      this.hideProjectTree()
      if (!this.chooseAble) {
        this.$Message.error('禁止切换')
        return
      }
      this.currentData = data
    },
    clearChoose () {
      this.currentData = { id: null }
    }
  },
  watch: {
    currentData (newValue, oldValue) {
      this.$emit('input', this.currentData.id)
    },
    rootId (newValue, oldValue) {
      this.tryGetProject()
    },
    currentProject (newValue, oldValue) {
      this.checkCurrentProject()
    },
    type (newValue, oldValue) {
      this.tryGetProject()
    },
    includeRoot (newValue, oldValue) {
      this.getProjectTree()
    }
  }
}
</script>

