import Request from '@/libs/request'

export default {
  /**
   * 查询扩展信息分组详情
   */
  async getAdditionalDataFilter (dataFiliterId) {
    try {
      const {
        data
      } = await Request.post('/mvp-manager/component/getAdditionalDataFilter', {
        dataFiliterId
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async getAdditionalDataType (dataTypeId) {
    try {
      const {
        data
      } = await Request.post('/mvp-manager/componentItemAdditionalData/getAdditionalDataType', {
        dataTypeId
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async listAdditionalData (additionalDataFilterId) {
    try {
      const {
        data
      } = await Request.post('/mvp-manager/componentItemAdditionalData/listAdditionalData', {
        additionalDataFilterId
      })
      console.log(data)
      if (data.response.code === 0) {
        return data.data
      } else if (data.response.code === 1) {
        return []
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async listAdditionalDataFiliter (componentId) {
    try {
      const {
        data
      } = await Request.post('/mvp-manager/componentItemAdditionalData/listAdditionalDataFiliter', {
        componentId
      })
      if (data.response.code === 0) {
        return data.data
      } else if (data.response.code === 1) {
        return []
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async removeAdditionalDataFiliter (dataFiliterId) {
    try {
      const {
        data
      } = await Request.post('/mvp-manager/componentItemAdditionalData/removeAdditionalDataFiliter', {
        dataFiliterId
      })
      return data
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async removeAdditionalDataType (dataTypeId) {
    try {
      const {
        data
      } = await Request.post('/mvp-manager/componentItemAdditionalData/removeAdditionalDataType', {
        dataTypeId
      })

      return data
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async saveAdditionalData (config) {
    const {
      additionalData,
      additionalDataFilterId,
      componentId
    } = config
    try {
      const {
        data
      } = await Request.post('/mvp-manager/componentItemAdditionalData/saveAdditionalData', {
        additionalData,
        additionalDataFilterId,
        componentId
      })
      if (data.response.code === 0) {
        return data.data
      } else {
        return null
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  /**
   * 添加/修改 组件扩展分组信息
   */
  async saveAdditionalDataFilter (config) {
    const {
      componentId,
      endTime,
      id,
      name,
      sort,
      startTime,
      type,
      dataSourceId,
      havaData,
      note,
      showName
    } = config
    try {
      if (id === 0) {
        const {
          data
        } = await Request.post('/mvp-manager/componentItemAdditionalData/saveAdditionalDataFilter', {
          componentId,
          endTime,
          name,
          sort,
          startTime,
          type,
          dataSourceId,
          havaData,
          note,
          showName
        })

        return data
      } else {
        const {
          data
        } = await Request.post('/mvp-manager/componentItemAdditionalData/updateAdditionalDataFilter', {
          id,
          componentId,
          endTime,
          name,
          sort,
          type,
          startTime,
          note,
          showName
        })

        return data
      }
    } catch (err) {
      console.log(err)
      return null
    }
  },
  async saveAddtionalDataType (config) {
    const {
      dataFilterId,
      id,
      name,
      otherData,
      sort,
      type,
      note,
      showName
    } = config
    try {
      if (id === 0) {
        const {
          data
        } = await Request.post('/mvp-manager/componentItemAdditionalData/saveAddtionalDataType', {
          dataFilterId,
          name,
          otherData,
          sort,
          type,
          note,
          showName
        })

        return data
      } else {
        const {
          data
        } = await Request.post('/mvp-manager/componentItemAdditionalData/updateAdditionalDataType', {
          dataFilterId,
          id,
          name,
          otherData,
          sort,
          type,
          note,
          showName
        })

        return data
      }
    } catch (err) {
      console.log(err)
      return null
    }
  }
}
