<template>
  <div class="checkbox-wrapper" :class="{'checked': currentValue, 'disabled': disabled}">
    <label class="checkbox-box">
      <span class="checkbox-icon"></span>
      <input
        type="checkbox"
        :checked="currentValue"
        :disabled="disabled"
        :name="name"
        v-model="model"
        :value="label"
        @change="change"
        style="display: none">
      <span class="checkbox-label"><slot/></span>
    </label>
  </div>
</template>

<script>
import { findComponentUpward } from './assist'

export default {
  name: 'xCheckbox',
  props: {
    value: {
      type: [String, Number, Boolean],
      default: false
    },
    trueValue: {
      type: [String, Number, Boolean],
      default: true
    },
    falseValue: {
      type: [String, Number, Boolean],
      default: false
    },
    label: {
      type: [String, Number]
    },
    disabled: {
      type: Boolean,
      default: false
    },
    name: {
      type: String
    }
  },
  data () {
    return {
      model: [],
      currentValue: this.value,
      group: false,
      parent: findComponentUpward(this, 'xCheckboxGroup')
    }
  },
  mounted () {
    this.parent = findComponentUpward(this, 'xCheckboxGroup')
    if (this.parent) {
      this.group = true
    }
    if (this.group) {
      this.parent.updateModel(true)
    } else {
      this.updateModel()
    }
  },
  watch: {
    value (val) {
      if (val === this.trueValue || val === this.falseValue) {
        this.updateModel()
      } else {
        throw new Error('Value should be trueValue or falseValue.')
      }
    }
  },
  methods: {
    change (event) {
      if (this.disabled) {
        return false
      }
      const checked = event.target.checked
      this.currentValue = checked
      const value = checked ? this.trueValue : this.falseValue
      this.$emit('input', value)
      if (this.group) {
        this.parent.change(this.model)
      } else {
        this.$emit('on-change', value)
      }
    },
    updateModel () {
      this.currentValue = this.value === this.trueValue
    }
  }
}
</script>

