<template>
  <div class="helper">
    <div class="helper-header">
      <div class="header-title">小助手</div>
      <div class="header-devide"></div>
      <div class="header-menu-list" @mousedown.stop="">
        <div class="header-menu" v-click-outside="hideMore">
          <Icon type="md-more" class="icon" @click="showMore"/>
          <div class="more" v-show="showMenuMore">
            <div class="more-item" @click.stop="changeShowPosition('bottom')">放置在下</div>
            <div class="more-item" @click.stop="changeShowPosition('left')">放置在左</div>
            <div class="more-item" @click.stop="changeShowPosition('right')">放置在右</div>
          </div>
        </div>
        <div class="header-menu" v-click-outside="hideSetting">
          <Icon type="md-settings" class="icon" @click="showSetting"/>
          <div class="more" v-show="showMenuSetting">
            <div class="more-item" @click.stop="setOpen('auto')">自动弹出</div>
            <div class="more-item" @click.stop="setOpen('manual')">手动打开</div>
          </div>
        </div>
        <div class="header-menu">
          <Icon type="ios-close" />
        </div>
      </div>
    </div>
    <div class="helper-content">
      <div class="resource-list">
        <div
          :class="{
            resource: true,
            'current-menu': true
          }"
          v-for="(item, title) of componentInfo"
          @click.stop="showMenu(title)"
          :key="title">
          {{ item.title }}
        </div>
      </div>
      <div class="resource-tab" v-for="(resource, idx) of resourceList" :key="idx">
        <div class="resource-header">
          <div class="resource-title" v-for="(title, index) of resource.list" :key="index">
            {{ componentInfo[title].title }}
            <Icon type="ios-close" />
          </div>
          <div class="resource-devide"></div>
          <div class="resource-menu">
            <div class="devide-page-button" title="向右分页" v-if="resource.list.length > 1">
              <div class="devide-page-box" v-for="idx in 2" :key="idx"></div>
            </div>
          </div>
        </div>
        <div class="component">
          <component
            v-bind="componentInfo[resource.current].props"
            :is="componentInfo[resource.current].component">
          </component>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import networkDisk from '@/components/network-disk'
import shortcutKeys from '@/components/shortcut-key'

export default {
  data () {
    return {
      showMenuMore: false,
      showMenuSetting: false,
      show: true,
      componentInfo: {},
      resourceList: [],
      conflictInfo: [
        ['codeTest', 'nodeTree']
      ]
    }
  },
  mounted () {
    this.$bus.$on('pop-helper-update-prop', this.updateProps)
    this.$bus.$on('pop-helper-register-component', this.registerComponent)
    this.$bus.$on('pop-helper-unregister-component', this.unregisterComponet)
    this.registerDefault()
  },
  beforeDestroy () {
    this.$bus.$off('pop-helper-update-prop')
    this.$bus.$off('pop-helper-register-component')
    this.$bus.$off('pop-helper-unregister-component')
    this.unregisterDefault()
  },
  methods: {
    registerDefault () {
      let data = {
        title: '网盘',
        component: networkDisk
      }
      this.registerComponent('networkDisk', data)
      data = {
        title: '快捷键',
        component: shortcutKeys
      }
      this.registerComponent('shortcutKey', data)
    },
    unregisterDefault () {
      this.unregisterComponet('networkDisk')
      this.unregisterComponet('shortcutKey')
    },
    registerComponent (type, options) {
      if (this.componentInfo[type]) return
      let data = {
        title: options.title || '',
        component: options.component,
        props: options.props || {}
      }
      this.$set(this.componentInfo, type, data)
    },
    unregisterComponet (type) {
      if (!this.componentInfo[type]) return
      this.closeMenu(type)
      delete this.componentInfo[type]
    },
    updateProps (type, props) {
      if (this.componentInfo[type]) {
        for (let key in props) {
          this.$set(this.componentInfo[type].props, key, props[key])
        }
      }
    },
    showMenu (type) {
      let excludeList = []
      for (let resource of this.resourceList) {
        if (resource.list.includes(type)) {
          resource.current = type
          return
        }
        if (!this.checkConflict(type, resource.list)) {
          excludeList.push(resource)
        }
      }
      if (excludeList.length) {
        excludeList[0].list.push(type)
        excludeList[0].current = type
      } else {
        this.resourceList.push({
          current: type,
          list: [type]
        })
      }
    },
    closeMenu (type) {
      let delList = []
      for (let resource of this.resourceList) {
        let idx = resource.list.indexOf(type)
        if (idx !== -1) {
          resource.list.splice(idx, 1)
          if (!resource.list.length) {
            delList.push(resource)
          }
        }
      }
      for (let delRes of delList) {
        let index = this.resourceList.indexOf(delRes)
        if (index !== -1) {
          this.resourceList.splice(index, 1)
        }
      }
    },
    splitPage (idx) {
      let resource = this.resourceList[idx]
      if (resource.list.length < 2) return
      let type = resource.current
      let index = resource.list.indexOf(type)
      if (index === -1) return
      resource.list.splice(index, 1)
      resource.current = resource.list[0] // 重新选中第一个
      this.resourceList.splice(idx + 1, 0, {
        current: type,
        list: type
      })
    },
    checkConflict (type, list) {
      for (let group of this.conflictInfo) {
        if (group.includes(type)) {
          for (let conflictType of group) {
            if (conflictType !== type && list.includes(conflictType)) {
              return true
            }
          }
        }
      }
      return false
    },
    showMore () {
      this.showMenuMore = true
    },
    hideMore () {
      this.showMenuMore = false
    },
    showSetting () {
      this.showMenuSetting = true
    },
    hideSetting () {
      this.showMenuSetting = false
    },
    changeShowPosition (position) {
      this.hideMore()
      this.$emit('change-position', position)
    },
    setOpen (value) {
      this.storageSetItem('open', value)
      this.hideSetting()
    },
    storageSetItem (attr, value) {
      let popInfo = localStorage.getItem('helperInfo')
      let data = popInfo ? JSON.parse(popInfo) : {}
      data[attr] = JSON.stringify(value)
      let strInfo = JSON.stringify(data)
      window.localStorage.setItem('helperInfo', strInfo)
    },
    storageGetItem (attr) {
      let popInfo = window.localStorage.getItem('helperInfo')
      if (!popInfo) return null
      let data = JSON.parse(popInfo)
      let value = data[attr] ? JSON.parse(data[attr]) : null
      return value
    }
  }
}
</script>

<style lang="stylus" scoped>
.helper
  background white
  display flex
  flex-direction column
  .helper-header
    min-height 1rem
    display flex
    background #D0DEF8
    align-items center
    .header-title
      margin-right .5rem
      padding-right .5rem
    .header-devide
      flex auto
    .header-menu-list
      margin-left .5rem
      padding-left .5rem
      border-left 1px solid #A9A9A9
      display flex
      font-size 1rem
      font-weight bloder
      align-items center
      .header-menu
        display flex
        align-items center
        justify-content center
        margin 0 2px
        cursor pointer
        width 1rem
        height 1rem
        position relative
        .icon
          position relative
          &:hover
            background #78A4FA
            border-radius 2px
          &:active
            left 1px
            top 1px
      .more
        position absolute
        text-align center
        right 0
        top 100%
        width 5rem
        box-shadow 0 0 1px 1px #3361D8
        background white
        border-radius 4px
        z-index 1
        .more-item
          cursor pointer
          &:hover
            background #D2D2D2
  .helper-content
    flex auto
    display flex
    .resource-list
      max-width 6rem
      border-right 1px solid gray
      text-align center
      .resource
        border-bottom 1px solid gray
        padding 0 .5rem
        cursor pointer
        &:hover
          backdround
    .resource-tab
      display flex
      flex-direction column
      flex auto
      .resource-header
        min-height 1rem
        display flex
        .resource-devide
          flex auto
        .resource-menu
          .devide-page-button
            color gray
            border-top 4px solid currentColor
            width 15px
            height 15px
            display flex
            cursor pointer
            margin-right 5px
            .devide-page-box
              border 1px solid currentColor
              flex auto
      .component
        display flex
        overflow auto
</style>
