import iView from 'iview'
import { getToken, getUserInfo, delToken, delUserInfo } from '@/libs/util'
import { checkLogin } from '@/api/login'

export async function AuthGuard (to, from, next) {
  console.log('检查localStorage')
  const userInfo = getUserInfo()
  const token = getToken()
  if (!token && userInfo && !userInfo.unionid) {
    iView.Message.error('请先登录!')
    delToken()
    delUserInfo()
    next({ name: 'Login' })
  } else {
    console.log('检查绑定状态')
    if (userInfo && userInfo.id && !userInfo.unionid) {
      next({ name: 'BindWX' })
    } else if (userInfo && userInfo.unionid && !userInfo.id) {
      next({ name: 'BindPhone' })
    } else {
      console.log('检查登录是否过期')
      const resp = await checkLogin()
      if (!resp.response.success) {
        iView.Message.error(`${resp.response.msg}，请重新登录`)
        delToken()
        delUserInfo()
        next({ name: 'Login' })
      } else {
        next()
      }
    }
  }
}

export function authedGard (to, from, next) {
  if (getToken()) { // TODO: 登录校验
    next({ name: 'Home' })
  } else {
    next()
  }
}
