import Request from '@/libs/request'

export function sendSmsCode (params) {
  return Request.post('/mvp-manager/sms/sendVcode', params).then(resp => {
    return resp.data
  })
}

export function getCaptcha () {
  return Request.post('/mvp-manager/captcha').then(resp => resp.data)
}

export function validateCaptcha (params) {
  return Request.post('/mvp-manager/captcha/validate', params).then(resp => resp.data)
}

export function sendEmailVcode (params) {
  return Request.post('/mvp-manager/mail/sendVcode', params).then(resp => resp.data)
}
