<template>
  <div class="network-disk">
    <div class="title">网盘</div>
    <div class="disk-content">
      <div class="disk-menu">
        <div class="menu-line">
          <Upload
            class="disk-menu-upload"
            :headers="headers"
            :data="{
              parentId: currentFolder
            }"
            :on-success="handleSuccess"
            :on-error="handleFail"
            :show-upload-list="false"
            :action="upLoadPath">
            <Button type="success" class="disk-menu-button">
              <Icon class="disk-icon" type="ios-cloud-upload"></Icon>
              上传
            </Button>
          </Upload>
          <Button type="success" class="disk-menu-button" @click.stop="showEditingNode">
            <Icon class="disk-icon" type="md-folder"></Icon>
            新建文件夹
          </Button>
          <ButtonGroup>
            <Button class="disk-menu-button" @click.stop="showEditingNode(chooseList[0])" v-if="showMenu.rename">
              <Icon type="ios-create-outline" />
              重命名
            </Button>
            <Button class="disk-menu-button" v-if="showMenu.download">
              <Icon type="ios-download-outline" />
              下载
            </Button>
            <Button class="disk-menu-button" @click.stop="tryDeleteFiles()" v-if="showMenu.delete">
              <Icon type="ios-trash-outline" />
              刪除
            </Button>
          </ButtonGroup>
        </div>
        <div class="menu-line file-path">
          <div class="file-path-item" v-for="(data, idx) of pathList" :key="idx">
            <div class="arrow" v-if="data.id">></div>
            <div
              class="folder-name"
              :class="data.id === currentFolder ? 'current-folder' : ''"
              @click.stop="enterPath(data.id)">
              {{ data.name }}
            </div>
          </div>
        </div>
        <div class="menu-line file-line file-list-title">
          <Checkbox
            @on-change="changeAllChooseState"
            class="file-choose"
            :style="{
              visibility: fileList.length ? 'visible' : 'hidden'
            }"
            :value="chooseList.length > 0 && chooseList.length === fileList.length">
          </Checkbox>
          <div class="file-name">文件名</div>
          <div class="file-size">大小</div>
          <div class="update-time">修改日期</div>
        </div>
      </div>
      <div class="file-list">
        <div class="file-line file-list-content" v-for="file of fileList" :key="file.id">
          <Checkbox @on-change="changeChooseState(file, arguments)" class="file-choose" :value="isChoosed(file.id)"></Checkbox>
          <div class="file-name" @click.stop="clickFile(file)">
            <Icon
              class="file-name-icon"
              :class="file.folder ? 'folder' : 'file'"
              :type=" file.folder ? 'md-folder' : fileIcon(file.name)"/>
            <div class="file-name-content">{{ file.name }}</div>
            <div class="divide"></div>
            <div class="file-name-menus">
              <div class="menu-more file-name-menu" @mouseenter.stop="showMoreMenu(file)" @mouseleave.stop="hideMoreMenu" @click.stop="">
                <Icon class="menu-icon menu-more-icons" type="ios-more-outline"/>
                <div class="more-menu-items" v-if="showMenuId===file.id">
                  <div class="more-menu-item" @click.stop="showEditingNode(file)">重命名</div>
                  <div class="more-menu-item" @click.stop="tryDeleteFiles(file)">删除</div>
                  <div class="more-menu-item" @click.stop="copyUrl" v-if="!file.folder">
                    复制URL
                    <div class="file-url" v-show="false" v-click-copy="'文件URL已复制到剪贴板'">
                      {{getUrl(file.id)}}
                    </div>
                  </div>
                  <div class="more-menu-item" v-if="!file.folder">
                    <a
                      class="menu-download"
                      target="_blank"
                      :href="getUrl(file.id)"
                      :download="file.name">
                      下载
                      </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="file-size">
            {{ file.fileSize ? formatSize(file.fileSize) : '-' }}
          </div>
          <div class="update-time">
            {{ formatTime(file.updateTime) }}
          </div>
        </div>
        <div class="file-list-empty" v-if="!fileList.length && !loading">
          这里还没有任何文件的呢
        </div>
      </div>
    </div>
    <Modal
      :title="editingNode.title"
      v-model="editingNode.show">
      <Input ref="folderNameInput" v-model="editingNode.info.name" @on-enter="updateInfo"></Input>
      <div slot="footer">
        <Button type="primary" @click.stop="updateInfo">保存</Button>
        <Button @click.stop="clearInfo">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import network from '@/api/networkDisk.js'
import { BASE_URL } from '@/libs/config.js'
import { getToken } from '@/libs/util.js'

export default {
  name: 'network-disk',
  data () {
    return {
      fileList: [],
      editingNode: {
        show: false,
        title: '请输入文件夹名称',
        info: {
          name: '',
          id: '',
          parentId: ''
        }
      },
      chooseList: [],
      upLoadPath: BASE_URL + '/mvp-manager/file/upload',
      pathList: [],
      showMenuId: '',
      headers: {
        token: getToken()
      },
      typeInfo: {
        '[png|jpg|jpeg|bmp|svg]': 'md-image',
        'default': 'md-document'
      },
      loading: false
    }
  },
  computed: {
    currentFolder () {
      let list = this.pathList
      let data = list[list.length - 1]
      return data ? data.id : null
    },
    showMenu () {
      let menu = {
        delete: false,
        rename: false,
        download: false
      }
      if (this.chooseList.length) {
        menu.delete = true
      }
      if (this.chooseList.length === 1) {
        menu.rename = true
      }
      return menu
    }
  },
  watch: {
    currentFolder (newValue) {
      if (newValue === null) return
      this.refreshPage()
    },
    pathList () {
      window.sessionStorage.setItem('networkDiskPath', JSON.stringify(this.pathList))
    }
  },
  mounted () {
    let pathList = window.sessionStorage.getItem('networkDiskPath')
    if (pathList) {
      this.pathList = JSON.parse(pathList) || []
    } else {
      let data = {
        id: '',
        name: '全部文件'
      }
      this.enterFolder(data)
    }
  },
  methods: {
    refreshPage () {
      let params = {
        id: this.currentFolder
      }
      this.getFileList(params)
    },
    async getFileList (params = {}) {
      this.chooseList = []
      this.fileList = []
      this.loading = true
      let data = await network.getFileList(params)
      this.loading = false
      if (data) {
        this.fileList = data
        this.fileList.sort((a, b) => {
          if (a.folder === b.folder) return 0
          if (a.folder) return -1
          return 1
        })
      } else {
        this.$Message.error('获取失败')
      }
    },
    showEditingNode (data = {}) {
      this.editingNode.show = true
      this.editingNode.info = {
        name: data.name || '',
        id: data.id || null,
        parentId: this.currentFolder
      }
      this.$nextTick(() => {
        this.$refs.folderNameInput.focus()
      })
    },
    async updateInfo () {
      let params = this.editingNode.info
      let data = await network.createFolder(params)
      if (data) {
        this.refreshPage()
        this.clearInfo()
      } else {
        this.$Message.error('保存失败')
      }
    },
    clearInfo () {
      this.editingNode.show = false
      this.editingNode.info = {
        name: '',
        id: '',
        parentId: ''
      }
    },
    tryDeleteFiles (file) {
      let sText = '你确定删除所选文件吗？'
      this.$confirm(sText, this.deleteFiles, file)
    },
    async deleteFiles (file) {
      let fileInfo = {}
      if (file) {
        fileInfo[file.id] = 1
      } else {
        this.chooseList.forEach(item => {
          fileInfo[item.id] = 1
        })
      }
      let params = { ids: Object.keys(fileInfo) }
      let data = await network.deleteFiles(params)
      if (data) {
        this.$Message.success('刪除成功')
        if (file) {
          let idx = this.chooseList.findIndex(item => item.id === file.id)
          if (idx !== -1) {
            this.chooseList.splice(idx, 1)
          } else {
            this.chooseList = []
          }
        }
      } else {
        this.$Message.error('刪除失败')
      }
      this.refreshPage()
    },
    copyUrl (event) {
      let node = event.target.querySelector('.file-url')
      if (node) {
        node.click()
        node.style.display = 'none'
      }
    },
    formatTime (timeStamp) {
      let formatNum = (num) => {
        return num < 10 ? '0' + num : num
      }
      let date = new Date(timeStamp)
      let year = date.getFullYear()
      let month = formatNum(date.getMonth() + 1)
      let day = formatNum(date.getDate())
      let hour = formatNum(date.getHours())
      let minute = formatNum(date.getMinutes())
      let sText = `${year}-${month}-${day} ${hour}:${minute}`
      return sText
    },
    formatSize (size) {
      if (size < 1024) {
        return size + ' B'
      }
      size = size / 1024
      if (size < 1024) {
        return size.toFixed(1) + ' KB'
      }
      size = size / 1024
      if (size < 1024) {
        return size.toFixed(1) + ' MB'
      }
      size = size / 1024
      return size.toFixed(1) + ' GB'
    },
    fileIcon (filename) {
      let idx = filename.lastIndexOf('.')
      let type = 'default'
      if (idx !== -1) {
        type = filename.slice(idx + 1)
      }
      for (let key in this.typeInfo) {
        let regex = new RegExp(key, 'gi')
        if (regex.test(type)) {
          return this.typeInfo[key]
        }
      }
      return this.typeInfo.default
    },
    enterFolder (data) {
      if (!data.id) {
        this.pathList = []
      }
      this.pathList.push({
        id: data.id,
        name: data.name
      })
    },
    enterPath (id) {
      if (id === this.currentFolder) return
      let idx = this.pathList.findIndex(item => item.id === id)
      if (idx !== -1) {
        this.pathList.splice(idx + 1)
      }
    },
    clickFile (file) {
      if (file.folder) {
        this.enterFolder(file)
      }
    },
    showMoreMenu (file) {
      this.showMenuId = file.id
    },
    hideMoreMenu (id) {
      this.showMenuId = ''
    },
    handleSuccess (res, file, fileList) {
      this.$Message.success('上传成功')
      this.refreshPage()
    },
    handleFail (res, file, fileList) {
      this.$Message.error('上传失败')
      this.refreshPage()
    },
    changeChooseState (file, args) {
      let value = args[0]
      let index = this.chooseList.findIndex(item => item.id === file.id)
      if (value) {
        if (index === -1) {
          this.chooseList.push(file)
        }
      } else {
        if (index !== -1) {
          this.chooseList.splice(index, 1)
        }
      }
    },
    isChoosed (id) {
      let data = this.chooseList.find(item => item.id === id)
      if (data) return true
      return false
    },
    changeAllChooseState (value) {
      this.chooseList = []
      if (value) {
        this.chooseList.push(...this.fileList)
      }
    },
    getUrl (id) {
      return BASE_URL + '/mvp-manager/file/download/' + id
    }
  }
}
</script>

