<template>
  <div class="node-edit-board"
       ref="board">
    <div v-if="node"
         class="node-editor">
      <div v-show="node.title">
        <div v-show="tabs === 'edit'">
          <Form :label-width="180"
                ref="formNodeEdit"
                :model="node"
                :rules="ruleValidate">
            <Row :gutter="16"
                 class="edit-item">
              <Col span="16">
              <Form-item label="问题下的输入提示"
                         v-if="selectType !== 'common'">
                <Input v-model="node.tip"
                       style="width: 100%"></Input>
              </Form-item>
              </Col>
              <Col span="16">
              <Form-item label="关联标签">
                <tagSelector v-model="node.tagInfo" />
              </Form-item>
              </Col>
              <Col span="8">
              <Form-item label="自定义代码"
                         prop="customVar">
                <Input v-model="node.customVar"
                       style="width: 100%"></Input>
              </Form-item>
              </Col>
            </Row>
            <Row :gutter="16"
                 class="edit-item">
              <Col span="16">
              <Form-item label="输入方式">
                <ButtonGroup>
                  <Button @click="clickTypeButton('judge')"
                          :class="selectType === 'judge' ? 'editing' : '' ">判断</Button>
                  <Button @click="clickTypeButton('singleSelect')"
                          :class="selectType === 'singleSelect' ? 'editing' : '' ">单选</Button>
                  <Button @click="clickTypeButton('multiSelect')"
                          :class="selectType === 'multiSelect' ? 'editing' : '' ">多选</Button>
                  <Button @click="clickTypeButton('multiInput')"
                          :class="selectType === 'multiInput' ? 'editing' : '' ">多填</Button>
                  <Button @click="clickTypeButton('common')"
                          :class="selectType === 'common' ? 'editing' : '' ">项目或共用包</Button>
                  <Button @click="clickTypeButton('other')"
                          :class="selectType === 'other' ? 'editing' : '' ">单填</Button>
                </ButtonGroup>
                <Button type="default"
                        style="margin-left: 10px"
                        shape="circle"
                        icon="md-add"
                        @click="addCustomComponent()">添加自定义组件</Button>
              </Form-item>
              </Col>
              <Col span="4">
              <template v-for="(com, index) in componentAllList"
                        v-if="com.id === node.componentId && selectType !== 'common'">
                <div :key="index"
                     v-show="selectType !== 'multiInput' || (selectType === 'other' && com.uuid !== '00000000-0000-0000-0000-000000000024')">
                  必填 <i-switch v-model="node.mustFlag"></i-switch>
                </div>
              </template>
              </Col>
              <Col span="4">
              <div v-if="parentIsOption">
                追加 <i-switch v-model="node.additional"></i-switch>
              </div>
              </Col>
            </Row>
            <Row v-if="selectType === 'common'">
              <Form-item props="common"
                                label="">
              <Col span="12">
                <Tabs :value="node.crossState ? 't' : 'f'" :animated="false" @on-click="changeCommonTab($event, node)">
                    <TabPane label="当前项目" name="f">
                      <Col span="24">
                        <Form-item props="common"
                                  label="项目或共用包">
                          <chooseProject :style="{
                              width: '15rem'
                            }"
                                        :allowNull="true"
                                        v-model="node.nestProjectId"
                                        :excludeList="[node.projectId]"
                                        :clearAble="true"
                                        :rootId="rootProject">
                          </chooseProject>
                        </Form-item>
                        </Col>
                        <Col span="24">
                        <Form-item props="common"
                                  label="每次调用都重复做题">
                          <i-switch :disabled="!node.nestProjectId"
                                    v-model="node.repeatFlag" />
                        </Form-item>
                        </Col>
                        <Col span="24">
                        <Form-item props="common"
                                  label="使用标题为前缀">
                          <i-switch :disabled="!node.nestProjectId"
                                    v-model="node.nestProjectTitle" />
                        </Form-item>
                        </Col>
                    </TabPane>
                    <TabPane label="跨项目" name="t">
                      <Form-item props="common"
                                label="共用包英文名">
                        <Input v-model="node.crossProjectEnName" style="width: 200px;"></Input>
                      </Form-item>
                    </TabPane>
                </Tabs>
              </Col>
              </Form-item>
            </Row>
            <Row v-if="selectType === 'other'">
              <Col span="24">
              <Form-item label="输入方式">
                <selectComponent :filterable="true"
                                 :size="'default'"
                                 :transfer="false"
                                 :selectUUID="node"
                                 :id="'otherSelectComponent'"
                                 @on-change="inputSelectChange"
                                 @addOption="addCustomComponent"
                                 v-model="node.componentId">
                  <!-- <OptionGroup label="常规组件"> -->
                  <Option v-for="com in componentList"
                          v-if="!com.otherConfig || (com.otherConfig && com.otherConfig.indexOf('cuShow') === -1 && com.otherConfig.indexOf('display') === -1)"
                          :key="com.id"
                          :value="com.id">{{com.name}}</Option>
                  <!-- </OptionGroup> -->
                  <!-- <OptionGroup label="自定义组件" v-if="customComponentList.length"> -->
                  <Option v-for="com in customComponentList"
                          v-if="!com.otherConfig || (com.otherConfig && com.otherConfig.indexOf('cuShow') === -1 && com.otherConfig.indexOf('display') === -1)"
                          :key="com.id"
                          :value="com.id">{{com.name}}</Option>
                  <!-- </OptionGroup> -->
                </selectComponent>
              </Form-item>
              </Col>
            </Row>
            <template v-if="showOptions">
              <div class="options-table"
                   ref="optionTable">
                <Row class="options-title"
                     :gutter="16">
                  <Col span="2">序号</Col>
                  <Col span="5">选项标题</Col>
                  <Col :span="selectType === 'multiSelect' || selectType === 'multiInput' ? '5' : '6'">选择或填写提示</Col>
                  <Col span="4"
                       v-if="selectType === 'judge' || selectType === 'singleSelect' || selectType === 'multiSelect'">选项类型</Col>
                  <Col span="5"
                       v-show="showSelectType">输入方式</Col>
                  <Col v-if="selectType === 'multiSelect'"
                       span="1">唯一</Col>
                  <Col span="1">必填</Col>
                  <Col span="3">自定义代码</Col>
                  <Col span="3"
                       v-if="selectType !== 'judge'">操作</Col>
                </Row>
                <draggable v-model="node.children"
                           :options="{handle: '.index', dragClass: 'dragging'}"
                           @start="dragging = true"
                           @end="dragging = false">
                  <Row class="options-item"
                       v-for="(option, index) in node.children"
                       v-if="(selectType !== 'judge' || index < 2) && option.type === 2"
                       :key="index"
                       :gutter="10">
                    <Col span="2"
                         class="index">#{{index + 1}} {{ option.code ? '(' + option.code + ')' : '' }}</Col>
                    <Col span="5">
                    <Input @on-keyup="checkKeyUp($event, index, 1)"
                           :class="'focus__' + index + '__1'"
                           placeholder="请输入标题"
                           type="text"
                           v-model="option.title" />
                    </Col>
                    <Col :span="selectType === 'multiSelect' || selectType === 'multiInput' ? '5' : '6'">
                    <Input @on-keyup="checkKeyUp($event, index, 2)"
                           :class="'focus__' + index + '__2'"
                           placeholder="选项描述"
                           type="text"
                           v-model="option.tip" />
                    </Col>
                    <Col span="4"
                         style="text-align: left"
                         v-show="selectType === 'judge' || selectType === 'singleSelect' || selectType === 'multiSelect'">
                    <Select :filterable="true"
                            :size="'default'"
                            :transfer="false"
                            clearable
                            placeholder="默认（选项）"
                            @on-change="changeComponentType($event, option, index)"
                            v-model="option.point">
                      <Option v-for="(com, index) in componentAllList"
                              :key="index"
                              v-if="com.uuid === '00000000-0000-0000-0000-000000000011' || com.uuid === '00000000-0000-0000-0000-000000000015' || com.uuid === '00000000-0000-0000-0000-000000000017' || com.uuid === '00000000-0000-0000-0000-000000000001'"
                              :value="com.id">{{ com.name }}</Option>
                    </Select>
                    </Col>
                    <Col span="5"
                         style="text-align: left">
                    <selectComponent :filterable="true"
                                     :size="'default'"
                                     :transfer="false"
                                     :id="'optionSelectComponent-' + index"
                                     :selectUUID="option"
                                     v-if="showSelectType"
                                     @on-change="inputSelectChange($event, option)"
                                     @addOption="addCustomComponent"
                                     v-model="option.componentId">
                      <!-- <OptionGroup label="常规组件"> -->
                      <Option v-for="com in componentList"
                              v-if="!com.otherConfig || (com.otherConfig && com.otherConfig.indexOf('cuShow') === -1 && com.otherConfig.indexOf('display') === -1)"
                              :key="com.id"
                              :value="com.id">{{com.name}}</Option>
                      <!-- </OptionGroup> -->
                      <!-- <OptionGroup label="自定义组件" v-if="customComponentList.length"> -->
                      <Option v-for="com in customComponentList"
                              v-if="!com.otherConfig || (com.otherConfig && com.otherConfig.indexOf('cuShow') === -1 && com.otherConfig.indexOf('display') === -1)"
                              :key="com.id"
                              :value="com.id">{{com.name}}</Option>
                      <!-- </OptionGroup> -->
                    </selectComponent>
                    </Col>
                    <Col v-if="selectType === 'multiSelect'"
                         span="1">
                    <Checkbox v-model="option.unique"
                              @on-change="onlyChange(node.children, option)" />
                    </Col>
                    <Col span="1">
                    <Checkbox v-model="option.mustFlag"
                              :disabled="selectType !== 'multiInput' && !new Boolean(option.point).valueOf()" />
                    </Col>
                    <Col span="3">
                    <Input @on-keyup="checkKeyUp($event, index, 3)"
                           :class="'focus__' + index + '__3'"
                           placeholder="请输入"
                           type="text"
                           v-model="option.customVar" />
                    </Col>
                    <Col span="3"
                         v-if="selectType !== 'judge'">
                    <Button @click="deleteOption(option, index)">删除</Button>
                    </Col>
                  </Row>
                </draggable>
              </div>
              <Button type="primary"
                      style="margin-top: 10px; float: left;"
                      shape="circle"
                      icon="md-add"
                      v-if="selectType !== 'judge'"
                      @click="addOption">添加选项 (F2)</Button>
            </template>
          </Form>
        </div>
        <div v-show="tabs === 'config'">
          <Row>
            <Col span="24">
            <Row :gutter="16"
                 class="edit-item">
              <Col span="2">输入提示</Col>
              <Col span="22">{{ node.tip }}</Col>
            </Row>
            <Row :gutter="16"
                 class="edit-item">
              <Col span="2">输入方式</Col>
              <template v-for="(com, index) in componentAllList"
                        v-if="com.id === node.componentId">
                <Col span="10"
                     :key="index">{{ (com.uuid === '00000000-0000-0000-0000-000000000024' ? '小报告' : inputType) || '&nbsp;' }}</Col>
              </template>
              <Col span="2">追加题</Col>
              <Col span="10"> {{ node.additional ? '是' : '否' }}
              </Col>
            </Row>
            <!-- <Row :gutter="16"
                    class="edit-item">
                <Col span="2">关联标签</Col>
                <Col span="22">
                  <tagSelector v-model="node.tagInfo" />
                </Col>
              </Row> -->
            <Row :gutter="16"
                 class="edit-item">
              <Col span="2">问题操作</Col>
              <Col span="22">
              <ButtonGroup>
                <template v-for="(com, index) in componentAllList"
                          v-if="com.id === node.componentId">
                  <Button :key="'a' + index"
                          @click="tabTo('edit', true)">编辑选项</Button>
                  <Button :key="'b' + index"
                          @click="tabTo('note')">编辑说明</Button>
                  <Button :key="'c' + index"
                          :type="checkCode(node.showCondition) ? 'success' : 'default'"
                          @click="controlQuestion('显示逻辑', 'showCondition')">显示逻辑</Button>
                  <Button :key="'d' + index"
                          :type="checkCode(node.translationCondition) ? 'success' : 'default'"
                          @click="controlQuestion('翻译逻辑', 'translationCondition')">翻译逻辑</Button>
                  <Button :key="'e' + index"
                          :type="checkCode(node.jumpCondition) ? 'success' : 'default'"
                          :disabled="com.uuid === '00000000-0000-0000-0000-000000000024'"
                          @click="controlQuestion('结束条件', 'jumpCondition')">结束条件</Button>
                  <Button :key="'f' + index"
                          :type="checkCode(node.automateCondition) ? 'success' : 'default'"
                          :disabled="com.uuid === '00000000-0000-0000-0000-000000000024'"
                          @click="controlQuestion('自动填写', 'automateCondition')">自动填写</Button>
                  <Button :key="'g' + index"
                          :type="checkCode(node.permissionCondition) ? 'success' : 'default'"
                          :disabled="com.uuid === '00000000-0000-0000-0000-000000000024'"
                          @click="controlQuestion('权限配置', 'permissionCondition')">权限配置</Button>
                  <Button :key="'h' + index"
                          :type="checkCode(node.validateCondition) ? 'success' : 'default'"
                          :disabled="com.uuid === '00000000-0000-0000-0000-000000000024'"
                          @click="controlQuestion('验证逻辑', 'validateCondition')">验证逻辑</Button>
                  <!-- <Button :key="'i' + index" @click="controlQuestion('关联标签', 'showCondition')">关联标签</Button> -->
                  <Button :key="'j' + index"
                          v-if="com.uuid !== '00000000-0000-0000-0000-000000000024'"
                          :disabled="!(selectType === 'other' || selectType === 'singleSelect' || selectType === 'judge' || selectType === 'multiSelect')"
                          @click="controlQuestion('输入方式', 'componentInputType', node)">输入方式</Button>
                  <Button :key="'k' + index"
                          v-if="com.uuid === '00000000-0000-0000-0000-000000000024'"
                          @click="controlQuestion('小报告内容', 'componentInputType', node)">小报告内容</Button>
                  <Button :key="'l' + index"
                          v-if="selectType === 'multiSelect'"
                          @click="controlQuestion('排异条件', 'rejectionCondition')">排异条件</Button>
                </template>
              </ButtonGroup>
              </Col>
            </Row>
            <!-- <Row :gutter="16"
                  class="edit-item">
              <Col span="2">关联标签</Col>
              <Col span="22">
              <Tag v-for="item in count"
                    :key="item"
                    :name="item"
                    closable
                    @on-close="handleClose2">标签{{ item + 1 }}</Tag>
              <Button icon="ios-plus-empty"
                      type="dashed"
                      size="small"
                      @click="handleAdd">添加标签</Button>
              </Col>
            </Row> -->
            </Col>
            <Col span="24"
                 class="view-table-area edit-item-control-area options-table"
                 v-if="selectType !== 'common' && selectType !== 'other'">
            <Row class="options-title"
                 :gutter="16">
              <Col span="2">序号/代码</Col>
              <Col :span="selectType === 'multiSelect' ? '6' : '7'">选项标题</Col>
              <Col span="3">选项描述</Col>
              <Col span="3">输入方式</Col>
              <Col v-if="selectType === 'multiSelect'"
                   span="1">唯一</Col>
              <Col span="1">必填</Col>
              <Col span="2">自定义代码</Col>
              <Col span="6">操作</Col>
            </Row>
            <Row class="options-item"
                 v-for="(option, index) in viewTableData"
                 v-if="option.code && (selectType !== 'judge' || index < 2) && option.type !== 1"
                 :key="index"
                 :gutter="10">
              <Col span="2">#{{index + 1}} {{ option.code ? '(' + option.code + ')' : '' }}</Col>
              <Col :span="selectType === 'multiSelect' ? '6' : '7'"
                   style="text-align: left">
              {{ option.title || '&nbsp;' }}
              </Col>
              <Col span="3">
              {{ option.tip || '&nbsp;' }}
              </Col>
              <Col span="3">
                <span @click="editComponentByComponentId(option.componentId)" style="cursor: pointer; user-select: none">{{ getComponentNameByComponentId(option.componentId) || '&nbsp;' }}</span>
              </Col>
              <Col v-if="selectType === 'multiSelect'"
                   span="1">
              {{ option.unique ? '是' : '否' }}
              </Col>
              <Col span="1">
              {{ option.mustFlag ? '是' : '否' }}
              </Col>
              <Col span="2">
              {{ option.customVar || '&nbsp;' }}
              </Col>
              <Col span="6"
                   style="text-align: left">
              <template v-for="(item, inx) in controls">
                <Button :type="checkCode(option[item.key]) ? 'success' : 'default'"
                        :disabled="!(item.key !== 'componentInputType' || (item.key === 'componentInputType' && selectType === 'multiInput')) || !(item.key !== 'validateCondition' || (item.key === 'validateCondition' && selectType === 'multiInput'))"
                        :key="inx"
                        class="table-control"
                        @click="controlQuestion(item.title, item.key, option)">{{ item.title }}</Button>
              </template>
              <template v-if="selectType === 'multiSelect'">
                <Button class="table-control"
                        :type="checkCode(option.rejectionCondition) ? 'success' : 'default'"
                        @click="controlQuestion('排异条件', 'rejectionCondition', option)">排异条件</Button>
              </template>
              </Col>
            </Row>
            <Row class="options-item"
                 v-if="viewTableData.length < 1">
              暂无数据
            </Row>
            </Col>
          </Row>
        </div>
        <div v-show="tabs === 'note'">
          <h2>说明</h2>
          <p style="margin-bottom: 10px">为什么添加该节点?具体做些什么事情？</p>
          <Input v-model="node.remark"
                 type="textarea"
                 :autosize="{minRows: 3, maxRows: 10}"
                 placeholder="请输入说明..."></Input>
        </div>
      </div>
      <Modal v-modal-drag
             v-modal-resize
             v-model="control.show"
             :mask-closable="false"
             width="980"
             :dragable="true">
        <p slot="header">
          {{ control.title }}
        </p>
        <Row :gutter="16"
             class="edit-item"
             style="cursor: pointer"
             @click="save(control)"
             v-if="control.node">
          <Col span="2">标题</Col>
          <Col span="10">
          <span style="font-weight: 600">{{ control.node.code }}</span> {{ control.node.title }}</Col>
          <Col span="2">自定义代码</Col>
          <Col span="10">{{ control.node.customVar }}</Col>
        </Row>
        <Row :gutter="16"
             style="margin-top: 10px">
          <Col span="24">
          <Input v-model="control.contentNote"
                 type="textarea"
                 :autosize="{minRows: 3,maxRows: 10}"
                 placeholder="请输入代码说明..."></Input>
          </Col>
          <Col span="24"
               ref="controlEditorArea"
               style="margin-top: 10px">
          <Spin size="large"
                fix
                v-if="!ControlEditorLoaded"></Spin>
          <Editor v-if="showControlEditor"
                  :sourceType="1"
                  ref="controlEditor"
                  v-model="control.content"
                  :projectId="rootProject"
                  @editorMounted="ControlEditorMounted">
          </Editor>
          </Col>
        </Row>
        <div slot="footer">
          <Button class="btn-main"
                  @click="save(control)">保存</Button>
        </div>
      </Modal>
    </div>

    <Modal v-model="showComponentInputType"
           :mask-closable="false"
           :loading="true"
           :closable="true"
           :draggable="false"
           @on-cancel="ComponentInputTypeCancel"
           width="85%">
      <p slot="header">
        输入方式配置
      </p>
      <template v-if="showComponentEditor">
        <!-- 判断是否是多填的多填或单填的多填 -->
        <template v-if="ComponentInputTypeNode.componentInfo.items[0].uuid === '00000000-0000-0000-0000-000000000012'">
          <div v-for="(it, inx) in ComponentInputTypeNode.componentInfo.items"
               :key="inx">
            <template v-if="inx > 0">
              <h2>{{ it.name }}</h2>
              <configComponent :componentData="ComponentInputTypeNode.componentInfo.items[inx]"
                               :componentInfoData="ComponentInputTypeNode.componentInfo"
                               :componentInfoDataInx="inx"
                               :cType="1"
                               :aType="true"
                               :useItem="true"
                               @updateComponentData="updateComponentData($event, ComponentInputTypeNode)"
                               :key="inx" />
            </template>
          </div>
        </template>
        <!-- 判断是否是自定义的单选、判断、多选 -->
        <template v-else-if="ComponentInputTypeNode.componentInfo.type === 2 && (ComponentInputTypeNode.componentInfo.items[0].uuid === '00000000-0000-0000-0000-000000000005' || ComponentInputTypeNode.componentInfo.items[0].uuid === '00000000-0000-0000-0000-000000000007' || ComponentInputTypeNode.componentInfo.items[0].uuid === '00000000-0000-0000-0000-000000000013')">
          <div v-for="(it, inx) in ComponentInputTypeNode.componentInfo.items"
               :key="inx">
            <template v-if="inx > 0">
              <h2>{{ it.name }}</h2>
              <configComponent :componentData="ComponentInputTypeNode.componentInfo.items[inx]"
                               :componentInfoData="ComponentInputTypeNode.componentInfo"
                               :componentInfoDataInx="inx"
                               :cType="1"
                               :aType="true"
                               :useItem="true"
                               @updateComponentData="updateComponentData($event, ComponentInputTypeNode)"
                               :key="inx" />
            </template>
          </div>
        </template>
        <configComponent :componentDefaultData="ComponentInputTypeNode.componentInfo"
                         :componentData="ComponentInputTypeNode.componentInfo"
                         :cType="1"
                         v-else
                         @updateComponentData="updateComponentData($event, ComponentInputTypeNode)" />
      </template>
      <Spin size="large"
            v-else
            fix></Spin>
      <div class="config-modal-footer"
           slot="footer">
        <Row type="flex"
             justify="end">
          <Button type="primary"
                  @click="ComponentInputTypeOk">
            保存
          </Button>
        </Row>
      </div>
    </Modal>

    <customComponentEdit v-model="showCustomComponent"
                         :editItem="editItem"
                         :judgeComponent="judgeComponent"
                         :org="currentCompany"
                         :team="'3-' + projectId"
                         @updateList="updateCustomComponentList"
                         :nowSelectUUID="nowSelectUUID"
                         @selectNewId="selectNewId"></customComponentEdit>
  </div>
</template>

<script>
import Editor from '@/components/editor'
import Common from '@/api/common.js'
import { EmitModel } from '@/libs/EmitModel'
import configComponent from '@components/configComponent'
import chooseProject from '@/components/choose-project'
import selectComponent from '@components/selectComponent'
import draggable from 'vuedraggable'
import customComponentEdit from '@/components/custom-component/edit'
import tagSelector from '../../components/tag-selector'
import { getCompontent } from '@/api/component'

export default {
  name: 'node-edit-board',
  components: {
    Editor,
    configComponent,
    chooseProject,
    draggable,
    customComponentEdit,
    selectComponent,
    tagSelector
  },
  props: {
    value: Boolean,
    node: Object,
    nodeManager: Object,
    isFirst: {
      type: Boolean,
      required: false,
      default: false
    },
    docID: {
      type: String | Number
    },
    projectId: String
  },
  watch: {
    'node.nestProjectId' () {
      if (!this.node.nestProjectId) {
        this.node.nestProjectTitle = false
        this.node.repeatFlag = false
      }
    }
  },
  computed: {
    inputType () {
      switch (this.selectType) {
        case 'singleSelect':
          return '单选'
        case 'multiSelect':
          return '多选'
        case 'multiInput':
          return '多填'
        case 'judge':
          return '判断'
        case 'common':
          return '共用包'
        default:
          return '其它'
      }
    },
    componentList () {
      return this.$store.state.component.list
    },
    customComponentList () {
      return this.$store.state.component.cList
    },
    componentAllList () {
      return [...this.$store.state.component.list, ...this.$store.state.component.cList]
    },
    componentQuestion () {
      return this.$store.state.component.question
    },
    componentOptions () {
      return this.$store.state.component.options
    },
    parentIsOption () {
      const tmp = this.nodeManager.nodeMap.get(this.node.parentUuid)
      if (tmp) {
        return +tmp.type === 2
      }
      return false
    },
    currentCompany () {
      return this.$store.state.user.currentCompany
    }
  },
  data () {
    const validateCustomVar = (rule, value, callback) => {
      const reg = /^[a-zA-Z\$_][a-zA-Z\d_]*$/ // eslint-disable-line
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error('变量名需以字母、$或_开头'))
      }
    }
    return {
      tabs: 'edit',
      rootProject: 0,
      columnsEditList: [],
      showModule: true,
      codeDraft: '',
      codeType: 'common',
      showEditor: false,
      showComponentEditor: false,
      showControlEditor: false,
      EditorLoaded: false,
      ControlEditorLoaded: false,
      updateTimer: null,
      selectType: 'judge',
      count: [0, 1, 2],
      viewTableData: [],
      showRejectionConditionEditor: false,
      control: {
        show: false,
        title: '显示逻辑',
        key: 'showCondition',
        content: '',
        contentNote: '',
        node: {}
      },
      controls: [
        {
          title: '显示逻辑',
          key: 'showCondition'
        },
        {
          title: '翻译逻辑',
          key: 'translationCondition'
        },
        {
          title: '结束条件',
          key: 'jumpCondition'
        },
        // {
        //   title: '自动填写',
        //   key: 'automateCondition'
        // },
        {
          title: '权限配置',
          key: 'permissionCondition'
        },
        {
          title: '验证逻辑',
          key: 'validateCondition'
        },
        // {
        //   title: '关联标签',
        //   key: 'label'
        // },
        {
          title: '输入方式',
          key: 'componentInputType'
        }
      ],
      move: {
        show: false,
        node: {},
        action: 3,
        moveAndCreateCommonNode: false,
        new: null,
        exists: null,
        existsList: [],
        children: null,
        childrenList: []
      },
      ruleValidate: {
        customVar: [
          { validator: validateCustomVar, trigger: 'change' }
        ],
        title: [
          { required: true, message: '标题不能为空', trigger: 'change' }
        ]
      },
      showSelectType: false,
      showOptions: false,
      existsCommonList: [],
      showCustomComponent: false,
      dragging: false, // 拖动选项
      showComponentInputType: false,
      ComponentInputTypeNode: null,
      nowSelectUUID: null,
      addnum: 10,
      dataItem: null,
      editItem: null,
      tabsClose: false,
      judgeComponent: null
    }
  },
  created () {
    // this.viewTableData = this.node.children
    this.initData()
    this.$bus.$emit('get-root-project', (projectId) => {
      this.rootProject = projectId
    })
  },
  beforeDestroy () {
  },
  mounted () {
    document.body.onkeydown = (e) => {
      if (e.keyCode === 113) {
        if (this.addOption) this.addOption()
      }
    }
    if (document.getElementById('nodeTitle') && document.getElementById('nodeTitle').childNodes[6]) {
      document.getElementById('nodeTitle').childNodes[6].focus()
    }
  },
  methods: {
    changeCommonTab (name, node) {
      if (name === 't') {
        node.crossState = true
      } else {
        node.crossState = false
      }
    },
    onlyChange (items, item) {
      const tmp = item.unique
      for (const n in items) {
        items[n].unique = false
      }
      item.unique = tmp
      // items.find(it => it.id === item.id).unique = true
      // console.log(items)
    },
    async editCustomComponent (item) {
      this.editItem = await getCompontent(item.id) // 获取当前选择的组件的全部信息
      this.showCustomComponent = true
    },
    changeComponentType (e, val, index) {

    },
    checkCode (code) {
      if (code) {
        return code.replace(/<\?/g, '').replace(/\?>/g, '').replace(/\s/g, '') !== ''
      } else {
        return false
      }
    },
    getComponentNameByComponentId (componentId) {
      for (const item of this.componentAllList) {
        if (item.id === componentId) {
          return item.name
        }
      }

      return null
    },
    editComponentByComponentId (componentId) {
      this.$Message.info('很遗憾，暂不允许通过此处直接编辑组件。请进入自定义组件列表进行操作。')
      // console.log('editComponentByComponentId')
      // for (const item of this.componentList) {
      //   if (item.id === componentId) {
      //     this.editCustomComponent(item)
      //   }
      // }
    },
    selectNewId ({ componentId, selectUUID }) { // 创建自定义组件后，自动选择创建的组件
      // console.log(selectUUID)
      if (selectUUID) {
        selectUUID.componentId = componentId
      }
    },
    async addCustomComponent (selectUUID) {
      this.editItem = null
      this.judgeComponent = await getCompontent(this.componentQuestion['judge'].id)
      this.nowSelectUUID = selectUUID
      this.showCustomComponent = true
    },
    ComponentInputTypeOk () {
      this.showComponentEditor = false
      this.showComponentInputType = false
      if (this.ComponentInputTypeNode.newComponentData) {
        this.ComponentInputTypeNode.componentInfo = this.ComponentInputTypeNode.newComponentData
      }
      // this.$emit('saveToGetCode', false, false)
      this.$emit('saveComponentInfo', [{
        id: this.node.id,
        componentId: this.node.componentId,
        componentInfo: this.node.componentInfo
      }])
    },
    ComponentInputTypeCancel () {
      this.showComponentEditor = false
      this.showComponentInputType = false
    },
    updateComponentData (val, node) {
      this.newComponentData = val
      this.$set(node, 'newComponentData', this.newComponentData)
    },
    updateCustomComponentList () {
      this.$emit('updateCustomComponentList')
    },
    async moveSave () {
      const move = this.move
      console.log(move)
      const r = await Common.setProject.call(this, {
        nodeId: move.node.id,
        action: move.action,
        projectCode: move.action === 1 ? move.exists : move.children,
        sharePackageName: move.new
      })
      move.show = false
      console.log(r)
    },
    controlQuestion (title, key, node = this.node) {
      if (key === 'componentInputType') {
        this.ComponentInputTypeNode = node
        console.log(this.ComponentInputTypeNode)
        this.showComponentInputType = true

        setTimeout(() => { // 别删！别以为这三行代码毫无意义😎
          this.showComponentEditor = true
        }, 1000)
        return
      }

      console.log(node)
      // if (this.$refs.controlEditor) {
      //   const editor = this.$refs.controlEditor.getMonaco()
      //   editor.setValue(node[key] || '')
      // }
      this.control.title = title
      this.control.node = node
      this.control.key = key
      this.control.content = (node[key] && node[key].replace(/\s+/g, '') !== '') ? node[key] : '<?\n\n?>\n'
      this.control.contentNote = node[key + 'Note']
      this.control.show = true
      this.showControlEditor = true
    },
    save (control) {
      control.node[control.key] = control.content
      control.node[control.key + 'Note'] = control.contentNote
      control.show = false
      this.$emit('saveCode', {
        id: control.node.id,
        conditionType: control.key,
        [control.key]: control.content,
        [control.key + 'Note']: control.contentNote
      })
      // this.$emit('saveToGetCode', false, true)
    },
    addOption (e, addnum) {
      let componentId = this.componentOptions.input.id
      if (this.node.componentId === this.componentQuestion['singleSelect'].id) {
        componentId = this.componentOptions['singleSelect-option'].id
      } else if (this.node.componentId === this.componentQuestion['multiSelect'].id) {
        componentId = this.componentOptions['multiSelect-option'].id
      } else if (this.node.componentId === this.componentQuestion['judge'].id) {
        componentId = this.componentOptions['judge-option'].id
      }

      this.$emit('addOption', new EmitModel(
        {
          name: 'addOption',
          event: e,
          vn: this.$vnode,
          params: {
            node: this.node,
            componentId
          }
        }
      ))
    },
    deleteOption (option, index, e) {
      if (this.node.children) {
        this.$emit('deleteOption', new EmitModel(
          {
            name: 'deleteOption',
            event: e,
            vn: this.$vnode,
            params: {
              node: this.node.children.splice(index, 1)[0]
            }
          }
        ))
      }
    },
    editorMounted () {
      this.EditorLoaded = true
    },
    ControlEditorMounted () {
      this.ControlEditorLoaded = true
    },
    tabTo (name, close = false, toSave = true) {
      this.tabs = name
      this.tabsClose = close
      if (name === 'config') {
        if (toSave) {
          this.$emit('saveToGetCode', false, true)
        }
        this.viewTableData = this.node.children
      } else if (name === 'edit') {
        this.initOptions()
      }
    },
    initOptions () {
      const tmp = 4 - this.node.children.length

      if (tmp > 0) {
        let componentId = this.componentOptions.input.id
        if (this.node.componentId === this.componentQuestion['singleSelect'].id) {
          componentId = this.componentOptions['singleSelect-option'].id
        } else if (this.node.componentId === this.componentQuestion['multiSelect'].id) {
          componentId = this.componentOptions['multiSelect-option'].id
        } else if (this.node.componentId === this.componentQuestion['judge'].id) {
          componentId = this.componentOptions['judge-option'].id
        }
        for (let n = 0; n < tmp; n++) {
          this.$emit('addOption', new EmitModel(
            {
              name: 'addOption',
              event: null,
              vn: this.$vnode,
              params: {
                node: this.node,
                componentId
              }
            }
          ))
        }
      }
    },
    clickTypeButton (type, old) {
      this.selectType = type
      if (type !== 'common') {
        this.node.nestProjectId = null
        this.node.crossState = false
      }
      switch (type) {
        case 'judge':
          this.node.componentId = this.componentQuestion[type].id
          // this.node.componentInfo = this.componentQuestion[type]
          for (const n in this.node.children) {
            this.node.children[n].componentId = this.componentOptions['judge-option'].id
          }
          this.showSelectType = false
          this.showOptions = true
          break
        case 'singleSelect':
          this.node.componentId = this.componentQuestion[type].id
          // this.node.componentInfo = this.componentQuestion[type]
          for (const n in this.node.children) {
            this.node.children[n].componentId = this.componentOptions['singleSelect-option'].id
          }
          this.showSelectType = false
          this.showOptions = true
          break
        case 'multiSelect':
          this.node.componentId = this.componentQuestion[type].id
          // this.node.componentInfo = this.componentQuestion[type]
          for (const n in this.node.children) {
            this.node.children[n].componentId = this.componentOptions['multiSelect-option'].id
          }
          this.showSelectType = false
          this.showOptions = true
          this.showEditor = true
          break
        case 'multiInput':
          this.node.componentId = this.componentQuestion[type].id
          // this.node.componentInfo = this.componentQuestion[type]
          for (const n in this.node.children) {
            if (this.node.children[n].componentId === null || this.node.children[n].componentId === '' || this.node.children[n].componentId === this.componentOptions['judge-option'].id || this.node.children[n].componentId === this.componentOptions['singleSelect-option'].id || this.node.children[n].componentId === this.componentOptions['multiSelect-option'].id) {
              this.node.children[n].componentId = this.componentOptions.input.id
            }
          }
          this.showSelectType = true
          this.showOptions = true
          break
        case 'common':
          this.showSelectType = false
          this.showOptions = false
          break
        case 'other':
          this.showSelectType = false
          this.showOptions = false
          // this.node.componentInfo = this.componentOptions.input
          if (this.node.componentId === null || this.node.componentId === '' || this.node.componentId === this.componentQuestion['multiSelect'].id || this.node.componentId === this.componentQuestion['singleSelect'].id || this.node.componentId === this.componentQuestion['judge'].id || this.node.componentId === this.componentQuestion['multiInput'].id) {
            this.node.componentId = this.componentOptions.input.id
          }
          break
      }
    },
    inputSelectChange (id, node = this.node) {
      // for (const n in this.componentList) {
      //   if (this.componentList[n].id === id) {
      //     node.componentInfo = this.componentList[n]
      //     break
      //   }
      // }
    },
    clickButton (button) {
      this.codeType = button.type
    },
    chooseMenu (idx) {
      this.choose = idx
    },
    initData () {
      if (this.node.nestProjectId) { // 如果是共用包
        this.selectType = 'common'
        this.clickTypeButton(this.selectType)
        return
      }

      if (this.node.componentId === null) {
        this.node.componentId = this.componentQuestion['judge'].id
        this.selectType = 'judge'
        this.clickTypeButton(this.selectType)
        return
      }

      this.tabTo('config', false, false)

      const tmp = this.node.componentId
      let tmp2 = false
      for (const n in this.componentQuestion) {
        if (this.componentQuestion[n] && this.componentQuestion[n].id === tmp) {
          this.selectType = n
          tmp2 = true
          break
        }
      }

      if (!tmp2) {
        this.selectType = 'other'
      }

      this.clickTypeButton(this.selectType, true)
    },
    handleAdd () {
      if (this.count.length) {
        this.count.push(this.count[this.count.length - 1] + 1)
      } else {
        this.count.push(0)
      }
    },
    handleClose2 (event, name) {
      const index = this.count.indexOf(name)
      if (this.count) this.count.splice(index, 1)
    },
    updateChild (value, data, key) {
      if (this.isFirst) {
        return
      }

      if (data) {
        data[key] = value ? 1 : 0
      }

      if (this.updateTimer) {
        clearTimeout(this.updateTimer)
        this.updateTimer = null
      }
      const updatedNodes = [this.node]
      this.updateTimer = setTimeout(() => {
        this.$emit('update', { name: 'update', data: { nodes: updatedNodes } })
        console.log('更新了节点')
      }, 600)
    },
    checkKeyUp (event, index, num) {
      if (this.$bus.inputing) return
      if (!event.ctrlKey) return
      let className = ''
      switch (event.key) {
        case 'ArrowUp':
          className = 'focus__' + (index - 1) + '__' + num
          break
        case 'ArrowDown':
          className = 'focus__' + (index + 1) + '__' + num
          break
        case 'ArrowLeft':
          className = 'focus__' + index + '__' + (num - 1)
          break
        case 'ArrowRight':
          className = 'focus__' + index + '__' + (num + 1)
          break
        case 'Enter':
          className = 'focus__' + (index + 1) + '__1'
          break
      }
      if (!className) return
      let node = this.$refs.optionTable.querySelector('.' + className)
      if (!node && event.key === 'Enter') {
        this.addOption(event)
      }
      this.$nextTick(() => {
        node = this.$refs.optionTable.querySelector('.' + className)
        if (node) {
          node.__vue__.focus()
        }
      })
    }
  }
}
</script>

