import usualApi from './util'
import Request from '@/libs/request'

export default {
  getFileList (params) {
    return usualApi(Request.post, '/mvp-manager/file/list', params, [])
  },
  createFolder (params) {
    return usualApi(Request.post, '/mvp-manager/file/save', params)
  },
  deleteFiles (params) {
    return usualApi(Request.post, '/mvp-manager/file/delete', params, {})
  }
}
