<template>
  <div class="ivu-input-wrapper ivu-input-type">
    <ul class="ivu-input tag-list">
      <li v-for="(item, index) in value" :key="index"><Tag color="success" :closable="true" @on-close="removeTag(index)">{{item.name}}</Tag></li>
      <Button :disabled="disabled" style="float: left" type="primary" size="small" icon="md-add" @click.native="showTagModal = true; getTagList()" />
    </ul>

    <Modal
      width="420"
      title="选择标签"
      v-model="showTagModal"
      :mask-closable="false"
      @on-ok="selectOk"
      @on-cancel="selectCancel"
      style="z-index: 999999999"
      >
      <Row style="margin-bottom: 0.5rem">
        <Input v-model="keyword" @keyup.native.enter="getTagList">
          <Button slot="append" icon="md-search" @click.native="getTagList" />
        </Input>
      </Row>
      <Table
        height="420"
        ref="table"
        :columns="columns"
        :data="tagData"
        @on-selection-change="selectionChange"/>
    </Modal>
  </div>
</template>

<script>
import { getTagList } from '@/api/tag'

export default {
  name: 'tag-selector',
  props: {
    value: {
      type: Array,
      required: true
    },
    professionalId: {
      type: Number | String,
      required: false
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  model: {
    prop: 'value',
    event: 'select'
  },
  data () {
    return {
      selected: [],
      tmpSelect: [],
      tagData: [],
      tagMap: new Map(),
      showTagModal: false,
      keyword: '',
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: '标签名称',
          key: 'name'
        }
      ]
    }
  },
  created () {
    this.getTagList()
  },
  watch: {
    value () {
      this.selected = this.value
    }
  },
  computed: {
  },
  methods: {
    getTagList () {
      getTagList({
        name: this.keyword || null,
        professionalId: this.professionalId || null
      }).then(resp => {
        if (resp.response.success) {
          for (const item of resp.data) {
            this.tagMap.set(item.id, item)
          }
          this.tagData = resp.data
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    selectionChange (selection) {
      this.tmpSelect = selection
    },
    removeTag (index) {
      this.selected.splice(index, 1)
      this.$emit('on-ok', this.selected)
    },
    selectOk () {
      if (this.selected.length === 0 && this.tmpSelect.length === 0) {
        return
      }
      for (const item of this.tmpSelect) {
        if (this.selected.length) {
          if (!this.selected.find(s => s.id === item.id)) {
            this.selected.push(item)
          }
        } else {
          this.selected.push(item)
        }
      }
      this.$emit('select', this.selected)
      this.$refs.table.selectAll(false)
    },
    selectCancel () {
      this.$refs.table.selectAll(false)
    },
    remakeTags () {
    }
  }
}
</script>

<style scoped lang="stylus">
.tag-list
  cursor pointer
  min-height 32px
  height auto
  list-style none
  min-width: 142px;
  >li
    float left
</style>
