<template>
  <div class="multinest">
    <Button type="info" @click.stop="additional">{{ title }}</Button>
    <div v-for="(item, index) in this.properties[this.multiKey].properties" :key="index">
      {{item.title}}：{{ getValue(item, answers[index]) }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'multinest',
  props: {
    title: {
      type: String,
      required: true
    },
    multiKey: {
      type: String,
      required: true
    },
    properties: {
      type: Object,
      required: true
    },
    answers: {
      type: Object
    }
  },
  methods: {
    additional () {
      if (this.multiKey) {
        this.$emit('additional', {
          allowClose: true,
          additionalKeys: [this.multiKey]
        })
      }
    },
    getValue (item, answer) {
      if (item.uuid === '00000000-0000-0000-0000-000000000005' || item.uuid === '00000000-0000-0000-0000-000000000007' || item.uuid === '00000000-0000-0000-0000-000000000013') {
        let tmp = []
        for (const n in answer) {
          tmp.push(item.properties[answer[n]].title)
        }
        return tmp.join(',')
      } else {
        return answer
      }
    }
  }
}
</script>
