import Vue from 'vue'
import Router from 'vue-router'
import { AuthGuard, authedGard } from './guard.js'
import Layout from '@/layouts/Layout'
import Page from '@/pages/Page'
import ReportTree from '@/pages/ReportTree'
import { PRODUCTION_NAME } from '@/libs/config'
import knowledge from './knowledge'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/layout'
    },
    {
      path: '/layout',
      component: Layout,
      beforeEnter: AuthGuard,
      children: [
        {
          path: '',
          redirect: '/layout/home'
        },
        {
          path: 'home',
          name: 'Home',
          component: () => import('@/pages/Home'),
          meta: {
            title: '首页'
          }
        },
        {
          path: 'page/:projectId',
          name: 'Page',
          component: Page,
          meta: {
            title: '项目'
          }
        },
        {
          path: 'rc',
          name: 'RegisterComponent',
          component: () => import('@/pages/RegisterComponent'),
          meta: {
            title: '组件注册'
          }
        },
        {
          path: 'report',
          name: 'Report',
          component: ReportTree,
          meta: {
            title: '报告编辑'
          }
        },
        {
          path: 'accountSetting',
          name: 'AccountSetting',
          component: () => import('@/pages/AccountSetting'),
          meta: {
            title: '账号设置'
          }
        },
        {
          path: '/question',
          name: 'Question',
          component: () => import('@/pages/Question'),
          meta: {
            title: '做题测试'
          }
        },
        {
          path: '/answerRecord',
          name: 'AnswerRecord',
          component: () => import('@/pages/AnswerRecord'),
          meta: {
            title: '做题记录'
          }
        },
        {
          path: '/dataComponent',
          name: 'DataComponent',
          component: () => import('@/pages/DataComponent'),
          meta: {
            //
          }
        },
        knowledge
      ]
    },
    {
      path: '/helper',
      name: 'Helper',
      component: () => import('@/pages/Helper'),
      meta: {
        title: '小助手'
      }
    },
    {
      path: '/bindPhone',
      name: 'BindPhone',
      component: () => import('@/pages/BindPhone'),
      meta: {
        title: '绑定手机'
      }
    },
    {
      path: '/bindwx',
      name: 'BindWX',
      component: () => import('@/pages/BindWX'),
      meta: {
        title: '绑定微信'
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/pages/Login'),
      beforeEnter: authedGard,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/register',
      name: 'Register',
      component: () => import('@/pages/Register'),
      meta: {
        title: '注册'
      }
    },
    {
      path: '/publicQuestion',
      name: 'PublicQuestion',
      component: () => import('@/pages/PublicQuestion'),
      meta: {
        title: '问答'
      }
    },
    {
      path: '/publicQuestionBig',
      name: 'PublicQuestionBig',
      component: () => import('@/pages/BigQuestion'),
      meta: {
        title: '大表单'
      }
    },
    {
      path: '*',
      component: () => import('@/router/404'),
      meta: {
        title: '错误地址'
      }
    }
  ]
})

router.beforeEach(function (to, from, next) {
  document.title = to.meta.title ? to.meta.title + ` - ${PRODUCTION_NAME}` : PRODUCTION_NAME
  next()
})

export default router
