<template>
  <div class="x-input">
    <input
      class="x-input-main"
      type="text"
      :value="value"
      :disabled="disabled"
      :readonly="readonly"
      :placeholder="placeholder"
      :name="name"
      :autocomplete="autocomplete"
      :spellcheck="spellcheck"
      @change="change"
      @input="input"
      @focus="focus"
      @blur="blur"
      @keyup="keyup"
      @keydown="keydown"
      @mouseup="mouseup">
  </div>
</template>

<script>
export default {
  name: 'xInput',
  model: {
    prop: 'value',
    event: 'update'
  },
  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    type: {
      type: String,
      default: 'text'
    },
    max: { // type 为 number时，设定最大值
      type: Number
    },
    min: { // type 为 number时，设定最小值
      type: Number
    },
    placeholder: {
      type: [String, Number],
      default: ''
    },
    readonly: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    name: {
      type: [String, Number],
      default: ''
    },
    autocomplete: {
      type: [String, Number, Boolean]
    },
    spellcheck: {
      type: [String, Boolean]
    }
  },
  methods: {
    change (e) {
      this.$emit('on-change', e.target.value)
    },
    input (e) {
      const value = this.typePip(e.target.value)
      e.target.value = value
      this.$emit('on-input', value)
      this.$emit('update', value)
    },
    focus (e) {
      this.$emit('on-focus', e)
    },
    blur (e) {
      this.$emit('on-blur', e)
    },
    keyup (e) {
      this.$emit('on-keyup', e)
    },
    keydown (e) {
      this.$emit('on-keydown', e)
    },
    mouseup (e) {
      this.$emit('oon-mouseup', e)
    },
    typePip (value) {
      switch (this.type) {
        case 'number': { // 整数
          let trueValue = ''
          if (value.startsWith('-')) {
            trueValue = '-'
          }
          const numbers = value.match(/[0-9]/g, '')
          trueValue += numbers ? numbers.join('') : ''
          if (typeof this.min === 'number' && +trueValue < this.min) {
            return this.min
          }
          if (typeof this.max === 'number' && +trueValue > this.max) {
            return this.max
          }
          return trueValue
        }
        default:
          return value
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.x-input
  display inline-block
  width 100%
  .x-input-main
    display block
    width 100%
    height 52px
    padding 0 19px
    font-size 14px
    border 1px solid #e5e5e5
    border-radius 10px
    margin-bottom 25px
    outline none
</style>
