export default {
  namespaced: true,
  state: {
    codeList: [],
    projectId: 0,
    show: false,
    soruceId: 0,
    sourceType: 0
  },
  mutations: {
    showTestBoard (state) {
      state.show = true
    },
    hideTestboard (state) {
      state.show = false
      this.clearState(state)
    },
    setState (state, data) {
      for (let key in data) {
        if (state.hasOwnProperty(key)) {
          state.key = data[key]
        }
      }
    },
    clearState (state) {
      state.codeList = []
      state.projectId = 0
      state.show = false
      state.sourceId = 0
      state.sourceType = 0
    }
  }
}
