// initial state
const state = {
  showNodeTree: false
}

// mutations
const mutations = {
  showNodeTree (state) {
    state.showNodeTree = true
  },
  hideNodeTree (state) {
    state.showNodeTree = false
  }
}
// getters
const getters = {

}
export default {
  namespaced: true,
  state,
  mutations,
  getters
}
