<template>
  <div
    ref="editorContainer"
    class="editor-area"
    @keydown.ctrl.enter.capture.stop=""
    :style="styleObject">
    <Alert
      class="explain"
      type="success"
      banner
      v-html="explain.text"
      v-if="explain.show">
    </Alert>
    <monaco-editor
      class="code-editor"
      :class="error ? 'error' : ''"
      ref="editor"
      v-model="codeString"
      language="php"
      :options="computedOptions"
      @change="codeChange"
      @layout="layoutChange"
      @keydown="keydown"
      @editorMount="editorMount"
      @focusText="focusText"
      @blurText="blurText"
      @keyup="checkKeyUp">
    </monaco-editor>
  </div>
</template>

<script>
import MonacoEditor from './monacoEditor'
import report from '@/api/report'

export default {
  name: 'Editor',
  components: {
    MonacoEditor
  },
  data () {
    return {
      codeString: '',
      styleObject: {
        width: '100%',
        height: '300px',
        maxHeight: '500px'
      },
      options: {
        language: 'php',
        scrollBeyondLastLine: false,
        wordWrap: 'on',
        theme: 'vs',
        minimap: {
          enabled: false
        },
        wordSeparators: "`~!@#%^&*()-=+[{]}\\|;:'\",.<>/?"
      },
      selection: {}, // 代码选择区域
      explain: {
        show: false,
        text: ''
      },
      focus: false,
      pageProjectId: 0, // Page页面的根目录
      observer: null,
      layoutOldWidth: '',
      layoutTimer: 0
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    refs: {
      type: String
    },
    projectId: {
      type: [String, Number]
    },
    currentProject: {
      type: [String, Number],
      default: ''
    },
    fillAll: {
      type: Boolean,
      default: false
    },
    updateCodeTest: {
      type: Boolean,
      default: true
    },
    heightLimit: {
      type: Boolean,
      default: true
    },
    maxHeight: {
      type: Number,
      default: 500
    },
    testSelf: {
      type: Boolean,
      default: true
    },
    sourceType: {
      type: Number // 1：节点，2：组件，3：报告，4：报告章节，5：报告章节代码块
    },
    error: {
      type: Boolean,
      default: false
    },
    customOptions: {
      type: Object,
      default: () => {}
    }
  },
  computed: {
    codeProject () { // 优先使用传入的projectId
      return this.projectId || this.pageProjectId
    },
    computedOptions () {
      return Object.assign({}, this.customOptions, this.options)
    }
  },
  created () {
    this.codeString = this.value
    if (this.codeString === null || this.codeString.replace(/\s+/g, '') === '') {
      this.codeString = '<?\n' +
                  '\n' +
                  '?>\n'
      this.$emit('input', this.codeString)
    }
    this.$bus.$emit('get-root-project', (projectId) => {
      this.pageProjectId = projectId
    })
  },
  mounted () {
    let MutationObserver = window.MutationObserver || window.WebKitMutationObserver || window.MozMutationObserver
    if (MutationObserver) {
      this.observer = new MutationObserver(() => {
        if (this.layoutTimer) return
        if (!this.$refs.editor) return
        if (this.layoutOldWidth === this.$refs.editor.$el.offsetWidth) return
        this.layoutTimer = setTimeout(() => {
          this.layoutTimer = 0
          this.layout()
        }, 500)
      })
      this.observer.observe(document.body, { attributes: true, subtree: true })
    }
    this.$nextTick(() => {
      this.$refs.editor.$el.addEventListener('dblclick', this.dblClick)
    })
  },
  beforeDestroy () {
    if (this.observer) {
      this.observer.takeRecords()
      this.observer.disconnect()
      this.observer = null
    }
    this.$refs.editor.$el.removeEventListener('dblclick', this.dblClick)
  },
  methods: {
    editorMount (IStandaloneCodeEditor) {
      const editor = IStandaloneCodeEditor
      editor.onDidChangeCursorSelection((event) => {
        this.selection = event.selection
        // this.explain.show = false
        // this.getExplain(editor.getPosition())
      })
      window.monaco.languages.register({ id: 'mySpecialLanguage' })
      window.monaco.languages.registerHoverProvider('php', {
        provideHover: async (model, position, token) => {
          this.emitExplain()
        }
      })
      this.layout()
      this.$emit('editorMounted')
    },
    focusText (event) {
      this.$bus.$emit('show-node-tree')
      this.focus = true
      this.openNodeTree()
    },
    blurText () {
      this.focus = false
      this.explain.show = false
    },
    isHoverChoose () {
      let editor = this.getMonaco()
      if (!editor) return
      let position = editor.getPosition()
      let hoverChoose = true // 是否悬浮在选择区域
      if (this.isEmptyObject(this.selection)) {
        hoverChoose = false
      } else if (position.lineNumber > this.selection.endLineNumber) {
        hoverChoose = false
      } else if (position.lineNumber < this.selection.startLineNumber) {
        hoverChoose = false
      } else if (position.lineNumber === this.selection.endLineNumber && position.column > this.selection.endColumn) {
        hoverChoose = false
      } else if (position.lineNumber === this.selection.startLineNumber && position.column < this.selection.startColumn) {
        hoverChoose = false
      }
      return hoverChoose
    },
    dblClick () {
      document.execCommand('Copy', 'false', null)
      this.emitExplain()
    },
    emitExplain () {
      if (this.timerIndex) {
        clearTimeout(this.timerIndex)
      }
      if (!this.isHoverChoose()) {
        this.explain.show = false
      } else {
        this.timerIndex = setTimeout(this.getExplain, 200)
      }
    },
    async getExplain () {
      if (!this.isHoverChoose()) return
      if (!this.codeProject) {
        this.explain.show = false
        return
      }
      let sText = this.getChooseText()
      if (sText === '') {
        this.explain.show = false
        return
      }
      this.explain.text = await this.getContentsByText(sText)
      this.explain.show = true
    },
    getChooseText () {
      if (this.isEmptyObject(this.selection)) return ''
      let editor = this.getMonaco()
      if (!editor) return
      let model = editor.getModel()
      let chooseRange = new window.monaco.Range(this.selection.startLineNumber, this.selection.startColumn,
        this.selection.endLineNumber, this.selection.endColumn)
      let text = model.getValueInRange(chooseRange)
      // if (text && this.selection.startColumn > 1) {
      //   let charRange = new window.monaco.Range(this.selection.startLineNumber, this.selection.startColumn - 1,
      //     this.selection.startLineNumber, this.selection.startColumn)
      //   let char = model.getValueInRange(charRange)
      //   if (char && char === '$') {
      //     text = '$' + text
      //   }
      // }
      return text
    },
    async getContentsByText (sText) {
      if (!sText) return '暂无信息'
      let varList = this.analysisCode(sText)
      let sSwapText = sText
      let contents = []
      if (varList.length) {
        let params = {
          projectId: this.codeProject,
          variablesNames: varList
        }
        let data = await report.getTokenExplain(params)
        for (let key in data) {
          if (data[key].title) {
            sSwapText = sSwapText.replace('$' + key, data[key].title)
          } else {
            contents.push('$' + key + '：请检查变量是否存在')
          }
        }
      }
      let lineList = sSwapText.split('\n')
      contents.unshift(...lineList)
      return '<pre>' + contents.join('<br />') + '</pre>'
    },
    analysisCode (sText) { // 提取变量
      if (!sText) return []
      let varReg = /\$q[p|P]?[\d|_a-zA-Z]+|\$v[p|P]?[\d|_a-zA-Z]+/g
      let lst = sText.match(varReg) || []
      return lst.map(item => item.slice(1))
    },
    codeChange (val, e) {
      this.$emit('input', this.codeString)
      this.layout()
    },
    keydown (e) {
    },
    layoutChange (e) {
    },
    layout () {
      const editor = this.getMonaco()
      if (!editor) return
      const lineCount = editor.getModel().getLineCount()
      let height = Math.max(lineCount * 20 + 20, 200)
      height = Math.min(this.maxHeight, height)
      this.styleObject.height = height + 'px' // 撑开页面父元素
      if (this.$refs.editor.$el.offsetWidth !== 0) {
        this.layoutOldWidth = this.$refs.editor.$el.offsetWidth
        editor.layout({
          width: this.$refs.editor.$el.offsetWidth,
          height
        })
      }
      // if (this.fillAll) {
      //   this.$nextTick(() => {
      //     if (this.$refs.editor.$el.parentNode.parentNode.offsetHeight > height) {
      //       height = this.$refs.editor.$el.parentNode.parentNode.offsetHeight - 50
      //       height = Math.min(this.maxHeight, height)
      //       this.styleObject.height = height + 'px'
      //       editor.layout({
      //         width: this.$refs.editor.$el.offsetWidth,
      //         height
      //       })
      //     }
      //   })
      // }
    },
    getMonaco () {
      if (this.$refs.editor) {
        return this.$refs.editor.getMonaco()
      }
      return null
    },
    checkKeyUp (event) {
      if (event.ctrlKey && event.code === 'Enter') {
        this.openTest()
      }
    },
    openTest () {
      if (this.updateCodeTest) {
        this.$bus.$emit('pop-helper-update-prop', 'codeTest', {
          projectId: this.codeProject,
          codeList: [this.codeString]
        })
        this.$bus.$emit('pop-helper-show-menu', 'codeTest')
        this.$bus.$emit('code-test')
      }
    },
    focusEditor () {
      this.$refs.editor.focus()
    },
    openNodeTree () {
      this.$bus.$emit('pop-helper-update-prop', 'nodeTree', {
        rootId: this.codeProject
      })
      this.$bus.$emit('pop-helper-show-menu', 'nodeTree')
    }
  },
  watch: {
    value (newValue, oldValue) {
      this.codeString = this.value
      if (this.testSelf) {
        this.$bus.$emit('pop-helper-update-prop', 'codeTest', {
          projectId: this.codeProject,
          codeList: [this.codeString],
          sourceType: this.sourceType
        })
      }
    },
    codeProject () {
      if (this.testSelf) {
        this.$bus.$emit('pop-helper-update-prop', 'codeTest', {
          projectId: this.codeProject,
          codeList: [this.codeString],
          sourceType: this.sourceType
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.explain
  position fixed
  left 0
  right 0
  top 60px
  z-index 2
  max-height 20%
  overflow auto
</style>

<style lang="stylus">
.code-editor
  .monaco-editor
    box-shadow 0 0 2px 1px #3361D8
    border-radius 4px
  &.error
    .monaco-editor
      box-shadow 0 0 2px 1px #FF6464
</style>
