import Request from '@/libs/request'

export function saveCase (params) {
  const url = params.id ? '/mvp-manager/cases/update' : '/mvp-manager/cases/save'
  return Request.put(url, params).then(resp => resp.data)
}

export function getCaseList (params) {
  return Request.post('/mvp-manager/cases/list', params).then(resp => resp.data)
}

export function getCaseDetail (params) {
  return Request.post('/mvp-manager/cases/detail?id=' + params.id).then(resp => resp.data)
}

export function deleteCase (params) {
  return Request.post('/mvp-manager/cases/delete', params).then(resp => resp.data)
}
